import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import en from './locales/en.json';
import zh from './locales/zh.json';
import fr from './locales/fr.json';
import es from './locales/es.json';
import ar from './locales/ar.json';
import enHelp from './locales/en/help.json';
import zhHelp from './locales/zh/help.json';
import frHelp from './locales/fr/help.json';
import esHelp from './locales/es/help.json';
import arHelp from './locales/ar/help.json';
import enPageHelp from './locales/en/pageHelp.json';
import zhPageHelp from './locales/zh/pageHelp.json';
import frPageHelp from './locales/fr/pageHelp.json';
import esPageHelp from './locales/es/pageHelp.json';
import arPageHelp from './locales/ar/pageHelp.json';

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    resources: {
      en: {
        translation: en,
        help: enHelp,
        pageHelp: enPageHelp,
      },
      zh: {
        translation: zh,
        help: zhHelp,
        pageHelp: zhPageHelp,
      },
      fr: {
        translation: fr,
        help: frHelp,
        pageHelp: frPageHelp,
      },
      es: {
        translation: es,
        help: esHelp,
        pageHelp: esPageHelp,
      },
      ar: {
        translation: ar,
        help: arHelp,
        pageHelp: arPageHelp,
      },
    },
    fallbackLng: 'en',
    interpolation: {
      escapeValue: false,
    },
  });

export default i18n;