import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useTranslation } from 'react-i18next';
import { HelpCircle, X, ChevronRight, ChevronDown, Play, Lightbulb, Keyboard, BookOpen, Sparkles, ExternalLink } from 'lucide-react';
import { useHelp, PageHelpContent, FeatureHelp } from '../../contexts/HelpContext';
import { pageHelpConfig } from '../../config/pageHelp.config';

interface PageHelpPanelProps {
  currentPath: string;
  onStartTour?: () => void;
}

export function PageHelpPanel({ currentPath, onStartTour }: PageHelpPanelProps) {
  const { t } = useTranslation(['help', 'pageHelp']);
  const [isOpen, setIsOpen] = useState(false);
  const [activeTab, setActiveTab] = useState<'features' | 'shortcuts' | 'tips'>('features');
  const [expandedFeature, setExpandedFeature] = useState<string | null>(null);
  const { setPageHelp } = useHelp();

  const config = pageHelpConfig[currentPath];

  useEffect(() => {
    if (config) {
      setPageHelp({
        pageId: config.pageId,
        title: t(config.titleKey, { ns: 'pageHelp' }),
        description: t(config.descriptionKey, { ns: 'pageHelp' }),
        features: config.features.map(f => ({
          ...f,
          title: t(f.titleKey, { ns: 'pageHelp' }),
          description: t(f.descriptionKey, { ns: 'pageHelp' }),
          tips: (f.tipKeys || []).map(key => t(key, { ns: 'pageHelp' }))
        })),
        shortcuts: (config.shortcuts || []).map(s => ({
          ...s,
          action: t(s.actionKey, { ns: 'pageHelp' })
        })),
        relatedPages: config.relatedPages
      });
    }
  }, [currentPath, config, setPageHelp, t]);

  if (!config) return null;

  return (
    <>
      <motion.button
        initial={{ scale: 0 }}
        animate={{ scale: 1 }}
        whileHover={{ scale: 1.1 }}
        onClick={() => setIsOpen(true)}
        className="fixed right-6 bottom-24 z-40 w-14 h-14 bg-gradient-to-br from-primary-600 to-accent-600 rounded-full shadow-lg shadow-primary-500/30 flex items-center justify-center text-white hover:shadow-xl transition-shadow"
      >
        <HelpCircle className="w-7 h-7" />
        <span className="absolute -top-1 -right-1 w-4 h-4 bg-error rounded-full text-xs flex items-center justify-center font-bold">!</span>
      </motion.button>

      <AnimatePresence>
        {isOpen && (
          <>
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              onClick={() => setIsOpen(false)}
              className="fixed inset-0 bg-black/20 backdrop-blur-sm z-40"
            />
            <motion.div
              initial={{ x: '100%' }}
              animate={{ x: 0 }}
              exit={{ x: '100%' }}
              transition={{ type: 'spring', damping: 25 }}
              className="fixed right-0 top-0 bottom-0 w-[450px] bg-white dark:bg-dark-800 shadow-2xl z-50 flex flex-col"
            >
              <div className="p-6 border-b border-slate-200 dark:border-dark-600">
                <div className="flex items-start justify-between">
                  <div>
                    <h2 className="text-xl font-bold flex items-center gap-2">
                      <BookOpen className="w-5 h-5 text-primary-500" />
                      {config.title}
                    </h2>
                    <p className="text-sm text-slate-500 mt-1">{config.description}</p>
                  </div>
                  <button onClick={() => setIsOpen(false)} className="p-2 hover:bg-slate-100 rounded-lg">
                    <X className="w-5 h-5 text-slate-400" />
                  </button>
                </div>

                <div className="flex gap-1 mt-6">
                  {[
                    { id: 'features', label: t('tabs.features'), icon: Sparkles },
                    { id: 'shortcuts', label: t('tabs.shortcuts'), icon: Keyboard },
                    { id: 'tips', label: t('tabs.tips'), icon: Lightbulb },
                  ].map((tab) => (
                    <button
                      key={tab.id}
                      onClick={() => setActiveTab(tab.id as any)}
                      className={`flex-1 flex items-center justify-center gap-2 px-4 py-2 rounded-lg text-sm font-medium transition-colors ${activeTab === tab.id
                          ? 'bg-primary-500 text-white'
                          : 'text-slate-600 hover:bg-slate-100'
                        }`}
                    >
                      <tab.icon className="w-4 h-4" />
                      {tab.label}
                    </button>
                  ))}
                </div>
              </div>

              <div className="flex-1 overflow-y-auto p-6">
                <AnimatePresence mode="wait">
                  {activeTab === 'features' && (
                    <FeaturesTab
                      features={config.features}
                      expandedFeature={expandedFeature}
                      onToggleFeature={setExpandedFeature}
                    />
                  )}
                  {activeTab === 'shortcuts' && <ShortcutsTab shortcuts={config.shortcuts} />}
                  {activeTab === 'tips' && <TipsTab />}
                </AnimatePresence>
              </div>

              <div className="p-4 border-t border-slate-200">
                <button
                  onClick={() => {
                    if (onStartTour) {
                      onStartTour();
                      setIsOpen(false);
                    }
                  }}
                  className="w-full flex items-center justify-center gap-2 py-3 bg-primary-500 text-white rounded-lg font-medium hover:bg-primary-600 disabled:opacity-50 disabled:cursor-not-allowed"
                  disabled={!onStartTour}
                >
                  <Play className="w-4 h-4" />
                  {t('actions.startTour')}
                </button>
              </div>
            </motion.div>
          </>
        )}
      </AnimatePresence>
    </>
  );
}

function FeaturesTab({ features, expandedFeature, onToggleFeature }: { features: FeatureHelp[]; expandedFeature: string | null; onToggleFeature: (id: string | null) => void }) {
  const { t } = useTranslation('help');

  return (
    <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="space-y-3">
      {features.map((feature, index) => (
        <motion.div
          key={feature.id}
          initial={{ opacity: 0, x: 20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: index * 0.05 }}
          className={`border rounded-xl overflow-hidden transition-colors ${expandedFeature === feature.id ? 'border-primary-500 bg-primary-50' : 'border-slate-200 hover:border-primary-300'
            }`}
        >
          <button
            onClick={() => onToggleFeature(expandedFeature === feature.id ? null : feature.id)}
            className="w-full flex items-center gap-3 p-4 text-left"
          >
            <div className={`w-10 h-10 rounded-lg flex items-center justify-center ${feature.important ? 'bg-warning/20 text-warning' : 'bg-primary-100 text-primary-600'}`}>
              <span className="text-lg">{feature.icon}</span>
            </div>
            <div className="flex-1">
              <div className="flex items-center gap-2">
                <h3 className="font-semibold">{feature.title}</h3>
                {feature.important && (
                  <span className="px-2 py-0.5 bg-warning/20 text-warning text-xs rounded-full">{t('labels.important')}</span>
                )}
              </div>
              <p className="text-sm text-slate-500 line-clamp-1">{feature.description}</p>
            </div>
            <ChevronDown className={`w-5 h-5 text-slate-400 transition-transform ${expandedFeature === feature.id ? 'rotate-180' : ''}`} />
          </button>

          <AnimatePresence>
            {expandedFeature === feature.id && (
              <motion.div initial={{ height: 0 }} animate={{ height: 'auto' }} exit={{ height: 0 }} className="border-t border-slate-200 px-4 pb-4">
                <div className="pt-4 space-y-4">
                  {feature.tips.length > 0 && (
                    <div className="bg-slate-50 rounded-lg p-4">
                      <h4 className="text-sm font-medium mb-2 flex items-center gap-2">
                        <Lightbulb className="w-4 h-4 text-warning" />
                        {t('labels.proTips')}
                      </h4>
                      <ul className="space-y-2">
                        {feature.tips.map((tip, idx) => (
                          <li key={idx} className="text-sm text-slate-600 flex items-start gap-2">
                            <span className="text-primary-500"></span>
                            {tip}
                          </li>
                        ))}
                      </ul>
                    </div>
                  )}
                  {feature.interactive && (
                    <button className="w-full py-2 border-2 border-dashed border-primary-300 text-primary-600 rounded-lg hover:bg-primary-50 font-medium">
                      {t('actions.highlightFeature')}
                    </button>
                  )}
                </div>
              </motion.div>
            )}
          </AnimatePresence>
        </motion.div>
      ))}
    </motion.div>
  );
}

function ShortcutsTab({ shortcuts }: { shortcuts: { key: string; action: string }[] }) {
  return (
    <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="space-y-3">
      {shortcuts.map((shortcut, index) => (
        <div key={index} className="flex items-center justify-between p-4 bg-slate-50 rounded-lg">
          <span className="text-slate-700">{shortcut.action}</span>
          <kbd className="px-3 py-1.5 bg-white border border-slate-300 rounded-lg text-sm font-mono">{shortcut.key}</kbd>
        </div>
      ))}
    </motion.div>
  );
}

function TipsTab() {
  const { t } = useTranslation('help');
  return (
    <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="space-y-4">
      <div className="bg-gradient-to-br from-primary-500/10 to-accent-500/10 border border-primary-200 rounded-xl p-4">
        <h3 className="font-semibold text-primary-700 flex items-center gap-2 mb-3">
          <Sparkles className="w-5 h-5" />
          {t('tips.recommended')}
        </h3>
        <p className="text-sm text-slate-600">{t('tips.comingSoon')}</p>
      </div>
    </motion.div>
  );
}
