import { useEffect, useState } from 'react';

interface User {
  userId: string;
  email: string;
  username: string;
  displayName: string;
  role: string;
  status: string;
  projectId: string | null;
  permissions: Record<string, boolean>;
}

const ROLES = {
  SUPER_ADMIN: 'super_admin',
  ADMIN: 'admin',
  PROJECT_ADMIN: 'project_admin',
  OPERATOR: 'operator',
  VIEWER: 'viewer',
  DEMO: 'demo',
};

export function useAuth() {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const storedUser = localStorage.getItem('user');
    if (storedUser) {
      try {
        setUser(JSON.parse(storedUser));
      } catch {
        setUser(null);
      }
    }
    setIsLoading(false);
  }, []);

  const isAuthenticated = !!user;
  const isDemoMode = user?.role === ROLES.DEMO;

  const hasRole = (roles: string[]) => {
    if (!user) return false;
    return roles.includes(user.role);
  };

  const isAdmin = () => {
    return hasRole([ROLES.SUPER_ADMIN, ROLES.ADMIN]);
  };

  const isProjectAdmin = () => {
    return hasRole([ROLES.SUPER_ADMIN, ROLES.ADMIN, ROLES.PROJECT_ADMIN]);
  };

  const canManageUsers = () => {
    if (Array.isArray(user?.permissions)) {
      return (user.permissions as any).includes('users:manage') || (user.permissions as any).includes('admin');
    }
    return !!user?.permissions?.canManageUsers;
  };

  const canManageTokens = () => {
    if (Array.isArray(user?.permissions)) {
      return (user.permissions as any).includes('tokens:manage') || (user.permissions as any).includes('admin');
    }
    return !!user?.permissions?.canManageTokens;
  };

  const canAccessProject = (projectId: string) => {
    if (!user) return false;
    if (isAdmin()) return true;
    return user.projectId === projectId;
  };

  const logout = () => {
    localStorage.removeItem('auth_token');
    localStorage.removeItem('refresh_token');
    localStorage.removeItem('demo_token');
    localStorage.removeItem('demo_expires_at');
    localStorage.removeItem('demo_mode');
    localStorage.removeItem('user');
    localStorage.removeItem('user_role');
    window.location.href = '/login';
  };

  return {
    user,
    isLoading,
    isAuthenticated,
    isDemoMode,
    hasRole,
    isAdmin,
    isProjectAdmin,
    canManageUsers,
    canManageTokens,
    canAccessProject,
    logout,
    ROLES,
  };
}

export default useAuth;
