import { Outlet, useLocation, NavLink } from 'react-router-dom'
import { motion, AnimatePresence } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
  LayoutDashboard,
  CreditCard,
  ArrowLeftRight,
  BarChart3,
  Settings,
  Bell,
  Search,
  Menu,
  X,
  User,
  Globe,
  Sun,
  Moon,
  Bus,
  Server,
  Users,
  Key,
  LogOut,
  MessageSquare,
  Activity,
} from 'lucide-react'
import { useState, useEffect } from 'react'
import { DemoBanner, DemoToggle } from '../demo/components/DemoIndicators'
import { isDemoMode } from '../demo/demoMode'
import { PageHelpPanel, SpotlightTour, useSpotlight } from './help'
import { pageHelpConfig } from '../config/pageHelp.config'
import { useAuth } from '../hooks/useAuth'

function Layout() {
  const isDemo = isDemoMode()
  const { user, logout, canManageUsers, canManageTokens } = useAuth();
  const [sidebarVisible, setSidebarVisible] = useState(true)
  const [isHovered, setIsHovered] = useState(false)
  const [hoverTimeout, setHoverTimeout] = useState<any>(null)
  const [hoveredItem, setHoveredItem] = useState<string | null>(null)
  const [languageMenuOpen, setLanguageMenuOpen] = useState(false)
  const location = useLocation()

  // Handle sidebar hover expansion with delay
  const handleMouseEnter = () => {
    if (!sidebarVisible) {
      const timeout = setTimeout(() => setIsHovered(true), 200)
      setHoverTimeout(timeout)
    }
  }

  const handleMouseLeave = () => {
    if (hoverTimeout) clearTimeout(hoverTimeout)
    setIsHovered(false)
    setHoveredItem(null)
  }
  const { t, i18n } = useTranslation()
  const currentPageConfig = pageHelpConfig[location.pathname]
  const tourSteps = currentPageConfig?.features.map(f => ({
    id: f.id,
    target: f.elementSelector,
    title: f.title,
    description: f.description,
    position: 'bottom' as const,
    tips: f.tips,
  })) || []

  const { isActive, currentStep, startTour, stopTour, nextStep, prevStep, goToStep } = useSpotlight(
    tourSteps,
    `tour-${location.pathname}`
  )

  const [theme, setTheme] = useState(() => {
    if (typeof window !== 'undefined') {
      return localStorage.getItem('theme') || 'dark'
    }
    return 'dark'
  })

  useEffect(() => {
    const root = window.document.documentElement
    root.classList.remove('light', 'dark')
    root.classList.add(theme)

    // RTL 
    const isRTL = i18n.language === 'ar'
    root.dir = isRTL ? 'rtl' : 'ltr'
    root.lang = i18n.language

    try {
      localStorage.setItem('theme', theme)
    } catch (e) {
      console.warn('Failed to save theme preference:', e)
    }
  }, [theme, i18n.language])

  const toggleTheme = () => {
    setTheme(prev => prev === 'dark' ? 'light' : 'dark')
  }

  const languages = [
    { code: 'en', label: 'English', short: 'EN' },
    { code: 'zh', label: '', short: 'ZH' },
    { code: 'fr', label: 'Franais', short: 'FR' },
    { code: 'es', label: 'Espaol', short: 'ES' },
    { code: 'ar', label: '', short: 'AR' },
  ]

  const navItems = [
    { path: '/', icon: LayoutDashboard, label: t('common.dashboard') },
    { path: '/cards', icon: CreditCard, label: t('common.cards') },
    { path: '/devices', icon: Server, label: t('common.deviceManagement') },
    { path: '/transactions', icon: ArrowLeftRight, label: t('common.transactions') },
    { path: '/transit', icon: Bus, label: t('common.transit') },
    { path: '/predictive-maintenance', icon: Activity, label: t('devices.predictiveMaintenance') },
    { path: '/reports', icon: BarChart3, label: t('common.reports') },
    { path: '/settings', icon: Settings, label: t('common.settings') },
  ]



  const getFeatureDesc = (path: string) => {
    switch (path) {
      case '/': return t('feature.dashboard')
      case '/cards': return t('feature.cards')
      case '/devices': return t('feature.devices')
      case '/transactions': return t('feature.transactions')
      case '/transit': return t('feature.transit')
      case '/predictive-maintenance': return t('devices.maintenanceSubtitle')
      case '/reports': return t('feature.reports')
      case '/settings': return t('feature.settings')
      case '/users': return t('feature.userManagement')
      case '/tokens': return t('feature.tokenManagement')
      default: return ''
    }
  }

  const isEffectiveOpen = sidebarVisible || isHovered

  return (
    <div className="min-h-screen flex">
      <DemoBanner />
      <motion.aside
        initial={{ width: 280 }}
        animate={{ width: isEffectiveOpen ? 280 : 80 }}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        className={`fixed start-0 top-0 h-screen z-50 transition-all duration-300 ${isEffectiveOpen
          ? 'glass-sidebar shadow-2xl border-e border-white/10'
          : 'bg-white dark:bg-dark-800 border-e border-slate-200 dark:border-dark-600'
          }`}
      >
        <div className="h-full flex flex-col">
          <div className="h-16 flex items-center justify-between px-6 border-b border-slate-200 dark:border-dark-600">
            {isEffectiveOpen ? (
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                className="flex items-center gap-3"
              >
                <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-primary-500 to-accent-500 flex items-center justify-center">
                  <CreditCard className="w-5 h-5 text-white" />
                </div>
                <span className="font-display font-bold text-lg gradient-text">
                  {t('common.appName')}
                </span>
              </motion.div>
            ) : (
              <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-primary-500 to-accent-500 flex items-center justify-center mx-auto">
                <CreditCard className="w-5 h-5 text-white" />
              </div>
            )}
            {isEffectiveOpen && (
              <button
                onClick={() => setSidebarVisible(!sidebarVisible)}
                className="p-2 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg transition-colors text-slate-500 dark:text-slate-400"
                title={sidebarVisible ? t('common.collapse') : t('common.expand')}
              >
                {sidebarVisible ? <X className="w-5 h-5" /> : <Menu className="w-5 h-5" />}
              </button>
            )}
          </div>

          <nav className="flex-1 py-6 px-3 space-y-1">
            {navItems.map((item) => (
              <div
                key={item.path}
                className="relative"
                onMouseEnter={() => !isEffectiveOpen && setHoveredItem(item.path)}
                onMouseLeave={() => setHoveredItem(null)}
              >
                <NavLink
                  to={item.path}
                  className={({ isActive }) =>
                    `nav-item ${isActive ? 'active' : ''} ${!isEffectiveOpen ? 'justify-center' : ''}`
                  }
                >
                  <div className="relative">
                    <item.icon className="w-5 h-5 flex-shrink-0" />
                  </div>
                  {isEffectiveOpen && (
                    <motion.span
                      initial={{ opacity: 0, x: i18n.language === 'ar' ? 10 : -10 }}
                      animate={{ opacity: 1, x: 0 }}
                      className="font-medium"
                    >
                      {item.label}
                    </motion.span>
                  )}
                </NavLink>

                <AnimatePresence>
                  {!isEffectiveOpen && hoveredItem === item.path && (
                    <motion.div
                      initial={{ opacity: 0, x: 10, scale: 0.95 }}
                      animate={{ opacity: 1, x: 0, scale: 1 }}
                      exit={{ opacity: 0, scale: 0.95 }}
                      transition={{ duration: 0.2 }}
                      className={`absolute ${i18n.language === 'ar' ? 'right-full mr-3' : 'left-full ml-3'} top-1/2 -translate-y-1/2 px-4 py-3 bg-slate-900/95 dark:bg-slate-800/95 backdrop-blur-md text-white rounded-xl shadow-xl z-50 w-64 border border-white/10`}
                    >
                      <div className="font-medium mb-1 flex items-center gap-2 text-primary-400">
                        <item.icon className="w-4 h-4" />
                        {item.label}
                      </div>
                      <div className="text-xs text-slate-300 leading-relaxed font-light">
                        {getFeatureDesc(item.path)}
                      </div>
                      {/* Arrow */}
                      <div className={`absolute ${i18n.language === 'ar' ? 'right-0 translate-x-full' : 'left-0 -translate-x-full'} top-1/2 -mt-1.5 border-[6px] border-transparent ${i18n.language === 'ar' ? 'border-l-slate-900/95 dark:border-l-slate-800/95' : 'border-r-slate-900/95 dark:border-r-slate-800/95'}`} />
                    </motion.div>
                  )}
                </AnimatePresence>
              </div>
            ))}

            <div className="pt-4 pb-2">
              {(canManageUsers() || canManageTokens()) && (
                <div className="px-3 py-2 text-xs font-semibold text-slate-500 dark:text-slate-400 uppercase tracking-wider">
                  {isEffectiveOpen && t('common.administration')}
                </div>
              )}
              {canManageUsers() && (
                <div key="users" className="relative" onMouseEnter={() => !isEffectiveOpen && setHoveredItem('/users')} onMouseLeave={() => setHoveredItem(null)}>
                  <NavLink to="/users" className={({ isActive }) => `nav-item ${isActive ? 'active' : ''} ${!isEffectiveOpen ? 'justify-center' : ''}`}>
                    <Users className="w-5 h-5 flex-shrink-0" />
                    {isEffectiveOpen && (
                      <motion.span initial={{ opacity: 0, x: i18n.language === 'ar' ? 10 : -10 }} animate={{ opacity: 1, x: 0 }} className="font-medium">
                        {t('common.userManagement')}
                      </motion.span>
                    )}
                  </NavLink>
                  <AnimatePresence>
                    {!isEffectiveOpen && hoveredItem === '/users' && (
                      <motion.div
                        initial={{ opacity: 0, x: 10, scale: 0.95 }}
                        animate={{ opacity: 1, x: 0, scale: 1 }}
                        exit={{ opacity: 0, scale: 0.95 }}
                        transition={{ duration: 0.2 }}
                        className={`absolute ${i18n.language === 'ar' ? 'right-full mr-3' : 'left-full ml-3'} top-1/2 -translate-y-1/2 px-4 py-3 bg-slate-900/95 dark:bg-slate-800/95 backdrop-blur-md text-white rounded-xl shadow-xl z-50 w-64 border border-white/10`}
                      >
                        <div className="font-medium mb-1 flex items-center gap-2 text-primary-400">
                          <Users className="w-4 h-4" />
                          {t('common.userManagement')}
                        </div>
                        <div className="text-xs text-slate-300 leading-relaxed font-light">
                          {getFeatureDesc('/users')}
                        </div>
                        <div className={`absolute ${i18n.language === 'ar' ? 'right-0 translate-x-full' : 'left-0 -translate-x-full'} top-1/2 -mt-1.5 border-[6px] border-transparent ${i18n.language === 'ar' ? 'border-l-slate-900/95 dark:border-l-slate-800/95' : 'border-r-slate-900/95 dark:border-r-slate-800/95'}`} />
                      </motion.div>
                    )}
                  </AnimatePresence>
                </div>
              )}
              {canManageTokens() && (
                <div key="tokens" className="relative" onMouseEnter={() => !isEffectiveOpen && setHoveredItem('/tokens')} onMouseLeave={() => setHoveredItem(null)}>
                  <NavLink to="/tokens" className={({ isActive }) => `nav-item ${isActive ? 'active' : ''} ${!isEffectiveOpen ? 'justify-center' : ''}`}>
                    <Key className="w-5 h-5 flex-shrink-0" />
                    {isEffectiveOpen && (
                      <motion.span initial={{ opacity: 0, x: i18n.language === 'ar' ? 10 : -10 }} animate={{ opacity: 1, x: 0 }} className="font-medium">
                        {t('common.tokenManagement')}
                      </motion.span>
                    )}
                  </NavLink>
                  <AnimatePresence>
                    {!isEffectiveOpen && hoveredItem === '/tokens' && (
                      <motion.div
                        initial={{ opacity: 0, x: 10, scale: 0.95 }}
                        animate={{ opacity: 1, x: 0, scale: 1 }}
                        exit={{ opacity: 0, scale: 0.95 }}
                        transition={{ duration: 0.2 }}
                        className={`absolute ${i18n.language === 'ar' ? 'right-full mr-3' : 'left-full ml-3'} top-1/2 -translate-y-1/2 px-4 py-3 bg-slate-900/95 dark:bg-slate-800/95 backdrop-blur-md text-white rounded-xl shadow-xl z-50 w-64 border border-white/10`}
                      >
                        <div className="font-medium mb-1 flex items-center gap-2 text-primary-400">
                          <Key className="w-4 h-4" />
                          {t('common.tokenManagement')}
                        </div>
                        <div className="text-xs text-slate-300 leading-relaxed font-light">
                          {getFeatureDesc('/tokens')}
                        </div>
                        <div className={`absolute ${i18n.language === 'ar' ? 'right-0 translate-x-full' : 'left-0 -translate-x-full'} top-1/2 -mt-1.5 border-[6px] border-transparent ${i18n.language === 'ar' ? 'border-l-slate-900/95 dark:border-l-slate-800/95' : 'border-r-slate-900/95 dark:border-r-slate-800/95'}`} />
                      </motion.div>
                    )}
                  </AnimatePresence>
                </div>
              )}

            </div>
          </nav>

          <div className="p-4 border-t border-slate-200 dark:border-dark-600">
            <div className={`flex items-center gap-3 ${!isEffectiveOpen ? 'justify-center' : ''}`}>
              <div className="w-10 h-10 rounded-full bg-gradient-to-br from-primary-500 to-accent-500 flex items-center justify-center flex-shrink-0">
                <User className="w-5 h-5 text-white" />
              </div>
              {isEffectiveOpen && (
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  className="overflow-hidden flex-1"
                >
                  <p className="text-sm font-medium text-slate-900 dark:text-slate-200 truncate" title={isDemo ? t('demo.demoVisitor') : user?.displayName}>
                    {isDemo ? t('demo.demoVisitor') : (user?.displayName || t('common.adminUser'))}
                  </p>
                  <p className="text-xs text-slate-500 truncate" title={isDemo ? 'demo@example.com' : user?.email}>
                    {isDemo ? 'demo@example.com' : (user?.email || 'admin@transitcard.io')}
                  </p>
                </motion.div>
              )}
              <button
                onClick={() => {
                  localStorage.removeItem('auth_token');
                  localStorage.removeItem('refresh_token');
                  localStorage.removeItem('user');
                  localStorage.removeItem('user_role');
                  localStorage.removeItem('demo_token');
                  localStorage.removeItem('transitcard_demo_mode');
                  localStorage.removeItem('demo_expires_at');
                  window.location.href = '/login';
                }}
                className="p-2 text-slate-400 hover:text-red-600 dark:hover:text-red-400 transition-colors"
                title={t('common.logout')}
              >
                <LogOut className="w-5 h-5" />
              </button>
            </div>
          </div>
        </div>
      </motion.aside>

      <main
        className="flex-1 transition-all duration-300"
        style={{ [i18n.language === 'ar' ? 'marginRight' : 'marginLeft']: sidebarVisible ? 280 : 80 }}
      >
        <header className="h-16 bg-white/80 dark:bg-dark-800/80 backdrop-blur-xl border-b border-slate-200 dark:border-dark-600 sticky top-0 z-40">
          <div className="h-full px-6 flex items-center justify-between">
            <div className="flex-1 max-w-xl">
              <div className="relative">
                <Search className="absolute start-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-500" />
                <input
                  type="text"
                  placeholder={t('common.searchPlaceholder')}
                  className="w-full ps-10 pe-4 py-2 bg-slate-50 dark:bg-dark-900 border border-slate-200 dark:border-dark-600 rounded-lg text-sm text-slate-900 dark:text-slate-200 placeholder-slate-400 dark:placeholder-slate-500 focus:outline-none focus:border-primary-500/50 focus:ring-1 focus:ring-primary-500/50"
                />
              </div>
            </div>

            <div className="flex items-center gap-4">
              <DemoToggle />

              <button
                onClick={toggleTheme}
                className="p-2 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg transition-colors flex items-center gap-2 text-slate-500 dark:text-slate-400 hover:text-slate-900 dark:hover:text-slate-200"
              >
                {theme === 'dark' ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
              </button>

              <div className="relative">
                <button
                  onClick={() => setLanguageMenuOpen(!languageMenuOpen)}
                  className="p-2 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg transition-colors flex items-center gap-2 text-slate-500 dark:text-slate-400 hover:text-slate-900 dark:hover:text-slate-200"
                >
                  <Globe className="w-5 h-5" />
                  <span className="text-sm font-medium">
                    {languages.find(l => l.code === i18n.language)?.short || 'EN'}
                  </span>
                </button>

                {languageMenuOpen && (
                  <>
                    <div
                      className="fixed inset-0 z-40"
                      onClick={() => setLanguageMenuOpen(false)}
                    />
                    <motion.div
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      exit={{ opacity: 0, y: 10 }}
                      className="absolute top-full end-0 mt-2 w-48 bg-white dark:bg-dark-800 rounded-xl shadow-lg border border-slate-200 dark:border-dark-600 py-1 z-50 overflow-hidden"
                    >
                      {languages.map((lang) => (
                        <button
                          key={lang.code}
                          onClick={() => {
                            i18n.changeLanguage(lang.code)
                            setLanguageMenuOpen(false)
                          }}
                          className={`w-full text-left px-4 py-2.5 text-sm hover:bg-slate-50 dark:hover:bg-dark-700 transition-colors flex items-center justify-between ${i18n.language === lang.code
                            ? 'text-primary-600 dark:text-primary-400 font-medium bg-primary-50 dark:bg-primary-900/10'
                            : 'text-slate-700 dark:text-slate-200'
                            }`}
                        >
                          <span>{lang.label}</span>
                          {i18n.language === lang.code && (
                            <div className="w-1.5 h-1.5 rounded-full bg-primary-500" />
                          )}
                        </button>
                      ))}
                    </motion.div>
                  </>
                )}
              </div>

              <button className="relative p-2 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg transition-colors">
                <Bell className="w-5 h-5 text-slate-500 dark:text-slate-400" />
                <span className="absolute top-1 right-1 w-2 h-2 bg-error rounded-full animate-pulse" />
              </button>

              <a
                href={(() => {
                  const userName = isDemo ? t('demo.demoVisitor') : (user?.displayName || t('common.adminUser'));
                  const userEmail = isDemo ? 'demo@example.com' : (user?.email || 'admin@transitcard.io');
                  const timestamp = new Date().toLocaleString(i18n.language);
                  const subject = encodeURIComponent(t('common.feedbackSubject', { userName }));
                  const body = encodeURIComponent(t('common.feedbackBody', { userName, userEmail, timestamp, language: i18n.language.toUpperCase() }));
                  return `mailto:benpay231119@gmail.com?subject=${subject}&body=${body}`;
                })()}
                className="p-2 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg transition-colors flex items-center gap-2 text-slate-500 dark:text-slate-400 hover:text-primary-600 dark:hover:text-primary-400"
                title={t('common.feedback')}
              >
                <MessageSquare className="w-5 h-5" />
                <span className="text-sm font-medium hidden md:inline">{t('common.feedback')}</span>
              </a>

              <div className="flex items-center gap-2 px-3 py-1.5 bg-slate-100 dark:bg-dark-700 rounded-lg border border-slate-200 dark:border-dark-600">
                <div className="w-2 h-2 rounded-full bg-success animate-pulse" />
                <span className="text-sm text-slate-700 dark:text-slate-300">{t('common.systemOnline')}</span>
              </div>
            </div>
          </div>
        </header>

        <div className="p-6">
          <Outlet />
        </div>
      </main>
      <PageHelpPanel currentPath={location.pathname} onStartTour={startTour} />

      {tourSteps.length > 0 && (
        <SpotlightTour
          steps={tourSteps}
          tourId={`tour-${location.pathname}`}
          isActive={isActive}
          currentStep={currentStep}
          onStepChange={goToStep}
          onComplete={stopTour}
          onSkip={stopTour}
        />
      )}
    </div>
  )
}

export default Layout
