import { useState, useEffect, useRef } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { useTranslation } from 'react-i18next';
import {
  User,
  Lock,
  Key,
  ArrowRight,
  Loader2,
  AlertCircle,
  Eye,
  EyeOff,
  Mail,
  Shield,
  ShieldCheck,
  Globe,
  Activity,
  CreditCard,
  Zap,
} from 'lucide-react';
import apiClient from '../api/client';
import LoginBackground from '../components/LoginBackground';

type LoginTab = 'credentials' | 'token';

interface LoginCredentials {
  email: string;
  password: string;
}

interface LoginResponse {
  access_token: string;
  refresh_token: string;
  user: {
    userId: string;
    email: string;
    username: string;
    displayName: string;
    role: string;
    status: string;
    projectId: string | null;
    permissions: Record<string, boolean>;
  };
  session: {
    expiresAt: string;
    type: string;
    defaultLanguage?: string;
  };
}

export default function Login() {

  const { t, i18n } = useTranslation();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [activeTab, setActiveTab] = useState<LoginTab>('credentials');
  const [isLoading, setIsLoading] = useState(false);
  const [isDemoLoading, setIsDemoLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [showPassword, setShowPassword] = useState(false);
  const [rememberMe, setRememberMe] = useState<boolean>(() => {
    const stored = localStorage.getItem('remember_me');
    if (stored === 'true') return true;
    if (stored === 'false') return false;
    return true;
  });
  const [failedAttempts, setFailedAttempts] = useState(0);
  const [isCaptchaRequired, setIsCaptchaRequired] = useState(false);
  const [captcha, setCaptcha] = useState('');
  const [requiresMfa, setRequiresMfa] = useState(false);
  const [mfaCode, setMfaCode] = useState('');
  const emailInputRef = useRef<HTMLInputElement | null>(null);

  const [credentials, setCredentials] = useState<LoginCredentials>({
    email: '',
    password: '',
  });
  const [token, setToken] = useState('');

  const currentLanguage = localStorage.getItem('i18nextLng') || 'en';

  const handleLanguageChange = (lang: string) => {
    i18n.changeLanguage(lang);
    localStorage.setItem('i18nextLng', lang);
  };

  useEffect(() => {
    const existingToken = localStorage.getItem('auth_token') || sessionStorage.getItem('auth_token');
    const demoToken = localStorage.getItem('demo_token');

    if (existingToken) {
      navigate('/');
      return;
    }

    if (demoToken) {
      navigate('/');
      return;
    }

    const magicToken = searchParams.get('token');
    if (magicToken) {
      handleMagicLinkLogin(magicToken);
    }

    const oauthError = searchParams.get('error');
    if (oauthError === 'oauth_failed') {
      setError(t('login.errors.oauthFailed'));
    }
  }, [navigate, searchParams]);

  useEffect(() => {
    if (activeTab === 'credentials' && emailInputRef.current) {
      emailInputRef.current.focus();
    }
  }, [activeTab]);

  const handleMagicLinkLogin = async (magicToken: string) => {
    setIsLoading(true);
    setError(null);

    try {
      const response = await apiClient.post<LoginResponse>('/auth/login/magic', {
        magicToken,
      });

      const { access_token, refresh_token, user } = response.data;

      localStorage.setItem('auth_token', access_token);
      localStorage.setItem('refresh_token', refresh_token);
      localStorage.setItem('user', JSON.stringify(user));
      localStorage.setItem('user_role', user.role);

      navigate('/');
    } catch (err: any) {
      const message = err.response?.data?.message;
      if (typeof message === 'string' && message.toLowerCase().includes('expired')) {
        setError(t('login.errors.magicLinkExpired'));
      } else {
        setError(t('login.errors.magicLinkInvalid'));
      }
    } finally {
      setIsLoading(false);
    }
  };

  const persistAuthSession = (accessToken: string, refreshToken: string, user: LoginResponse['user']) => {
    const primaryStorage = rememberMe ? localStorage : sessionStorage;
    const secondaryStorage = rememberMe ? sessionStorage : localStorage;

    primaryStorage.setItem('auth_token', accessToken);
    primaryStorage.setItem('refresh_token', refreshToken);
    primaryStorage.setItem('user', JSON.stringify(user));
    primaryStorage.setItem('user_role', user.role);

    secondaryStorage.removeItem('auth_token');
    secondaryStorage.removeItem('refresh_token');
    secondaryStorage.removeItem('user');
    secondaryStorage.removeItem('user_role');

    localStorage.setItem('remember_me', rememberMe ? 'true' : 'false');
  };

  const handleCredentialsLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);

    if (isCaptchaRequired && !captcha.trim()) {
      setError(t('login.errors.captchaRequired'));
      return;
    }

    if (requiresMfa && !mfaCode.trim()) {
      setError(t('login.errors.mfaRequired'));
      return;
    }

    setIsLoading(true);

    try {
      const response = await apiClient.post<LoginResponse>('/auth/login', {
        ...credentials,
        captcha: isCaptchaRequired ? captcha : undefined,
        mfaCode: requiresMfa ? mfaCode : undefined,
      });

      const { access_token, refresh_token, user } = response.data;

      persistAuthSession(access_token, refresh_token, user);
      setFailedAttempts(0);
      setIsCaptchaRequired(false);
      setCaptcha('');
      setRequiresMfa(false);
      setMfaCode('');

      navigate('/');
    } catch (err: any) {
      const message = err.response?.data?.message;

      if (typeof message === 'string') {
        const lower = message.toLowerCase();

        if (lower.includes('captcha required')) {
          setIsCaptchaRequired(true);
          setError(t('login.errors.captchaRequired'));
        } else if (lower.includes('mfa required')) {
          setRequiresMfa(true);
          setError(t('login.errors.mfaRequired'));
        } else if (lower.includes('invalid credentials')) {
          const nextAttempts = failedAttempts + 1;
          setFailedAttempts(nextAttempts);
          if (nextAttempts >= 3) {
            setIsCaptchaRequired(true);
            setError(t('login.errors.captchaRequired'));
          } else {
            setError(t('login.errors.invalidCredentials'));
          }
        } else if (lower.includes('locked')) {
          setError(t('login.errors.accountLocked'));
        } else if (lower.includes('not active') || lower.includes('inactive')) {
          setError(t('login.errors.accountInactive'));
        } else {
          setError(t('login.error'));
        }
      } else {
        setError(t('login.error'));
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleTokenLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);

    try {
      const response = await apiClient.post<LoginResponse>('/auth/login/token', {
        token,
      });

      const { access_token, refresh_token, user, session } = response.data;

      // Automatically switch language if token has a default one
      if (session.defaultLanguage) {
        i18n.changeLanguage(session.defaultLanguage);
        localStorage.setItem('i18nextLng', session.defaultLanguage);
      }

      if (session.type === 'demo') {
        localStorage.setItem('demo_token', access_token);
        localStorage.setItem('transitcard_demo_mode', 'true');
      } else {
        localStorage.setItem('auth_token', access_token);
        localStorage.setItem('refresh_token', refresh_token);
      }

      localStorage.setItem('user', JSON.stringify(user));
      localStorage.setItem('user_role', user.role);

      navigate('/');
    } catch (err: any) {
      const msg = err.response?.data?.message;
      if (typeof msg === 'string' && msg.toLowerCase().includes('expired')) {
        setError(t('login.errors.tokenExpired'));
      } else if (typeof msg === 'string') {
        setError(t('login.errors.tokenInvalid'));
      } else {
        setError(t('login.error'));
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleDemoLogin = async () => {
    setIsDemoLoading(true);
    setError(null);

    try {
      const response = await apiClient.post('/demo/token');
      const { token: demoToken, expiresAt } = response.data;

      localStorage.setItem('demo_token', demoToken);
      localStorage.setItem('demo_expires_at', expiresAt);
      localStorage.setItem('transitcard_demo_mode', 'true');
      const demoUser = {
        userId: 'demo',
        email: 'demo@example.com',
        username: 'demo',
        displayName: t('demo.demoVisitor'),
        role: 'demo',
        status: 'active',
        projectId: null,
        permissions: {}
      };
      localStorage.setItem('user', JSON.stringify(demoUser));
      localStorage.setItem('user_role', 'demo');

      navigate('/');
    } catch (err: any) {
      const message = err.response?.data?.message;
      if (typeof message === 'string') {
        setError(t('login.errors.demoFailed'));
      } else {
        setError(t('login.error'));
      }
    } finally {
      setIsDemoLoading(false);
    }
  };

  const handleGoogleLogin = () => {
    const apiUrl = import.meta.env.VITE_API_URL || '';
    window.location.href = `${apiUrl}/api/auth/google`;
  };

  const handleForgotPassword = () => {
    alert(t('login.forgotPasswordHint'));
  };

  const tabs = [
    { id: 'credentials' as LoginTab, label: t('login.tabs.credentials'), icon: User },
    { id: 'token' as LoginTab, label: t('login.tabs.token'), icon: Key },
  ];

  return (
    <div className="min-h-screen bg-slate-900 flex overflow-hidden">
      {/* Background Effects */}
      <div className="absolute inset-0 z-0 overflow-hidden">
        {/* Animated Background Canvas */}
        <LoginBackground />

        {/* Overlay Gradient for readability */}
        <div className="absolute inset-0 bg-slate-900/30 "></div>

        {/* Dynamic Lines - Abstract Representation */}
        <div className="absolute top-0 left-0 w-full h-full opacity-30 pointer-events-none">
          <svg className="w-full h-full" viewBox="0 0 100 100" preserveAspectRatio="none">
            <path d="M0 50 Q 25 20 50 50 T 100 50" stroke="url(#gradient1)" strokeWidth="0.1" fill="none" className="animate-pulse-slow">
              <animate attributeName="d" dur="10s" repeatCount="indefinite" values="M0 50 Q 25 20 50 50 T 100 50; M0 50 Q 25 80 50 50 T 100 50; M0 50 Q 25 20 50 50 T 100 50" />
            </path>
            <path d="M0 60 Q 25 30 50 60 T 100 60" stroke="url(#gradient2)" strokeWidth="0.1" fill="none" className="animate-pulse-slow" style={{ animationDelay: '1s' }}>
              <animate attributeName="d" dur="12s" repeatCount="indefinite" values="M0 60 Q 25 30 50 60 T 100 60; M0 60 Q 25 90 50 60 T 100 60; M0 60 Q 25 30 50 60 T 100 60" />
            </path>
            <defs>
              <linearGradient id="gradient1" x1="0%" y1="0%" x2="100%" y2="0%">
                <stop offset="0%" stopColor="#0891b2" stopOpacity="0" />
                <stop offset="50%" stopColor="#22d3ee" stopOpacity="0.8" />
                <stop offset="100%" stopColor="#0891b2" stopOpacity="0" />
              </linearGradient>
              <linearGradient id="gradient2" x1="0%" y1="0%" x2="100%" y2="0%">
                <stop offset="0%" stopColor="#a855f7" stopOpacity="0" />
                <stop offset="50%" stopColor="#c084fc" stopOpacity="0.8" />
                <stop offset="100%" stopColor="#a855f7" stopOpacity="0" />
              </linearGradient>
            </defs>
          </svg>
        </div>
      </div>
      <div className="absolute top-[-20%] right-[-10%] w-[50%] h-[50%] bg-primary-500/10 blur-[120px] rounded-full"></div>
      <div className="absolute bottom-[-20%] left-[-10%] w-[40%] h-[40%] bg-accent-500/10 blur-[100px] rounded-full"></div>

      <div className="container mx-auto flex w-full relative z-10">

        {/* Left Panel: Branding & Stats */}
        <div className="hidden lg:flex flex-col justify-center w-1/2 p-12 text-white">
          <motion.div
            initial={{ opacity: 0, x: -50 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6 }}
          >
            <div className="flex items-center gap-3 mb-8">
              <div className="w-12 h-12 bg-primary-600 rounded-xl flex items-center justify-center shadow-lg shadow-primary-500/20">
                <ShieldCheck className="w-7 h-7 text-white" />
              </div>
              <h1 className="text-3xl font-display font-bold tracking-tight">
                {t('login.brand.name')} <span className="text-primary-400">{t('login.brand.badge')}</span>
              </h1>
            </div>

            <h2 className="text-5xl font-bold leading-tight mb-6 bg-gradient-to-r from-white via-white to-slate-400 text-transparent bg-clip-text">
              {t('login.brand.headlineLine1')} <br />
              {t('login.brand.headlineLine2')}
            </h2>

            <p className="text-lg text-slate-400 mb-12 max-w-lg">
              {t('login.brand.description')}
            </p>

            {/* Live Stats Mockup */}
            <div className="grid grid-cols-2 gap-4 max-w-lg">
              <div className="p-4 bg-white/5 backdrop-blur-md border border-white/10 rounded-xl">
                <div className="flex items-center gap-2 mb-2 text-primary-300">
                  <Activity className="w-4 h-4" />
                  <span className="text-xs font-semibold uppercase tracking-wider">{t('login.stats.uptime.label')}</span>
                </div>
                <div className="text-2xl font-mono font-bold">99.99%</div>
                <div className="text-xs text-slate-500 mt-1">{t('login.stats.uptime.sublabel')}</div>
              </div>
              <div className="p-4 bg-white/5 backdrop-blur-md border border-white/10 rounded-xl">
                <div className="flex items-center gap-2 mb-2 text-accent-300">
                  <Zap className="w-4 h-4" />
                  <span className="text-xs font-semibold uppercase tracking-wider">{t('login.stats.transactions.label')}</span>
                </div>
                <div className="text-2xl font-mono font-bold">12M+</div>
                <div className="text-xs text-slate-500 mt-1">{t('login.stats.transactions.sublabel')}</div>
              </div>
            </div>
          </motion.div>
        </div>

        {/* Right Panel: Login Form */}
        <div className="w-full lg:w-1/2 flex flex-col justify-center items-center p-4">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
            className="w-full max-w-md"
          >
            {/* Language & Support Header */}
            <div className="flex justify-end mb-6 gap-4">
              <div className="flex items-center gap-2 px-3 py-1.5 rounded-full bg-white/5 border border-white/10 hover:bg-white/10 transition-colors backdrop-blur-sm cursor-pointer">
                <Globe className="w-4 h-4 text-slate-400" />
                <select
                  value={currentLanguage}
                  onChange={(e) => handleLanguageChange(e.target.value)}
                  className="bg-transparent text-xs font-medium text-slate-300 border-none focus:ring-0 cursor-pointer outline-none"
                >
                  <option value="en" className="bg-slate-800">{t('login.languages.en')}</option>
                  <option value="zh" className="bg-slate-800">{t('login.languages.zh')}</option>
                  <option value="fr" className="bg-slate-800">{t('login.languages.fr')}</option>
                  <option value="es" className="bg-slate-800">{t('login.languages.es')}</option>
                  <option value="ar" className="bg-slate-800">{t('login.languages.ar')}</option>
                </select>
              </div>
            </div>

            {/* Login Card */}
            <div className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-2xl shadow-[0_8px_32px_0_rgba(0,0,0,0.37)] overflow-hidden relative">

              {/* Decorative top highlight */}
              <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-primary-500 via-accent-500 to-primary-500"></div>

              <div className="p-8">
                <div className="mb-8 text-center lg:text-left">
                  <h3 className="text-2xl font-bold text-white mb-2">{t('login.title')}</h3>
                  <p className="text-slate-400 text-sm">{t('login.subtitle')}</p>
                </div>

                {/* Custom Tabs */}
                <div className="flex gap-1 mb-8 p-1 bg-black/20 rounded-lg">
                  {tabs.map((tab) => (
                    <button
                      key={tab.id}
                      onClick={() => {
                        setActiveTab(tab.id);
                        setError(null);
                      }}
                      className={`flex-1 flex items-center justify-center gap-2 py-2 px-3 rounded-md text-sm font-medium transition-all duration-200 ${activeTab === tab.id
                        ? 'bg-primary-600/90 text-white shadow-sm ring-1 ring-white/10'
                        : 'text-slate-400 hover:text-white hover:bg-white/5'
                        }`}
                    >
                      <tab.icon className="w-4 h-4" />
                      <span>{tab.label}</span>
                    </button>
                  ))}
                </div>

                <AnimatePresence mode="wait">
                  {error && (
                    <motion.div
                      initial={{ opacity: 0, height: 0 }}
                      animate={{ opacity: 1, height: 'auto' }}
                      exit={{ opacity: 0, height: 0 }}
                      className="mb-6 p-3 bg-red-500/10 border border-red-500/20 rounded-lg flex items-start gap-3"
                    >
                      <AlertCircle className="w-4 h-4 text-red-400 flex-shrink-0 mt-0.5" />
                      <span className="text-sm text-red-200">{error}</span>
                    </motion.div>
                  )}
                </AnimatePresence>

                <AnimatePresence mode="wait">
                  {activeTab === 'credentials' && (
                    <motion.form
                      key="credentials"
                      initial={{ opacity: 0, x: 20 }}
                      animate={{ opacity: 1, x: 0 }}
                      exit={{ opacity: 0, x: -20 }}
                      transition={{ duration: 0.2 }}
                      onSubmit={handleCredentialsLogin}
                      className="space-y-5"
                    >
                      <div>
                        <label className="block text-xs font-medium text-slate-300 mb-1.5 uppercase tracking-wide">
                          {t('login.email')}
                        </label>
                        <div className="relative group">
                          <Mail className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-500 group-focus-within:text-primary-400 transition-colors" />
                          <input
                            ref={emailInputRef}
                            type="email"
                            value={credentials.email}
                            onChange={(e) => setCredentials({ ...credentials, email: e.target.value })}
                            className="w-full pl-10 pr-4 py-2.5 bg-black/20 border border-white/10 rounded-lg text-white placeholder-slate-600 focus:outline-none focus:ring-2 focus:ring-primary-500/50 focus:border-primary-500/50 transition-all"
                            placeholder={t('login.placeholders.email')}
                            required
                          />
                        </div>
                      </div>

                      <div>
                        <label className="block text-xs font-medium text-slate-300 mb-1.5 uppercase tracking-wide">
                          {t('login.password')}
                        </label>
                        <div className="relative group">
                          <Lock className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-500 group-focus-within:text-primary-400 transition-colors" />
                          <input
                            type={showPassword ? 'text' : 'password'}
                            value={credentials.password}
                            onChange={(e) => setCredentials({ ...credentials, password: e.target.value })}
                            className="w-full pl-10 pr-10 py-2.5 bg-black/20 border border-white/10 rounded-lg text-white placeholder-slate-600 focus:outline-none focus:ring-2 focus:ring-primary-500/50 focus:border-primary-500/50 transition-all"
                            placeholder=""
                            required
                          />
                          <button
                            type="button"
                            onClick={() => setShowPassword(!showPassword)}
                            className="absolute right-3 top-1/2 -translate-y-1/2 text-slate-500 hover:text-slate-300 transition-colors"
                          >
                            {showPassword ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
                          </button>
                        </div>
                      </div>

                      {isCaptchaRequired && (
                        <div>
                          <label className="block text-xs font-medium text-slate-300 mb-1.5 uppercase tracking-wide">
                            {t('login.captchaLabel')}
                          </label>
                          <input
                            type="text"
                            value={captcha}
                            onChange={(e) => setCaptcha(e.target.value)}
                            className="w-full px-4 py-2.5 bg-black/20 border border-white/10 rounded-lg text-white placeholder-slate-600 focus:outline-none focus:ring-2 focus:ring-primary-500/50 focus:border-primary-500/50 transition-all"
                            placeholder={t('login.placeholders.captcha')}
                            required={isCaptchaRequired}
                          />
                        </div>
                      )}

                      {requiresMfa && (
                        <div>
                          <label className="block text-xs font-medium text-slate-300 mb-1.5 uppercase tracking-wide">
                            {t('login.mfaLabel')}
                          </label>
                          <input
                            type="text"
                            value={mfaCode}
                            onChange={(e) => setMfaCode(e.target.value)}
                            className="w-full px-4 py-2.5 bg-black/20 border border-white/10 rounded-lg text-white placeholder-slate-600 focus:outline-none focus:ring-2 focus:ring-primary-500/50 focus:border-primary-500/50 transition-all"
                            placeholder={t('login.placeholders.mfa')}
                            required={requiresMfa}
                          />
                        </div>
                      )}

                      <div className="flex items-center justify-between text-xs text-slate-400">
                        <label className="inline-flex items-center gap-2 cursor-pointer select-none">
                          <input
                            type="checkbox"
                            checked={rememberMe}
                            onChange={(e) => {
                              const checked = e.target.checked;
                              setRememberMe(checked);
                              localStorage.setItem('remember_me', checked ? 'true' : 'false');
                            }}
                            className="w-3.5 h-3.5 rounded border-white/20 bg-black/30 text-primary-500 focus:ring-primary-500/60"
                          />
                          <span>{t('login.rememberMe')}</span>
                        </label>
                        <button
                          type="button"
                          onClick={handleForgotPassword}
                          className="text-primary-300 hover:text-primary-200 transition-colors"
                        >
                          {t('login.forgotPassword')}
                        </button>
                      </div>

                      <button
                        type="submit"
                        disabled={isLoading}
                        className="w-full py-3 px-4 bg-gradient-to-r from-primary-600 to-primary-500 hover:from-primary-500 hover:to-primary-400 disabled:from-slate-700 disabled:to-slate-800 disabled:text-slate-500 text-white rounded-lg font-bold flex items-center justify-center gap-2 transition-all transform hover:scale-[1.02] shadow-lg shadow-primary-900/20"
                      >
                        {isLoading ? (
                          <Loader2 className="w-5 h-5 animate-spin" />
                        ) : (
                          <>
                            {t('login.signIn')}
                            <ArrowRight className="w-5 h-5" />
                          </>
                        )}
                      </button>
                    </motion.form>
                  )}

                  {activeTab === 'token' && (
                    <motion.form
                      key="token"
                      initial={{ opacity: 0, x: 20 }}
                      animate={{ opacity: 1, x: 0 }}
                      exit={{ opacity: 0, x: -20 }}
                      transition={{ duration: 0.2 }}
                      onSubmit={handleTokenLogin}
                      className="space-y-5"
                    >
                      <div>
                        <label className="block text-xs font-medium text-slate-300 mb-1.5 uppercase tracking-wide">
                          {t('login.accessToken')}
                        </label>
                        <div className="relative group">
                          <Key className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-500 group-focus-within:text-primary-400 transition-colors" />
                          <input
                            type="text"
                            value={token}
                            onChange={(e) => setToken(e.target.value)}
                            className="w-full pl-10 pr-4 py-2.5 bg-black/20 border border-white/10 rounded-lg text-white placeholder-slate-600 focus:outline-none focus:ring-2 focus:ring-primary-500/50 focus:border-primary-500/50 transition-all font-mono text-sm"
                            placeholder={t('login.placeholders.accessToken')}
                            required
                          />
                        </div>
                        <p className="mt-2 text-xs text-slate-500">
                          {t('login.tokenHelp')}
                        </p>
                      </div>

                      <button
                        type="submit"
                        disabled={isLoading}
                        className="w-full py-3 px-4 bg-gradient-to-r from-primary-600 to-primary-500 hover:from-primary-500 hover:to-primary-400 disabled:from-slate-700 disabled:to-slate-800 disabled:text-slate-500 text-white rounded-lg font-bold flex items-center justify-center gap-2 transition-all transform hover:scale-[1.02] shadow-lg shadow-primary-900/20"
                      >
                        {isLoading ? (
                          <Loader2 className="w-5 h-5 animate-spin" />
                        ) : (
                          <>
                            {t('login.signInWithToken')}
                            <ArrowRight className="w-5 h-5" />
                          </>
                        )}
                      </button>
                    </motion.form>
                  )}
                </AnimatePresence>

                <div className="mt-8 pt-6 border-t border-white/10">
                  <button
                    onClick={handleGoogleLogin}
                    className="w-full py-2.5 px-4 bg-white/5 hover:bg-white/10 border border-white/10 text-white rounded-lg font-medium flex items-center justify-center gap-3 transition-colors"
                  >
                    <svg className="w-5 h-5" viewBox="0 0 24 24">
                      <path
                        fill="currentColor"
                        d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
                      />
                      <path
                        fill="currentColor"
                        d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
                      />
                      <path
                        fill="currentColor"
                        d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
                      />
                      <path
                        fill="currentColor"
                        d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
                      />
                    </svg>
                    <span className="text-sm">{t('login.signInWithGoogle')}</span>
                  </button>
                </div>

              </div>

              {/* Demo Mode Footer Link */}
              <div className="bg-black/40 p-4 text-center backdrop-blur-sm">
                <button
                  onClick={handleDemoLogin}
                  disabled={isDemoLoading}
                  className="text-sm text-slate-300 hover:text-white transition-colors flex items-center justify-center gap-2 mx-auto border border-white/10 rounded-lg px-4 py-2 hover:border-white/30"
                >
                  {isDemoLoading ? (
                    <Loader2 className="w-4 h-4 animate-spin" />
                  ) : (
                    <Zap className="w-4 h-4 text-accent-400" />
                  )}
                  <span>
                    {isDemoLoading ? t('login.demoInitializing') : t('login.demoCta')}
                  </span>
                </button>
              </div>
            </div>

            {/* Footer Info */}
            <div className="mt-8 flex justify-between items-center text-xs text-slate-500 w-full px-2">
              <span>{t('login.footerInfo.version', { version: '2.4.0', build: '8921' })}</span>
              <div className="flex gap-4">
                <div className="flex items-center gap-1" title={t('login.footerInfo.isoTitle')}>
                  <Shield className="w-3 h-3" />
                  <span>{t('login.footerInfo.isoLabel')}</span>
                </div>
                <div className="flex items-center gap-1" title={t('login.footerInfo.pciTitle')}>
                  <CreditCard className="w-3 h-3" />
                  <span>{t('login.footerInfo.pciLabel')}</span>
                </div>
              </div>
            </div>

          </motion.div>
        </div>
      </div>
    </div>
  );
}
