// Demo data flag - can be controlled via environment variable or localStorage
const DEMO_MODE_KEY = 'transitcard_demo_mode'

export const isDemoMode = (): boolean => {
  // Check localStorage first (for runtime toggle)
  const localStorageValue = localStorage.getItem(DEMO_MODE_KEY)
  if (localStorageValue !== null) {
    return localStorageValue === 'true'
  }
  
  // Fall back to environment variable
  return import.meta.env.VITE_DEMO_MODE === 'true'
}

export const setDemoMode = (enabled: boolean): void => {
  localStorage.setItem(DEMO_MODE_KEY, enabled.toString())
  // Reload page to apply changes
  window.location.reload()
}

export const toggleDemoMode = (): void => {
  setDemoMode(!isDemoMode())
}