import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { useTranslation } from 'react-i18next';
import {
  Play,
  Clock,
  Eye,
  Shield,
  Globe,
  ArrowRight,
  CheckCircle,
  AlertCircle,
  Loader2,
} from 'lucide-react';
import apiClient from '../api/client';

interface DemoSession {
  token: string;
  sessionId: string;
  expiresAt: string;
  expiresIn: number;
  mode: string;
  restrictions: {
    readOnly: boolean;
    maxSessionTime: number;
    allowedOperations: string[];
    disallowedOperations: string[];
  };
}

export default function DemoLogin() {
  const { t, i18n } = useTranslation();
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [countdown, setCountdown] = useState(30 * 60);

  useEffect(() => {
    const existingToken = localStorage.getItem('demo_token');
    const expiresAt = localStorage.getItem('demo_expires_at');
    
    if (existingToken && expiresAt) {
      const expiresTime = new Date(expiresAt).getTime();
      const now = Date.now();
      
      if (now < expiresTime) {
        navigate('/');
        return;
      } else {
        localStorage.removeItem('demo_token');
        localStorage.removeItem('demo_expires_at');
        localStorage.removeItem('demo_mode');
      }
    }
  }, [navigate]);

  useEffect(() => {
    const timer = setInterval(() => {
      setCountdown((prev) => (prev > 0 ? prev - 1 : 0));
    }, 1000);
    return () => clearInterval(timer);
  }, []);

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  const handleStartDemo = async () => {
    setIsLoading(true);
    setError(null);

    try {
      const response = await apiClient.post<DemoSession>('/demo/token');
      const { token, expiresAt } = response.data;

      localStorage.setItem('demo_token', token);
      localStorage.setItem('demo_expires_at', expiresAt);
      localStorage.setItem('demo_mode', 'true');

      window.dispatchEvent(new Event('demo-login-success'));

      navigate('/', { state: { demoMode: true } });
    } catch (err: any) {
      console.error('Demo login error:', err);
      let errorMessage: string;
      
      if (err.code === 'ECONNREFUSED' || err.code === 'ERR_NETWORK') {
        errorMessage = 'Cannot connect to server. Please ensure the backend is running on port 6001.';
      } else if (err.response?.status === 404) {
        errorMessage = 'Demo API endpoint not found. Please check if the backend is properly configured.';
      } else if (err.response?.status === 429) {
        errorMessage = 'Too many demo sessions. Please try again later.';
      } else {
        errorMessage = err.response?.data?.message || err.message || 'Failed to start demo session. Please try again.';
      }
      
      setError(errorMessage);
    } finally {
      setIsLoading(false);
    }
  };

  const features = [
    {
      icon: Eye,
      title: t('demo.features.view.title', 'Full System Access'),
      description: t(
        'demo.features.view.description',
        'Explore all features and interfaces without registration'
      ),
    },
    {
      icon: Clock,
      title: t('demo.features.time.title', '30-Minute Session'),
      description: t(
        'demo.features.time.description',
        'Plenty of time to evaluate the system capabilities'
      ),
    },
    {
      icon: Shield,
      title: t('demo.features.safe.title', 'Safe Environment'),
      description: t(
        'demo.features.safe.description',
        'Read-only mode with sample data - no risk to production'
      ),
    },
    {
      icon: Globe,
      title: t('demo.features.i18n.title', 'Multi-Language'),
      description: t(
        'demo.features.i18n.description',
        'Experience in your preferred language'
      ),
    },
  ];

  const restrictions = [
    t('demo.restrictions.readOnly', 'Read-only access'),
    t('demo.restrictions.sampleData', 'Sample data only'),
    t('demo.restrictions.noExport', 'No data export'),
    t('demo.restrictions.timeLimit', '30-minute session limit'),
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 dark:from-gray-900 dark:to-gray-800 flex items-center justify-center p-4">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="w-full max-w-4xl"
      >
        <div className="bg-white dark:bg-gray-800 rounded-2xl shadow-2xl overflow-hidden">
          <div className="grid md:grid-cols-2">
            <div className="p-8 md:p-12">
              <motion.div
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.2 }}
              >
                <div className="flex items-center gap-3 mb-6">
                  <div className="w-12 h-12 bg-blue-600 rounded-xl flex items-center justify-center">
                    <Play className="w-6 h-6 text-white" />
                  </div>
                  <div>
                    <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
                      {t('demo.title', 'Live Demo')}
                    </h1>
                    <p className="text-sm text-gray-500 dark:text-gray-400">
                      {t('demo.subtitle', 'Experience TransitCard Pro')}
                    </p>
                  </div>
                </div>

                <p className="text-gray-600 dark:text-gray-300 mb-8">
                  {t(
                    'demo.description',
                    'Get instant access to our full-featured demo environment. No registration required - just click and explore.'
                  )}
                </p>

                {error && (
                  <motion.div
                    initial={{ opacity: 0, height: 0 }}
                    animate={{ opacity: 1, height: 'auto' }}
                    className="mb-6 p-4 bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg"
                  >
                    <div className="flex items-center gap-3 mb-2">
                      <AlertCircle className="w-5 h-5 text-red-600 dark:text-red-400 flex-shrink-0" />
                      <span className="text-sm font-medium text-red-700 dark:text-red-300">
                        Error Starting Demo
                      </span>
                    </div>
                    <p className="text-sm text-red-600 dark:text-red-400 ml-8">
                      {error}
                    </p>
                    <p className="text-xs text-red-500 dark:text-red-400 mt-2 ml-8">
                      API URL: {import.meta.env.VITE_API_URL || 'http://localhost:4001/api/v1'}
                    </p>
                  </motion.div>
                )}

                <button
                  onClick={handleStartDemo}
                  disabled={isLoading}
                  className="w-full mb-6 py-4 px-6 bg-blue-600 hover:bg-blue-700 disabled:bg-blue-400 text-white rounded-xl font-semibold flex items-center justify-center gap-2 transition-all duration-200 shadow-lg hover:shadow-xl"
                >
                  {isLoading ? (
                    <>
                      <Loader2 className="w-5 h-5 animate-spin" />
                      {t('demo.loading', 'Starting demo...')}
                    </>
                  ) : (
                    <>
                      {t('demo.startButton', 'Start Demo Session')}
                      <ArrowRight className="w-5 h-5" />
                    </>
                  )}
                </button>

                <div className="p-4 bg-amber-50 dark:bg-amber-900/20 border border-amber-200 dark:border-amber-800 rounded-lg mb-6">
                  <div className="flex items-center gap-2 mb-2">
                    <Clock className="w-4 h-4 text-amber-600 dark:text-amber-400" />
                    <span className="text-sm font-medium text-amber-800 dark:text-amber-200">
                      {t('demo.sessionDuration', 'Session Duration')}
                    </span>
                  </div>
                  <p className="text-sm text-amber-700 dark:text-amber-300">
                    {t(
                      'demo.sessionDescription',
                      'Your demo session will last 30 minutes. You can explore all features during this time.'
                    )}
                  </p>
                </div>

                <div className="space-y-2">
                  <h3 className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-3">
                    {t('demo.restrictionsTitle', 'Demo Restrictions')}
                  </h3>
                  {restrictions.map((restriction, index) => (
                    <div
                      key={index}
                      className="flex items-center gap-2 text-sm text-gray-600 dark:text-gray-400"
                    >
                      <CheckCircle className="w-4 h-4 text-green-500" />
                      <span>{restriction}</span>
                    </div>
                  ))}
                </div>
              </motion.div>
            </div>

            <div className="bg-gray-50 dark:bg-gray-700/50 p-8 md:p-12">
              <motion.div
                initial={{ opacity: 0, x: 20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.3 }}
              >
                <h2 className="text-lg font-semibold text-gray-900 dark:text-white mb-6">
                  {t('demo.featuresTitle', 'What You Get')}
                </h2>

                <div className="space-y-4">
                  {features.map((feature, index) => (
                    <motion.div
                      key={index}
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      transition={{ delay: 0.4 + index * 0.1 }}
                      className="flex items-start gap-4 p-4 bg-white dark:bg-gray-700 rounded-lg shadow-sm"
                    >
                      <div className="w-10 h-10 bg-blue-100 dark:bg-blue-900/30 rounded-lg flex items-center justify-center flex-shrink-0">
                        <feature.icon className="w-5 h-5 text-blue-600 dark:text-blue-400" />
                      </div>
                      <div>
                        <h3 className="font-medium text-gray-900 dark:text-white">
                          {feature.title}
                        </h3>
                        <p className="text-sm text-gray-600 dark:text-gray-300 mt-1">
                          {feature.description}
                        </p>
                      </div>
                    </motion.div>
                  ))}
                </div>

                <div className="mt-8 p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
                  <p className="text-sm text-blue-800 dark:text-blue-200">
                    {t(
                      'demo.helpText',
                      'Need help? Our interactive guide will assist you throughout the demo.'
                    )}
                  </p>
                </div>
              </motion.div>
            </div>
          </div>
        </div>

        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.8 }}
          className="mt-8 text-center"
        >
          <p className="text-sm text-gray-500 dark:text-gray-400">
            {t('demo.footer', 'TransitCard Pro - Financial-grade Transit Management')}
          </p>
        </motion.div>
      </motion.div>
    </div>
  );
}
