import { useState, useEffect, useCallback, useRef } from 'react'
import { createPortal } from 'react-dom'
import { motion, AnimatePresence } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
    AlertTriangle,
    CheckCircle,
    XCircle,
    Info,
    Loader2,
    X
} from 'lucide-react'

// 
export type ConfirmDialogType = 'warning' | 'danger' | 'success' | 'info'

export interface ConfirmDialogData {
    type?: ConfirmDialogType
    title: string
    message: string
    details?: { label: string; value: string }[]
    riskWarning?: string
    confirmText?: string
    cancelText?: string
    countdown?: number // 
    requireConfirmText?: string // required confirmation text
}

interface ConfirmDialogProps {
    open: boolean
    data: ConfirmDialogData
    isLoading?: boolean
    onConfirm: () => void
    onCancel: () => void
}

const typeConfig: Record<ConfirmDialogType, { icon: typeof AlertTriangle; color: string; bg: string }> = {
    warning: { icon: AlertTriangle, color: 'text-warning', bg: 'bg-warning/15' },
    danger: { icon: XCircle, color: 'text-error', bg: 'bg-error/15' },
    success: { icon: CheckCircle, color: 'text-success', bg: 'bg-success/15' },
    info: { icon: Info, color: 'text-primary-600', bg: 'bg-primary-100 dark:bg-primary-900/30' }
}

export function ConfirmDialog({
    open,
    data,
    isLoading = false,
    onConfirm,
    onCancel
}: ConfirmDialogProps) {
    const { t } = useTranslation()
    const [countdown, setCountdown] = useState(0)
    const [confirmInput, setConfirmInput] = useState('')

    const type = data.type || 'warning'
    const config = typeConfig[type] || typeConfig.warning
    const Icon = config.icon

    // 
    useEffect(() => {
        if (open) {
            setCountdown(data.countdown || 0)
            setConfirmInput('')
        }
    }, [open, data.countdown])

    // 
    useEffect(() => {
        if (!open || countdown <= 0) return

        const timer = setInterval(() => {
            setCountdown(prev => Math.max(0, prev - 1))
        }, 1000)

        return () => clearInterval(timer)
    }, [open, countdown])

    // 
    const handleKeyDown = useCallback((e: KeyboardEvent) => {
        if (!open) return
        if (e.key === 'Escape') {
            onCancel()
        }
    }, [open, onCancel])

    useEffect(() => {
        if (open) {
            document.addEventListener('keydown', handleKeyDown)
            return () => document.removeEventListener('keydown', handleKeyDown)
        }
    }, [open, handleKeyDown])

    // Whether can confirm
    const canConfirm =
        countdown === 0 &&
        !isLoading &&
        (!data.requireConfirmText || confirmInput === data.requireConfirmText)

    //  Portal  body
    return createPortal(
        <AnimatePresence>
            {open && (
                <div className="fixed inset-0 flex items-center justify-center p-4 z-[9999]">
                    {/*  */}
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        onClick={onCancel}
                        className="fixed inset-0 bg-black/60 backdrop-blur-[4px]"
                    />

                    {/*  */}
                    <motion.div
                        initial={{ opacity: 0, scale: 0.95, y: 20 }}
                        animate={{ opacity: 1, scale: 1, y: 0 }}
                        exit={{ opacity: 0, scale: 0.95, y: 20 }}
                        transition={{ type: 'spring', duration: 0.3 }}
                        className="bg-white dark:bg-dark-800 rounded-2xl shadow-2xl border border-slate-200 dark:border-dark-600 overflow-hidden w-full max-w-md relative z-10"
                    >
                        {/*  */}
                        <div className="flex items-start gap-4 p-6 pb-4">
                            <div className={`flex-shrink-0 p-3 rounded-xl ${config.bg}`}>
                                <Icon className={`w-6 h-6 ${config.color}`} />
                            </div>
                            <div className="flex-1 min-w-0">
                                <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100">
                                    {data.title}
                                </h3>
                                <p className="mt-1 text-sm text-slate-600 dark:text-slate-400 whitespace-pre-wrap">
                                    {data.message}
                                </p>
                            </div>
                            <button
                                onClick={onCancel}
                                className="flex-shrink-0 p-1 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg transition-colors"
                            >
                                <X className="w-5 h-5 text-slate-400" />
                            </button>
                        </div>

                        {/*  */}
                        {data.details && data.details.length > 0 && (
                            <div className="mx-6 p-4 bg-slate-50 dark:bg-dark-900 rounded-xl space-y-2">
                                {data.details.map((detail, index) => (
                                    <div key={index} className="flex justify-between text-sm">
                                        <span className="text-slate-500">{detail.label}</span>
                                        <span className="font-mono font-medium text-slate-900 dark:text-slate-100">
                                            {detail.value}
                                        </span>
                                    </div>
                                ))}
                            </div>
                        )}

                        {/*  */}
                        {data.riskWarning && (
                            <div className="mx-6 mt-4 p-3 bg-error/10 border border-error/20 rounded-xl">
                                <div className="flex items-start gap-2">
                                    <AlertTriangle className="w-4 h-4 text-error flex-shrink-0 mt-0.5" />
                                    <p className="text-sm text-error font-medium">{data.riskWarning}</p>
                                </div>
                            </div>
                        )}

                        {/*  */}
                        {data.requireConfirmText && (
                            <div className="mx-6 mt-4">
                                <label className="block text-sm text-slate-600 dark:text-slate-400 mb-2">
                                    {t('common.typeToConfirm', { text: data.requireConfirmText })}
                                </label>
                                <input
                                    type="text"
                                    autoFocus
                                    value={confirmInput}
                                    onChange={(e) => setConfirmInput(e.target.value)}
                                    placeholder={data.requireConfirmText}
                                    className="w-full px-4 py-2 bg-slate-50 dark:bg-dark-900 border border-slate-200 dark:border-dark-600 rounded-lg text-slate-900 dark:text-slate-100 focus:outline-none focus:ring-2 focus:ring-primary-500/20 focus:border-primary-500"
                                />
                            </div>
                        )}

                        {/*  */}
                        <div className="flex gap-3 p-6 pt-4">
                            <button
                                onClick={onCancel}
                                disabled={isLoading}
                                className="flex-1 px-4 py-2.5 bg-slate-100 dark:bg-dark-700 text-slate-700 dark:text-slate-300 font-medium rounded-xl hover:bg-slate-200 dark:hover:bg-dark-600 transition-colors disabled:opacity-50"
                            >
                                {data.cancelText || t('common.cancel')}
                            </button>
                            <button
                                onClick={onConfirm}
                                disabled={!canConfirm}
                                className={`flex-1 flex items-center justify-center gap-2 px-4 py-2.5 font-medium rounded-xl transition-colors disabled:opacity-50 disabled:cursor-not-allowed ${type === 'danger'
                                    ? 'bg-error text-white hover:bg-error/90'
                                    : type === 'warning'
                                        ? 'bg-warning text-white hover:bg-warning/90'
                                        : 'bg-primary-600 text-white hover:bg-primary-700'
                                    }`}
                            >
                                {isLoading ? (
                                    <Loader2 className="w-4 h-4 animate-spin" />
                                ) : countdown > 0 ? (
                                    `${data.confirmText || t('common.confirm')} (${countdown}s)`
                                ) : (
                                    data.confirmText || t('common.confirm')
                                )}
                            </button>
                        </div>
                    </motion.div>
                </div>
            )}
        </AnimatePresence>,
        document.body
    )
}

// Hook: convenient confirmation dialog hook
export function useConfirmDialog() {
    const [open, setOpen] = useState(false)
    const [data, setData] = useState<ConfirmDialogData>({ title: '', message: '' })
    const [isLoading, setIsLoading] = useState(false)
    const resolveRef = useRef<((value: boolean) => void) | null>(null)

    const confirm = useCallback((dialogData: ConfirmDialogData): Promise<boolean> => {
        return new Promise((resolve) => {
            resolveRef.current = resolve
            setData(dialogData)
            setIsLoading(false)
            setOpen(true)
        })
    }, [])

    const handleConfirm = useCallback(() => {
        setOpen(false)
        resolveRef.current?.(true)
        resolveRef.current = null
    }, [])

    const handleCancel = useCallback(() => {
        setOpen(false)
        resolveRef.current?.(false)
        resolveRef.current = null
    }, [])

    const setLoading = useCallback((loading: boolean) => {
        setIsLoading(loading)
    }, [])

    return {
        confirm,
        setLoading,
        dialogProps: {
            open,
            data,
            isLoading,
            onConfirm: handleConfirm,
            onCancel: handleCancel
        }
    }
}
