import { isStaticDemoMode, staticApi, clearDemoModifications } from '../api/staticClient';

// 
export { isStaticDemoMode };

//  API
export { staticApi, clearDemoModifications };

// 
export interface StaticDemoInfo {
  mode: 'static';
  version: string;
  features: string[];
  limitations: string[];
}

// 
export const getStaticDemoInfo = (): StaticDemoInfo => ({
  mode: 'static',
  version: '1.0.0',
  features: [
    '',
    '',
    '',
    '',
    'MDM',
    '',
    '',
    '',
  ],
  limitations: [
    'localStorage',
    '',
    '',
    'WebSocket',
    '',
  ],
});

// 
export const initStaticDemo = (): void => {
  // 
  localStorage.setItem('static_demo_mode', 'true');
  localStorage.setItem('static_demo_initialized', Date.now().toString());

  // 
  const demoUser = {
    userId: 'USER-DEMO-001',
    email: 'demo@example.com',
    username: 'demo_user',
    displayName: '',
    role: 'demo',
    status: 'active',
    projectId: null,
    permissions: {
      cards: true,
      transactions: true,
      terminals: true,
      reports: true,
      settings: true,
      users: false, // 
      admin: false,
    },
  };

  localStorage.setItem('user', JSON.stringify(demoUser));
  localStorage.setItem('auth_token', 'demo-token-static-2026');

  console.log('[StaticDemo] ');
};

// 
export const isStaticDemoInitialized = (): boolean => {
  return localStorage.getItem('static_demo_mode') === 'true';
};

// 
export const exitStaticDemo = (): void => {
  clearDemoModifications();
  localStorage.removeItem('static_demo_mode');
  localStorage.removeItem('static_demo_initialized');
  localStorage.removeItem('user');
  localStorage.removeItem('auth_token');

  console.log('[StaticDemo] ');
};

// 
export const getStaticDemoSession = () => ({
  token: 'demo-token-static-2026',
  expiresAt: new Date(Date.now() + 24 * 60 * 60 * 1000).toISOString(),
  mode: 'STATIC_DEMO',
  message: ' - ',
});

// 
export default {
  isStaticDemoMode,
  isStaticDemoInitialized,
  initStaticDemo,
  exitStaticDemo,
  getStaticDemoInfo,
  getStaticDemoSession,
  api: staticApi,
  clearModifications: clearDemoModifications,
};
