import React, { createContext, useContext, useState, useCallback, useEffect } from 'react';

export interface PageHelpContent {
  pageId: string;
  title: string;
  description: string;
  features: FeatureHelp[];
  shortcuts: KeyboardShortcut[];
  relatedPages: string[];
}

export interface FeatureHelp {
  id: string;
  title: string;
  description: string;
  icon: string;
  elementSelector: string;
  demoVideo?: string;
  tips: string[];
  important?: boolean;
  interactive?: boolean;
}

export interface KeyboardShortcut {
  key: string;
  action: string;
}

export interface UserAction {
  type: string;
  page: string;
  timestamp: number;
  metadata?: Record<string, any>;
}

export interface RecommendedAction {
  id: string;
  label: string;
  icon: React.ElementType;
  action: () => void;
  priority: number;
}

export interface HelpContextType {
  activeMode: 'none' | 'tooltip' | 'spotlight' | 'tour';
  pageHelp: PageHelpContent | null;
  viewedHelps: Set<string>;
  actionHistory: UserAction[];
  currentLanguage: string;
  showHelp: (helpId: string) => void;
  hideHelp: () => void;
  markHelpAsViewed: (helpId: string) => void;
  getRecommendedActions: () => RecommendedAction[];
  trackAction: (action: UserAction) => void;
  setPageHelp: (help: PageHelpContent | null) => void;
  setActiveMode: (mode: HelpContextType['activeMode']) => void;
  isHelpViewed: (helpId: string) => boolean;
  clearHistory: () => void;
}

const HelpContext = createContext<HelpContextType | undefined>(undefined);

const STORAGE_KEYS = {
  VIEWED_HELPS: 'help_viewed_items',
  ACTION_HISTORY: 'help_action_history',
  LANGUAGE: 'help_preferred_language',
};

export function HelpProvider({ children }: { children: React.ReactNode }) {
  const [activeMode, setActiveModeState] = useState<HelpContextType['activeMode']>('none');
  const [pageHelp, setPageHelpState] = useState<PageHelpContent | null>(null);
  const [viewedHelps, setViewedHelps] = useState<Set<string>>(new Set());
  const [actionHistory, setActionHistory] = useState<UserAction[]>([]);
  const [currentLanguage, setCurrentLanguage] = useState<string>('en');
  const [isInitialized, setIsInitialized] = useState(false);

  useEffect(() => {
    try {
      const savedViewed = localStorage.getItem(STORAGE_KEYS.VIEWED_HELPS);
      if (savedViewed) {
        setViewedHelps(new Set(JSON.parse(savedViewed)));
      }

      const savedHistory = localStorage.getItem(STORAGE_KEYS.ACTION_HISTORY);
      if (savedHistory) {
        const parsed = JSON.parse(savedHistory);
        setActionHistory(parsed.slice(-100));
      }

      const savedLang = localStorage.getItem(STORAGE_KEYS.LANGUAGE);
      if (savedLang) {
        setCurrentLanguage(savedLang);
      }
    } catch (error) {
      console.warn('Failed to load help state from storage:', error);
    }
    setIsInitialized(true);
  }, []);

  useEffect(() => {
    if (!isInitialized) return;
    try {
      localStorage.setItem(STORAGE_KEYS.VIEWED_HELPS, JSON.stringify([...viewedHelps]));
    } catch (error) {
      console.warn('Failed to save viewed helps:', error);
    }
  }, [viewedHelps, isInitialized]);

  useEffect(() => {
    if (!isInitialized) return;
    try {
      localStorage.setItem(STORAGE_KEYS.ACTION_HISTORY, JSON.stringify(actionHistory));
    } catch (error) {
      console.warn('Failed to save action history:', error);
    }
  }, [actionHistory, isInitialized]);

  const showHelp = useCallback((helpId: string) => {
    setActiveModeState('spotlight');
  }, []);

  const hideHelp = useCallback(() => {
    setActiveModeState('none');
  }, []);

  const markHelpAsViewed = useCallback((helpId: string) => {
    setViewedHelps(prev => {
      const newSet = new Set(prev);
      newSet.add(helpId);
      return newSet;
    });
    localStorage.setItem(`help_viewed_${helpId}`, 'true');
  }, []);

  const isHelpViewed = useCallback((helpId: string) => {
    return viewedHelps.has(helpId) || localStorage.getItem(`help_viewed_${helpId}`) === 'true';
  }, [viewedHelps]);

  const trackAction = useCallback((action: UserAction) => {
    setActionHistory(prev => {
      const newHistory = [...prev, action];
      return newHistory.slice(-100);
    });
  }, []);

  const generateRecommendations = (lastAction: UserAction | undefined, history: UserAction[]): RecommendedAction[] => {
    if (!lastAction) return [];

    const recommendations: RecommendedAction[] = [];
    
    switch (lastAction.type) {
      case 'view_card':
        recommendations.push(
          { id: 'edit_card', label: 'Edit Card Details', icon: () => null, action: () => {}, priority: 1 },
          { id: 'view_transactions', label: 'View Card Transactions', icon: () => null, action: () => {}, priority: 2 }
        );
        break;
      case 'issue_card':
        recommendations.push(
          { id: 'view_cards', label: 'View All Cards', icon: () => null, action: () => {}, priority: 1 },
          { id: 'export_cards', label: 'Export Card List', icon: () => null, action: () => {}, priority: 3 }
        );
        break;
      case 'filter_applied':
        recommendations.push(
          { id: 'save_filter', label: 'Save This Filter', icon: () => null, action: () => {}, priority: 1 },
          { id: 'clear_filter', label: 'Clear Filters', icon: () => null, action: () => {}, priority: 2 }
        );
        break;
      default:
        if (lastAction.page === '/cards') {
          recommendations.push(
            { id: 'issue_card', label: 'Issue New Card', icon: () => null, action: () => {}, priority: 1 },
            { id: 'bulk_operations', label: 'Bulk Operations', icon: () => null, action: () => {}, priority: 2 }
          );
        }
    }

    return recommendations.sort((a, b) => a.priority - b.priority).slice(0, 3);
  };

  const getRecommendedActions = useCallback(() => {
    const lastAction = actionHistory[actionHistory.length - 1];
    return generateRecommendations(lastAction, actionHistory);
  }, [actionHistory]);

  const setPageHelp = useCallback((help: PageHelpContent | null) => {
    setPageHelpState(help);
  }, []);

  const setActiveMode = useCallback((mode: HelpContextType['activeMode']) => {
    setActiveModeState(mode);
  }, []);

  const clearHistory = useCallback(() => {
    setActionHistory([]);
    setViewedHelps(new Set());
    localStorage.removeItem(STORAGE_KEYS.VIEWED_HELPS);
    localStorage.removeItem(STORAGE_KEYS.ACTION_HISTORY);
  }, []);

  const value: HelpContextType = {
    activeMode,
    pageHelp,
    viewedHelps,
    actionHistory,
    currentLanguage,
    showHelp,
    hideHelp,
    markHelpAsViewed,
    getRecommendedActions,
    trackAction,
    setPageHelp,
    setActiveMode,
    isHelpViewed,
    clearHistory,
  };

  return (
    <HelpContext.Provider value={value}>
      {children}
    </HelpContext.Provider>
  );
}

export function useHelp() {
  const context = useContext(HelpContext);
  if (!context) {
    throw new Error('useHelp must be used within HelpProvider');
  }
  return context;
}

export default HelpContext;
