import { useEffect, useState } from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { useTranslation } from 'react-i18next';
import { Clock, AlertTriangle } from 'lucide-react';
import apiClient from '../api/client';
import { isStaticDemoInitialized, exitStaticDemo } from '../demo/staticDemo';

interface SessionInfo {
  valid: boolean;
  userId: string;
  role: string;
  expiresAt: string;
  type: 'auth' | 'demo';
}

interface AuthRouteGuardProps {
  children: React.ReactNode;
}

export default function AuthRouteGuard({ children }: AuthRouteGuardProps) {
  const { t } = useTranslation();
  const location = useLocation();
  const [isValidating, setIsValidating] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isDemoMode, setIsDemoMode] = useState(false);
  const [sessionInfo, setSessionInfo] = useState<SessionInfo | null>(null);
  const [timeRemaining, setTimeRemaining] = useState(0);
  const [showWarning, setShowWarning] = useState(false);
  const [user, setUser] = useState<any>(null);

  useEffect(() => {
    const validateSession = async () => {
      // 
      if (isStaticDemoInitialized()) {
        setIsAuthenticated(true);
        setIsDemoMode(true);
        setIsValidating(false);

        //  localStorage 
        const storedUser = localStorage.getItem('user');
        if (storedUser) {
          try {
            setUser(JSON.parse(storedUser));
          } catch {
            setUser(null);
          }
        }
        return;
      }

      const localAuthToken = localStorage.getItem('auth_token');
      const sessionAuthToken = sessionStorage.getItem('auth_token');
      const authToken = localAuthToken || sessionAuthToken;
      const demoToken = localStorage.getItem('demo_token');
      const storedUser = localStorage.getItem('user') || sessionStorage.getItem('user');

      if (storedUser) {
        try {
          setUser(JSON.parse(storedUser));
        } catch {
          setUser(null);
        }
      }

      if (!authToken && !demoToken) {
        setIsValidating(false);
        return;
      }

      const token = authToken || demoToken;
      const isDemo = !!demoToken;

      try {
        if (isDemo) {
          const response = await apiClient.post('/demo/validate', {});
          if (response.data.valid) {
            setIsAuthenticated(true);
            setIsDemoMode(true);
            const expiresTime = new Date(response.data.expiresAt).getTime();
            setTimeRemaining(Math.floor((expiresTime - Date.now()) / 1000));
          }
        } else {
          const response = await apiClient.get('/auth/me');
          if (response.data) {
            setIsAuthenticated(true);
            setIsDemoMode(false);
            setUser(response.data);
            localStorage.setItem('user', JSON.stringify(response.data));
          }
        }
      } catch {
        localStorage.removeItem('auth_token');
        localStorage.removeItem('demo_token');
        localStorage.removeItem('user');
      } finally {
        setIsValidating(false);
      }
    };

    validateSession();
  }, []);

  useEffect(() => {
    if (!isAuthenticated || !isDemoMode || timeRemaining <= 0) return;

    const timer = setInterval(() => {
      setTimeRemaining((prev) => {
        const newTime = prev - 1;

        if (newTime <= 300 && newTime > 0) {
          setShowWarning(true);
        }

        if (newTime <= 0) {
          handleLogout();
          return 0;
        }

        return newTime;
      });
    }, 1000);

    return () => clearInterval(timer);
  }, [isAuthenticated, isDemoMode, timeRemaining]);

  const handleLogout = async () => {
    // 
    if (isStaticDemoInitialized()) {
      exitStaticDemo();
      setIsAuthenticated(false);
      setIsDemoMode(false);
      window.location.href = '/static-demo';
      return;
    }

    const authToken = localStorage.getItem('auth_token') || sessionStorage.getItem('auth_token');
    const demoToken = localStorage.getItem('demo_token');

    try {
      if (authToken) {
        await apiClient.post('/auth/logout');
      } else if (demoToken) {
        await apiClient.post('/demo/logout');
      }
    } catch {
      // Ignore logout errors
    }

    localStorage.removeItem('auth_token');
    localStorage.removeItem('refresh_token');
    localStorage.removeItem('demo_token');
    localStorage.removeItem('demo_expires_at');
    localStorage.removeItem('demo_mode');
    localStorage.removeItem('user');
    localStorage.removeItem('user_role');

    sessionStorage.removeItem('auth_token');
    sessionStorage.removeItem('refresh_token');
    sessionStorage.removeItem('user');
    sessionStorage.removeItem('user_role');

    setIsAuthenticated(false);
    setIsDemoMode(false);
    window.location.href = '/login';
  };

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  if (isValidating) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex items-center justify-center">
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          className="flex flex-col items-center gap-4"
        >
          <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin" />
          <p className="text-gray-600 dark:text-gray-400">
            {t('auth.validating', 'Validating session...')}
          </p>
        </motion.div>
      </div>
    );
  }

  if (!isAuthenticated) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  return (
    <>
      <AnimatePresence>
        {showWarning && isDemoMode && (
          <motion.div
            initial={{ opacity: 0, y: -50 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -50 }}
            className="fixed top-4 left-1/2 transform -translate-x-1/2 z-50"
          >
            <div className="bg-amber-500 text-white px-6 py-3 rounded-lg shadow-lg flex items-center gap-4">
              <AlertTriangle className="w-5 h-5" />
              <div>
                <p className="font-medium">
                  {t('demo.expiringSoon', 'Session expiring soon')}
                </p>
                <p className="text-sm opacity-90">
                  {t('demo.timeRemaining', 'Time remaining: {{time}}', {
                    time: formatTime(timeRemaining),
                  })}
                </p>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      {isDemoMode && (
        <>
          <div className="fixed top-4 right-4 z-40 flex items-center gap-2">
            <div className="bg-blue-600 text-white px-3 py-1.5 rounded-full text-xs font-medium flex items-center gap-2">
              <Clock className="w-3 h-3" />
              <span>{formatTime(timeRemaining)}</span>
            </div>
          </div>

          <div className="fixed bottom-4 right-4 z-40">
            <div className="bg-blue-600 text-white px-4 py-2 rounded-lg shadow-lg text-sm font-medium">
              {t('demo.modeIndicator', 'Demo Mode')}
            </div>
          </div>
        </>
      )}

      {children}
    </>
  );
}
