import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { terminalsApi } from '../api/terminals'
import apiClient from '../api/client'
import type {
  Terminal,
  TerminalFilters,
  RegisterTerminalRequest,
  UpdateTerminalRequest,
  TerminalStats
} from '../api/terminals'

import { isDemoMode } from '../demo/demoMode'

const TERMINALS_QUERY_KEY = 'terminals'
const TERMINAL_STATS_QUERY_KEY = 'terminalStats'

export const useTerminals = (filters?: TerminalFilters) => {
  return useQuery({
    queryKey: [TERMINALS_QUERY_KEY, filters],
    queryFn: async () => {
      if (isDemoMode()) {
        const now = Date.now()
        const routes = ['1', '2', '10', '15', '22', '1', '1']
        const mockTerminals = Array.from({ length: 45 }).map((_, i) => ({
          id: `TERM-${1000 + i}`,
          type: ['ONBOARD', 'TOPUP', 'VALIDATOR'][Math.floor(Math.random() * 3)],
          status: ['ONLINE', 'OFFLINE', 'MAINTENANCE'][Math.floor(Math.random() * 3)],
          location: `Station ${String.fromCharCode(65 + Math.floor(Math.random() * 10))}`,
          route: routes[Math.floor(Math.random() * routes.length)],
          lastSync: new Date(now - Math.floor(Math.random() * 300000)).toISOString(),
          battery: Math.floor(Math.random() * 100),
          storage: Math.floor(Math.random() * 100)
        }))

        let filtered = mockTerminals
        if (filters?.type) filtered = filtered.filter(t => t.type === filters.type)
        if (filters?.status) filtered = filtered.filter(t => t.status === filters.status)
        if (filters?.search) {
          const search = filters.search.toLowerCase()
          filtered = filtered.filter(t =>
            t.id.toLowerCase().includes(search) ||
            t.location.toLowerCase().includes(search)
          )
        }

        return {
          data: filtered.slice(0, 20),
          total: filtered.length,
          page: 1,
          pageSize: 20
        }
      }
      return terminalsApi.getTerminals(filters)
    },
    refetchInterval: isDemoMode() ? 5000 : false,
  })
}

export const useTerminal = (id: string) => {
  return useQuery({
    queryKey: [TERMINALS_QUERY_KEY, id],
    queryFn: async () => {
      if (isDemoMode()) {
        const index = parseInt(id.split('-')[1]) || 0
        return {
          id: id,
          type: (['ONBOARD', 'TOPUP', 'VALIDATOR'] as const)[index % 3],
          status: (['ONLINE', 'OFFLINE', 'MAINTENANCE'] as const)[index % 3],
          location: `Station ${String.fromCharCode(65 + (index % 5))}`,
          route: index % 3 === 0 ? `Route ${101 + index}` : 'Route 202',
          lastSync: new Date().toLocaleString(),
          battery: 85,
          storage: 42,
          //  (P0 )
          serialNumber: `SN-${20240000 + index}`,
          manufacturer: 'Decard Tech',
          model: 'DPT-500',
          firmwareVersion: 'v2.1.0',
          purchaseDate: '2024-01-15',
          warrantyExpiry: '2026-01-15',
          stationId: `STATION-00${index % 5}`,
          blacklistVersion: '20250208A',
          transactionCount: 1240 + index
        }
      }
      return terminalsApi.getTerminal(id)
    },
    enabled: !!id,
  })
}

export const useRegisterTerminal = () => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: (data: RegisterTerminalRequest) => terminalsApi.registerTerminal(data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [TERMINALS_QUERY_KEY] })
      queryClient.invalidateQueries({ queryKey: [TERMINAL_STATS_QUERY_KEY] })
    },
  })
}

export const useUpdateTerminal = () => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: ({ id, data }: { id: string; data: UpdateTerminalRequest }) =>
      terminalsApi.updateTerminal(id, data),
    onSuccess: (_, variables) => {
      queryClient.invalidateQueries({ queryKey: [TERMINALS_QUERY_KEY, variables.id] })
      queryClient.invalidateQueries({ queryKey: [TERMINALS_QUERY_KEY] })
      queryClient.invalidateQueries({ queryKey: [TERMINAL_STATS_QUERY_KEY] })
    },
  })
}

export const useDeleteTerminal = () => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: (id: string) => terminalsApi.deleteTerminal(id),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [TERMINALS_QUERY_KEY] })
      queryClient.invalidateQueries({ queryKey: [TERMINAL_STATS_QUERY_KEY] })
    },
  })
}

export const useTerminalStats = () => {
  return useQuery({
    queryKey: [TERMINAL_STATS_QUERY_KEY],
    queryFn: async () => {
      if (isDemoMode()) {
        return {
          total: 15,
          online: 5,
          offline: 5,
          maintenance: 5,
          pendingSync: 2
        }
      }
      return terminalsApi.getTerminalStats()
    },
  })
}

export const useSyncTerminal = () => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: (id: string) => terminalsApi.syncTerminal(id),
    onSuccess: (_, id) => {
      queryClient.invalidateQueries({ queryKey: [TERMINALS_QUERY_KEY, id] })
      queryClient.invalidateQueries({ queryKey: [TERMINALS_QUERY_KEY] })
      queryClient.invalidateQueries({ queryKey: [TERMINAL_STATS_QUERY_KEY] })
    },
  })
}

export const useFirmwareVersions = () => {
  return useQuery({
    queryKey: ['firmware-versions'],
    queryFn: async () => {
      if (isDemoMode()) {
        return [
          { version: 'v2.1.0', releaseDate: '2024-01-01', type: 'STABLE', size: '42MB' },
          { version: 'v2.2.0-beta', releaseDate: '2024-02-01', type: 'BETA', size: '45MB' },
        ]
      }
      const response = await apiClient.get('/terminals/firmware/versions')
      return response.data
    }
  })
}

export const useInventoryAlerts = () => {
  return useQuery({
    queryKey: ['inventory-alerts'],
    queryFn: async () => {
      if (isDemoMode()) {
        return [
          { type: 'ONBOARD', stock: 2, threshold: 5, status: 'CRITICAL' },
          { type: 'VALIDATOR', stock: 8, threshold: 10, status: 'WARNING' },
        ]
      }
      const response = await apiClient.get('/terminals/inventory/alerts')
      return response.data
    }
  })
}

export const useRemoteDiagnostics = () => {
  return useMutation({
    mutationFn: async ({ id, command }: { id: string, command: 'REBOOT' | 'LOG_TRANSFER' | 'WAKE_UP' }) => {
      if (isDemoMode()) {
        await new Promise(resolve => setTimeout(resolve, 1000))
        return { status: 'SUCCESS', message: `Command ${command} sent to ${id}` }
      }
      const response = await apiClient.post(`/terminals/${id}/diagnostics`, { command })
      return response.data
    }
  })
}
