import { motion } from 'framer-motion'
import { useNavigate } from 'react-router-dom'
import { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import {
  CreditCard,
  ArrowUpRight,
  ArrowDownRight,
  Activity,
  Users,
  Monitor,
  TrendingUp,
  AlertCircle,
  Loader2,
  RefreshCw,
} from 'lucide-react'
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  AreaChart,
  Area,
  PieChart,
  Pie,
  Cell,
  Legend,
} from 'recharts'
import { useDashboardMetrics, useRevenueChart } from '../hooks/useReports'
import { useTransactions } from '../hooks/useTransactions'
import { useDeviceAlerts } from '../hooks/useMdm'
import { useTerminals } from '../hooks/useTerminals'
import { useCards } from '../hooks/useCards'
import { ExchangeRates } from '../components/transit/ExchangeRates'
import { Leaf, RefreshCw as RefreshIcon } from 'lucide-react'
import { useExchangeRates } from '../hooks/useTransit'
import { useHelp } from '../contexts/HelpContext'
import { SmartTooltip, SpotlightManager } from '../components/help'

function Dashboard() {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const [timeRange, setTimeRange] = useState<'24H' | '7D' | '30D'>('24H')
  const [chartMetric, setChartMetric] = useState<'transactions' | 'revenue'>('transactions')
  const [showTour, setShowTour] = useState(false)
  const { trackAction } = useHelp()

  useEffect(() => {
    trackAction({
      type: 'view_page',
      page: '/dashboard',
      timestamp: Date.now()
    })
  }, [trackAction])

  const { data: metricsData, isLoading: metricsLoading, refetch: refetchMetrics } = useDashboardMetrics()
  const { data: revenueChartData, isLoading: chartLoading } = useRevenueChart(
    timeRange === '24H' ? '7d' : timeRange === '7D' ? '7d' : '30d'
  )
  const { data: recentTransactionsData, isLoading: transactionsLoading } = useTransactions({ limit: 5 })
  const { data: alertsData, isLoading: alertsLoading } = useDeviceAlerts({ status: 'ACTIVE' }, { retry: false, enabled: false })
  const { data: terminalsData } = useTerminals()
  const { data: cardsData } = useCards()

const defaultMetrics = {
  totalTransactions: 0,
  successfulTransactions: 0,
  pendingTransactions: 0,
  totalRevenue: 0,
  activeCards: 0,
  activeTerminals: 0,
  averageTransactionAmount: 0,
  successRate: 0,
  todayTransactions: 0,
  todayRevenue: 0,
  yesterdayTransactions: 0,
  yesterdayRevenue: 0,
  transactionGrowth: 0,
  revenueGrowth: 0,
}

  const metrics = {
    ...defaultMetrics,
    ...(metricsData || {}),
  }

  // Ensure all values are valid numbers
  const safeNumber = (val: any): number => {
    const num = Number(val)
    return isNaN(num) ? 0 : num
  }

  const recentTransactions = recentTransactionsData?.data || []
  const alerts = alertsData?.data || []
  const terminals = terminalsData?.data || []
  const cards = cardsData?.data || []

  const activeTerminalsCount = terminals.filter((t: any) => t.status === 'ONLINE').length
  const totalCardsCount = cards.length


  const revenueGrowth = metrics.yesterdayRevenue > 0
    ? ((metrics.todayRevenue - metrics.yesterdayRevenue) / metrics.yesterdayRevenue) * 100
    : 0
  const transactionGrowth = metrics.yesterdayTransactions > 0
    ? ((metrics.todayTransactions - metrics.yesterdayTransactions) / metrics.yesterdayTransactions) * 100
    : 0

  const chartData = revenueChartData?.labels?.map((label: string, i: number) => ({
    time: label,
    transactions: revenueChartData.datasets[0]?.data[i] || 0,
    revenue: revenueChartData.datasets[1]?.data[i] || 0,
    previousRevenue: (revenueChartData.datasets[1]?.data[i] || 0) * 0.85, // Mock comparison data
  })) || []

  // Mock data for new components
  const channelData = [
    { name: t('paymentMethods.ALIPAY'), value: 4500, color: '#00A1E9' },
    { name: t('paymentMethods.WECHAT'), value: 3200, color: '#07C160' },
    { name: t('paymentMethods.BANK_CARD'), value: 2100, color: '#FFB800' },
    { name: t('paymentMethods.UNIONPAY'), value: 1200, color: '#C22026' },
  ]

  const alertLogStream = [
    { id: 1, time: '10:25', type: 'TERMINAL', msg: t('alerts.powerAbnormal', { deviceId: 'TERM-1043' }), severity: 'HIGH' },
    { id: 2, time: '10:21', type: 'TRANSIT', msg: t('alerts.transactionSurge', { route: '502' }), severity: 'INFO' },
    { id: 3, time: '10:15', type: 'SECURITY', msg: t('alerts.unauthorizedAccess'), severity: 'CRITICAL' },
    { id: 4, time: '10:10', type: 'MDM', msg: t('alerts.blacklistDeployed', { version: 'v2.4' }), severity: 'SUCCESS' },
    { id: 5, time: '09:55', type: 'TERMINAL', msg: t('alerts.networkLatency', { deviceId: 'TERM-2088' }), severity: 'WARN' },
  ]

  const failureReasonData = [
    { name: t('transactions.errors.insufficientBalance'), value: 45, color: '#f59e0b' },
    { name: t('transactions.errors.timeout'), value: 25, color: '#ef4444' },
    { name: t('transactions.errors.invalidCard'), value: 15, color: '#6366f1' },
    { name: t('transactions.errors.unknown'), value: 15, color: '#94a3b8' },
  ]

  const highLatencyCount = 3 // Mock count for unprocessed orders > 10m

  const isLoading = metricsLoading || chartLoading || transactionsLoading || alertsLoading

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="w-8 h-8 animate-spin text-primary-600" />
      </div>
    )
  }

  const stats = [
    {
      title: t('dashboard.totalCards'),
      value: safeNumber(totalCardsCount).toLocaleString(),
      change: '+12.5%',
      trend: 'up' as const,
      icon: CreditCard,
      color: 'primary',
    },
    {
      title: t('dashboard.dailyTransactions'),
      value: safeNumber(metrics.todayTransactions).toLocaleString(),
      change: `${transactionGrowth > 0 ? '+' : ''}${safeNumber(transactionGrowth).toFixed(1)}%`,
      trend: transactionGrowth >= 0 ? ('up' as const) : ('down' as const),
      icon: Activity,
      color: 'success',
    },
    {
      title: t('dashboard.activeTerminals'),
      value: safeNumber(activeTerminalsCount).toString(),
      change: `${((safeNumber(activeTerminalsCount) / (terminals.length || 1)) * 100).toFixed(0)}%`,
      trend: 'up' as const,
      icon: Monitor,
      color: 'warning',
    },
    {
      title: t('dashboard.revenueToday'),
      value: `${safeNumber(metrics.todayRevenue).toLocaleString()}`,
      change: `${revenueGrowth > 0 ? '+' : ''}${safeNumber(revenueGrowth).toFixed(1)}%`,
      trend: revenueGrowth >= 0 ? ('up' as const) : ('down' as const),
      icon: TrendingUp,
      color: 'accent',
      sparklineData: [
        { value: 400 }, { value: 300 }, { value: 600 }, { value: 800 }, { value: 500 }, { value: 900 }, { value: 700 }
      ]
    },
  ]


  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="space-y-6"
    >
      {showTour && (
        <SpotlightManager
          steps={[
            {
              id: 'welcome',
              target: '',
              title: t('dashboard.tour.welcome.title', 'Welcome to Dashboard'),
              description: t('dashboard.tour.welcome.desc', 'Your command center for monitoring the entire transit card system.'),
              position: 'center',
            },
            {
              id: 'stats-cards',
              target: '[data-help="stats-cards"]',
              title: t('dashboard.tour.stats.title', 'Key Metrics'),
              description: t('dashboard.tour.stats.desc', 'Real-time overview of cards, transactions, and system health.'),
              position: 'bottom',
              tips: [
                t('dashboard.tour.stats.tip1', 'Click any card for detailed view'),
                t('dashboard.tour.stats.tip2', 'Data updates every 30 seconds'),
              ],
            },
            {
              id: 'chart-section',
              target: '[data-help="chart-section"]',
              title: t('dashboard.tour.chart.title', 'Transaction Trends'),
              description: t('dashboard.tour.chart.desc', 'Visualize transaction patterns and identify trends.'),
              position: 'top',
              tips: [
                t('dashboard.tour.chart.tip1', 'Switch between 24H, 7D, and 30D views'),
                t('dashboard.tour.chart.tip2', 'Hover over data points for details'),
              ],
            },
          ]}
          tourId="dashboard-intro"
          onComplete={() => setShowTour(false)}
          onSkip={() => setShowTour(false)}
        />
      )}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-display font-bold text-slate-900 dark:text-slate-100">{t('common.dashboard')}</h1>
          <p className="text-slate-500 dark:text-slate-400 mt-1">{t('dashboard.subtitle')}</p>
        </div>
        {highLatencyCount > 0 && (
          <motion.div
            animate={{ opacity: [1, 0.5, 1], scale: [1, 1.02, 1] }}
            transition={{ duration: 1.5, repeat: Infinity, ease: "easeInOut" }}
            className="flex items-center gap-3 px-4 py-2 bg-error/10 border border-error/20 rounded-xl text-error"
          >
            <AlertCircle className="w-5 h-5 animate-pulse" />
            <span className="text-sm font-bold">
              {t('dashboard.alerts.highLatency', { count: highLatencyCount }) || `${highLatencyCount} 10, !`}
            </span>
          </motion.div>
        )}
        <div className="flex gap-3">
          <button onClick={() => refetchMetrics()} className="btn-secondary flex items-center gap-2">
            <RefreshCw className="w-4 h-4" />
            {t('common.refresh')}
          </button>
          <button
            onClick={() => navigate('/terminals')}
            className="btn-secondary flex items-center gap-2"
          >
            <Activity className="w-4 h-4" />
            {t('dashboard.liveMonitor')}
          </button>
          <button onClick={() => navigate('/cards')} className="btn-primary flex items-center gap-2">
            <CreditCard className="w-4 h-4" />
            {t('cards.issueCard')}
          </button>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6" data-help="stats-cards">
        {stats.map((stat, index) => (
          <motion.div
            key={stat.title}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.1 }}
            className="stat-card overflow-hidden group"
          >
            <div className="flex items-start justify-between relative z-10">
              <div>
                <p className="text-sm text-slate-500 dark:text-slate-400 font-medium">{stat.title}</p>
                <p className="text-2xl font-display font-bold text-slate-900 dark:text-slate-100 mt-2">{stat.value}</p>
                <div className="flex items-center gap-1 mt-2">
                  {stat.trend === 'up' ? (
                    <ArrowUpRight className="w-4 h-4 text-emerald-500" />
                  ) : (
                    <ArrowDownRight className="w-4 h-4 text-rose-500" />
                  )}
                  <span className={`text-sm font-semibold ${stat.trend === 'up' ? 'text-emerald-500' : 'text-rose-500'}`}>{stat.change}</span>
                  <span className="text-xs text-slate-400 ml-1">{t('common.vsYesterday')}</span>
                </div>
              </div>
              <div className={`p-2.5 rounded-xl bg-${stat.color}-500/10 transition-transform group-hover:scale-110 duration-300`}>
                <stat.icon className={`w-5 h-5 text-${stat.color}-600 dark:text-${stat.color}-400`} />
              </div>
            </div>

            {/* Sparkline integration */}
            <div className="absolute bottom-0 left-0 right-0 h-12 opacity-40 group-hover:opacity-60 transition-opacity">
              <ResponsiveContainer width="100%" height="100%">
                <AreaChart data={stat.sparklineData || chartData.slice(-7)}>
                  <Area
                    type="monotone"
                    dataKey="value"
                    stroke={stat.color === 'primary' ? '#3b82f6' : stat.color === 'success' ? '#10b981' : stat.color === 'warning' ? '#f59e0b' : '#8b5cf6'}
                    fill={stat.color === 'primary' ? '#3b82f6' : stat.color === 'success' ? '#10b981' : stat.color === 'warning' ? '#f59e0b' : '#8b5cf6'}
                    fillOpacity={0.1}
                    strokeWidth={2}
                  />
                </AreaChart>
              </ResponsiveContainer>
            </div>
          </motion.div>
        ))}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4 }}
          className="lg:col-span-2 glass-panel p-6"
          data-help="chart-section"
        >
          <div className="flex items-center justify-between mb-6">
            <div className="flex items-center gap-4">
              <h2 className="text-lg font-display font-semibold text-slate-900 dark:text-slate-100">{t('dashboard.revenueComparison')}</h2>
              <div className="flex p-1 bg-slate-100 dark:bg-dark-900 rounded-lg border border-slate-200 dark:border-dark-600">
                <button
                  onClick={() => setChartMetric('transactions')}
                  className={`px-3 py-1 text-xs font-medium rounded-md transition-all ${chartMetric === 'transactions'
                    ? 'bg-white dark:bg-dark-700 text-primary-600 shadow-sm'
                    : 'text-slate-500 hover:text-slate-700'
                    }`}
                >
                  {t('dashboard.metrics.count')}
                </button>
                <button
                  onClick={() => setChartMetric('revenue')}
                  className={`px-3 py-1 text-xs font-medium rounded-md transition-all ${chartMetric === 'revenue'
                    ? 'bg-white dark:bg-dark-700 text-primary-600 shadow-sm'
                    : 'text-slate-500 hover:text-slate-700'
                    }`}
                >
                  {t('dashboard.metrics.amount')}
                </button>
              </div>
              <div className="flex items-center gap-4 ms-4">
                <div className="flex items-center gap-1.5">
                  <div className="w-2.5 h-2.5 rounded-full bg-primary-500" />
                  <span className="text-xs text-slate-500">{t('common.today')}</span>
                </div>
                <div className="flex items-center gap-1.5">
                  <div className="w-2.5 h-2.5 rounded-full bg-slate-300 dark:bg-dark-600" />
                  <span className="text-xs text-slate-500">{t('common.yesterday')}</span>
                </div>
              </div>
            </div>
            <div className="flex gap-2">
              <button
                onClick={() => setTimeRange('24H')}
                className={`px-3 py-1 text-sm rounded-lg border transition-colors ${timeRange === '24H'
                  ? 'bg-primary-50 dark:bg-primary-500/20 text-primary-600 dark:text-primary-400 border-primary-200 dark:border-primary-500/30'
                  : 'text-slate-500 dark:text-slate-400 border-transparent hover:text-slate-900 dark:hover:text-slate-200'
                  }`}
              >
                24H
              </button>
              <button
                onClick={() => setTimeRange('7D')}
                className={`px-3 py-1 text-sm rounded-lg border transition-colors ${timeRange === '7D'
                  ? 'bg-primary-50 dark:bg-primary-500/20 text-primary-600 dark:text-primary-400 border-primary-200 dark:border-primary-500/30'
                  : 'text-slate-500 dark:text-slate-400 border-transparent hover:text-slate-900 dark:hover:text-slate-200'
                  }`}
              >
                7D
              </button>
              <button
                onClick={() => setTimeRange('30D')}
                className={`px-3 py-1 text-sm rounded-lg border transition-colors ${timeRange === '30D'
                  ? 'bg-primary-50 dark:bg-primary-500/20 text-primary-600 dark:text-primary-400 border-primary-200 dark:border-primary-500/30'
                  : 'text-slate-500 dark:text-slate-400 border-transparent hover:text-slate-900 dark:hover:text-slate-200'
                  }`}
              >
                30D
              </button>
            </div>
          </div>
          <div className="h-80">
            <ResponsiveContainer width="100%" height="100%">
              <AreaChart data={chartData}>
                <defs>
                  <linearGradient id="colorMetric" x1="0" y1="0" x2="0" y2="1">
                    <stop offset="5%" stopColor={chartMetric === 'transactions' ? "#06b6d4" : "#8b5cf6"} stopOpacity={0.3} />
                    <stop offset="95%" stopColor={chartMetric === 'transactions' ? "#06b6d4" : "#8b5cf6"} stopOpacity={0} />
                  </linearGradient>
                </defs>
                <CartesianGrid strokeDasharray="3 3" className="stroke-slate-200 dark:stroke-dark-600" vertical={false} />
                <XAxis dataKey="time" stroke="#64748b" fontSize={12} tickLine={false} axisLine={false} />
                <YAxis stroke="#64748b" fontSize={12} tickLine={false} axisLine={false} />
                <Tooltip
                  formatter={(value: number, name: string) => [
                    chartMetric === 'revenue' ? `${value.toLocaleString()}` : value.toLocaleString(),
                    name === 'previousRevenue' ? t('common.yesterday') : (chartMetric === 'revenue' ? t('dashboard.metrics.amount') : t('dashboard.metrics.count'))
                  ]}
                  contentStyle={{
                    backgroundColor: 'rgba(255, 255, 255, 0.95)',
                    borderColor: '#e2e8f0',
                    borderRadius: '12px',
                    boxShadow: '0 10px 15px -3px rgba(0, 0, 0, 0.1)'
                  }}
                />
                <Area
                  type="monotone"
                  dataKey="previousRevenue"
                  stroke="#94a3b8"
                  strokeWidth={2}
                  strokeDasharray="5 5"
                  fillOpacity={0}
                  fill="transparent"
                />
                <Area
                  type="monotone"
                  dataKey={chartMetric}
                  stroke={chartMetric === 'transactions' ? "#06b6d4" : "#8b5cf6"}
                  strokeWidth={3}
                  fillOpacity={1}
                  fill="url(#colorMetric)"
                />
              </AreaChart>
            </ResponsiveContainer>
          </div>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.5 }}
          className="glass-panel p-6"
        >
          <div className="flex items-center gap-2 mb-6">
            <AlertCircle className="w-5 h-5 text-warning" />
            <h2 className="text-lg font-display font-semibold text-slate-900 dark:text-slate-100">{t('dashboard.systemAlerts')}</h2>
          </div>
          <div className="space-y-4">
            {alerts.length === 0 ? (
              <p className="text-slate-500 text-center py-4">{t('dashboard.noAlerts')}</p>
            ) : (
              alerts.slice(0, 5).map((alert: any) => (
                <div
                  key={alert.id}
                  className="flex items-start gap-3 p-3 rounded-lg bg-slate-50 dark:bg-dark-700/50 border border-slate-200 dark:border-dark-600"
                >
                  <div
                    className={`w-2 h-2 rounded-full mt-2 flex-shrink-0 ${alert.severity === 'CRITICAL' || alert.severity === 'HIGH'
                      ? 'bg-error'
                      : alert.severity === 'MEDIUM'
                        ? 'bg-warning'
                        : 'bg-info'
                      }`}
                  />
                  <div className="flex-1 min-w-0">
                    <p className="text-sm text-slate-700 dark:text-slate-300">{alert.title}</p>
                    <p className="text-xs text-slate-500 mt-1">{new Date(alert.createdAt).toLocaleString()}</p>
                  </div>
                </div>
              ))
            )}
          </div>
          <button
            onClick={() => navigate('/mdm')}
            className="w-full mt-4 py-2 text-sm text-primary-600 dark:text-primary-400 hover:text-primary-500 dark:hover:text-primary-300 transition-colors border border-dashed border-slate-300 dark:border-dark-600 rounded-lg hover:border-primary-500/30"
          >
            {t('dashboard.viewAllAlerts')}
          </button>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ delay: 0.6 }}
          className="glass-panel p-6 flex flex-col"
        >
          <h2 className="text-lg font-display font-semibold text-slate-900 dark:text-slate-100 mb-6">{t('transactions.stats.settlementRate')}</h2>
          <div className="flex-1 flex flex-col items-center justify-center">
            <div className="relative w-40 h-40">
              <ResponsiveContainer width="100%" height="100%">
                <PieChart>
                  <Pie
                    data={[
                      { name: 'Settled', value: safeNumber(metrics.successfulTransactions), color: '#10b981' },
                      { name: 'Pending', value: safeNumber(metrics.totalTransactions - metrics.successfulTransactions), color: '#f1f5f9' }
                    ]}
                    cx="50%"
                    cy="50%"
                    innerRadius={55}
                    outerRadius={75}
                    startAngle={90}
                    endAngle={-270}
                    paddingAngle={0}
                    dataKey="value"
                  >
                    <Cell key="cell-0" fill="#10b981" />
                    <Cell key="cell-1" fill="currentColor" className="text-slate-100 dark:text-dark-700" />
                  </Pie>
                </PieChart>
              </ResponsiveContainer>
              <div className="absolute inset-0 flex flex-col items-center justify-center">
                <span className="text-3xl font-display font-bold text-slate-900 dark:text-slate-100">
                  {metrics.totalTransactions > 0 ? Math.round((metrics.successfulTransactions / metrics.totalTransactions) * 100) : 100}%
                </span>
                <span className="text-[10px] text-slate-400 uppercase font-bold tracking-wider">Target 95%</span>
              </div>
            </div>

            <div className="grid grid-cols-2 gap-4 w-full mt-6">
              <div className="p-3 bg-slate-50 dark:bg-dark-700/50 rounded-xl border border-slate-100 dark:border-dark-600">
                <p className="text-xs text-slate-500 mb-1">{t('transactions.status.COMPLETED')}</p>
                <p className="text-lg font-bold text-success">{metrics.successfulTransactions}</p>
              </div>
              <div className="p-3 bg-slate-50 dark:bg-dark-700/50 rounded-xl border border-slate-100 dark:border-dark-600">
                <p className="text-xs text-slate-500 mb-1">{t('transactions.reconciliation.pending')}</p>
                <p className="text-lg font-bold text-warning">{metrics.pendingTransactions}</p>
              </div>
            </div>

            {/* Failure Reason Breakdown (Enhanced to PieChart) */}
            <div className="w-full mt-8 pt-6 border-t border-slate-100 dark:border-dark-600">
              <h3 className="text-sm font-semibold text-slate-700 dark:text-slate-300 mb-4 flex items-center gap-2">
                <AlertCircle className="w-4 h-4 text-error" />
                {t('dashboard.failureAnalysis') || ''}
              </h3>
              <div className="h-48 relative">
                <ResponsiveContainer width="100%" height="100%">
                  <PieChart>
                    <Pie
                      data={failureReasonData}
                      cx="50%"
                      cy="50%"
                      innerRadius={40}
                      outerRadius={60}
                      paddingAngle={5}
                      dataKey="value"
                    >
                      {failureReasonData.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={entry.color} />
                      ))}
                    </Pie>
                    <Tooltip
                      contentStyle={{ borderRadius: '12px', border: 'none', boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)' }}
                    />
                  </PieChart>
                </ResponsiveContainer>
              </div>
              <div className="mt-4 grid grid-cols-2 gap-x-4 gap-y-2">
                {failureReasonData.map((item) => (
                  <div key={item.name} className="flex items-center gap-2">
                    <div className="w-2.5 h-2.5 rounded-full" style={{ backgroundColor: item.color }} />
                    <span className="text-[11px] text-slate-500 truncate">{item.name}</span>
                    <span className="text-[11px] font-bold ml-auto text-slate-700 dark:text-slate-300">{item.value}%</span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </motion.div>

        {/* Alert Log Stream */}
        {/* Terminal Health Distribution (Enhanced) */}
        <motion.div
          initial={{ opacity: 0, x: 20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: 0.7 }}
          className="lg:col-span-2 glass-panel p-6"
        >
          <div className="flex items-center justify-between mb-6">
            <div className="flex items-center gap-2 text-primary-600">
              <Monitor className="w-5 h-5" />
              <h2 className="text-lg font-display font-semibold text-slate-900 dark:text-slate-100">{t('dashboard.terminalHeatmap') || ''}</h2>
            </div>
            <span className="text-xs font-medium px-2 py-1 bg-primary-50 dark:bg-primary-900/20 text-primary-600 rounded-lg">
              {t('dashboard.abnormalTerminals', { count: 3 }) || ' 3 '}
            </span>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {[
              { id: 'TERM-101', rate: '12%', status: 'error', location: '' },
              { id: 'TERM-204', rate: '0.5%', status: 'success', location: '' },
              { id: 'TERM-088', rate: '8%', status: 'warning', location: '' },
              { id: 'TERM-156', rate: '0.2%', status: 'success', location: '' },
            ].map((device) => (
              <div key={device.id} className="p-4 bg-slate-50 dark:bg-dark-700/30 rounded-xl border border-slate-100 dark:border-dark-600 hover:border-primary-500/30 transition-all cursor-pointer group">
                <div className="flex justify-between items-start mb-2">
                  <div className="flex items-center gap-2">
                    <div className={`w-2 h-2 rounded-full ${device.status === 'error' ? 'bg-error animate-ping' : device.status === 'warning' ? 'bg-warning' : 'bg-success'}`} />
                    <span className="font-mono text-sm font-bold text-slate-700 dark:text-slate-200">{device.id}</span>
                  </div>
                  <span className={`text-xs font-bold ${device.status === 'error' ? 'text-error' : device.status === 'warning' ? 'text-warning' : 'text-success'}`}>
                    : {device.rate}
                  </span>
                </div>
                <p className="text-xs text-slate-500">{device.location}</p>
                <div className="mt-3 w-full h-1.5 bg-slate-200 dark:bg-dark-800 rounded-full overflow-hidden">
                  <div
                    className={`h-full rounded-full ${device.status === 'error' ? 'bg-error' : device.status === 'warning' ? 'bg-warning' : 'bg-success'}`}
                    style={{ width: device.status === 'error' ? '85%' : device.status === 'warning' ? '40%' : '15%' }}
                  />
                </div>
              </div>
            ))}
          </div>
        </motion.div>
      </div>

      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.6 }}
        className="glass-panel"
      >
        <div className="flex items-center justify-between p-6 border-b border-slate-200 dark:border-dark-600">
          <h2 className="text-lg font-display font-semibold text-slate-900 dark:text-slate-100">{t('dashboard.recentTransactions')}</h2>
          <button
            onClick={() => navigate('/transactions')}
            className="text-sm text-primary-600 dark:text-primary-400 hover:text-primary-500 dark:hover:text-primary-300 transition-colors"
          >
            {t('common.viewAll')}
          </button>
        </div>
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-slate-200 dark:border-dark-600">
                <th className="table-header">{t('dashboard.table.transactionId')}</th>
                <th className="table-header">{t('dashboard.table.cardId')}</th>
                <th className="table-header">{t('dashboard.table.type')}</th>
                <th className="table-header">{t('dashboard.table.amount')}</th>
                <th className="table-header">{t('dashboard.table.status')}</th>
                <th className="table-header">{t('dashboard.table.time')}</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-slate-200 dark:divide-dark-600">
              {recentTransactions.map((tx: any) => (
                <tr key={tx.id} className="hover:bg-slate-50 dark:hover:bg-dark-700/30 transition-colors">
                  <td className="table-cell font-mono text-primary-600 dark:text-primary-400">{tx.id}</td>
                  <td className="table-cell font-mono text-slate-700 dark:text-slate-300">{tx.cardNumber || tx.cardId}</td>
                  <td className="table-cell">
                    <span
                      className={`inline-flex items-center gap-1.5 ${tx.type === 'PURCHASE'
                        ? 'text-slate-600 dark:text-slate-300'
                        : tx.type === 'RECHARGE'
                          ? 'text-success'
                          : 'text-warning'
                        }`}
                    >
                      {tx.type === 'PURCHASE' && <ArrowDownRight className="w-4 h-4" />}
                      {tx.type === 'RECHARGE' && <ArrowUpRight className="w-4 h-4" />}
                      {tx.type === 'REFUND' && <ArrowUpRight className="w-4 h-4" />}
                      {tx.type}
                    </span>
                  </td>
                  <td className="table-cell font-mono text-slate-700 dark:text-slate-200">
                    {tx.type === 'PURCHASE' ? '-' : '+'}
                    {safeNumber(tx.amount).toLocaleString()}
                  </td>
                  <td className="table-cell">
                    <span
                      className={`badge ${tx.status === 'COMPLETED'
                        ? 'badge-success'
                        : tx.status === 'PENDING'
                          ? 'badge-warning'
                          : 'badge-error'
                        }`}
                    >
                      {tx.status}
                    </span>
                  </td>
                  <td className="table-cell text-slate-500 dark:text-slate-400">
                    {new Date(tx.createdAt).toLocaleString()}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </motion.div>
    </motion.div>
  )
}

export default Dashboard
