import { useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
    FlaskConical,
    Lock,
    ArrowRightLeft,
    X,
    AlertTriangle
} from 'lucide-react'
import { ConfirmDialog, useConfirmDialog } from './ConfirmDialog'

interface DemoModeBannerProps {
    isDemo: boolean
    onSwitch: (toDemo: boolean) => Promise<void>
    onClose?: () => void
    accessTimeout?: number // 
    hideSwitchButton?: boolean // 
}

export function DemoModeBanner({
    isDemo,
    onSwitch,
    onClose,
    accessTimeout,
    hideSwitchButton
}: DemoModeBannerProps) {
    const { t } = useTranslation()
    const [isSwitching, setIsSwitching] = useState(false)
    const { confirm, setLoading, dialogProps } = useConfirmDialog()

    const handleSwitch = async () => {
        if (isDemo) {
            // 
            const ok = await confirm({
                type: 'warning',
                title: t('demo.switchToReal'),
                message: t('demo.switchToRealDesc'),
                riskWarning: t('demo.switchWarning'),
                confirmText: t('demo.confirmSwitch'),
                countdown: 3
            })

            if (ok) {
                setIsSwitching(true)
                setLoading(true)
                try {
                    await onSwitch(false)
                } finally {
                    setIsSwitching(false)
                    setLoading(false)
                }
            }
        } else {
            // 
            setIsSwitching(true)
            try {
                await onSwitch(true)
            } finally {
                setIsSwitching(false)
            }
        }
    }

    return (
        <>
            <motion.div
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -10 }}
                className={`relative overflow-hidden rounded-xl border backdrop-blur-md ${isDemo
                    ? 'bg-warning/10 border-warning/30'
                    : 'bg-success/10 border-success/30'
                    }`}
            >
                {/*  */}
                <div className={`absolute left-0 top-0 bottom-0 w-1 ${isDemo ? 'bg-warning' : 'bg-success'
                    }`} />

                <div className="flex items-center justify-between px-4 py-3 pl-5">
                    {/*  */}
                    <div className="flex items-center gap-3">
                        <div className={`p-2 rounded-lg ${isDemo ? 'bg-warning/20' : 'bg-success/20'
                            }`}>
                            {isDemo ? (
                                <FlaskConical className="w-5 h-5 text-warning" />
                            ) : (
                                <Lock className="w-5 h-5 text-success" />
                            )}
                        </div>
                        <div>
                            <p className={`font-medium ${isDemo ? 'text-warning' : 'text-success'
                                }`}>
                                {isDemo ? t('demo.demoMode') : t('demo.realMode')}
                            </p>
                            <p className="text-sm text-slate-500 dark:text-slate-400">
                                {isDemo ? t('demo.demoModeDesc') : t('demo.realModeDesc')}
                            </p>
                        </div>
                    </div>

                    {/*  */}
                    <div className="flex items-center gap-2">
                        {/*  */}
                        {!isDemo && accessTimeout && (
                            <span className="text-xs text-slate-500 bg-slate-100 dark:bg-dark-700 px-2 py-1 rounded">
                                {t('demo.accessTimeout', { time: formatTime(accessTimeout) })}
                            </span>
                        )}

                        {/*  -  */}
                        {!hideSwitchButton && (
                            <button
                                onClick={handleSwitch}
                                disabled={isSwitching}
                                className={`flex items-center gap-2 px-4 py-2 rounded-lg text-sm font-medium transition-colors disabled:opacity-50 ${isDemo
                                    ? 'bg-warning/20 text-warning hover:bg-warning/30'
                                    : 'bg-success/20 text-success hover:bg-success/30'
                                    }`}
                            >
                                <ArrowRightLeft className="w-4 h-4" />
                                {isDemo ? t('demo.switchToReal') : t('demo.switchToDemo')}
                            </button>
                        )}

                        {/*  */}
                        {onClose && (
                            <button
                                onClick={onClose}
                                className="p-1.5 hover:bg-slate-200 dark:hover:bg-dark-600 rounded-lg transition-colors"
                            >
                                <X className="w-4 h-4 text-slate-400" />
                            </button>
                        )}
                    </div>
                </div>
            </motion.div>

            <ConfirmDialog {...dialogProps} />
        </>
    )
}

// 
function formatTime(seconds: number): string {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins}:${secs.toString().padStart(2, '0')}`
}
