/**
 *  API 
 * 
 */

import type { Card } from '../types/api';
import type { Transaction } from '../types/transactions';

// 
let staticDataCache: Record<string, any> = {};

// 
const delay = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

// 
export const isStaticDemoMode = (): boolean => {
  return import.meta.env.VITE_STATIC_DEMO === 'true';
};

//  JSON 
const loadStaticData = async (endpoint: string): Promise<any> => {
  const cleanEndpoint = endpoint.replace(/^\//, '');
  if (staticDataCache[cleanEndpoint]) {
    return staticDataCache[cleanEndpoint];
  }
  try {
    await delay(200 + Math.random() * 600);
    const response = await fetch('/demo-data/' + cleanEndpoint + '.json');
    if (!response.ok) {
      throw new Error('Failed to load ' + cleanEndpoint + ': ' + response.status);
    }
    const data = await response.json();
    staticDataCache[cleanEndpoint] = data;
    return data;
  } catch (error) {
    console.error('[StaticDemo] Failed to load ' + cleanEndpoint + ':', error);
    throw error;
  }
};

//  localStorage
const saveModifiedData = (key: string, data: any): void => {
  try {
    localStorage.setItem('demo:' + key, JSON.stringify(data));
    staticDataCache[key] = data;
  } catch (error) {
    console.error('[StaticDemo] Failed to save data:', error);
  }
};

// ==========================================
// API 
// ==========================================

export const staticApi = {
  // ==========  ==========
  auth: {
    login: async (credentials: { username: string; password: string }) => {
      await delay(500 + Math.random() * 500);
      const userData = await loadStaticData('user');
      return {
        token: userData.session.token,
        user: userData.user,
        expiresAt: userData.session.expiresAt,
      };
    },
    logout: async () => {
      await delay(200);
      return { success: true };
    },
    me: async () => {
      const userData = await loadStaticData('user');
      return userData.user;
    },
  },

  // ==========  ==========
  cards: {
    getAll: async (filters?: { status?: string; type?: string; search?: string }) => {
      let data = await loadStaticData('cards');
      if (filters?.status) {
        data = { ...data, data: data.data.filter((card: Card) => card.status === filters.status) };
      }
      if (filters?.type) {
        data = { ...data, data: data.data.filter((card: Card) => card.type === filters.type) };
      }
      if (filters?.search) {
        const searchLower = filters.search.toLowerCase();
        data = { ...data, data: data.data.filter((card: Card) =>
          card.number.toLowerCase().includes(searchLower) ||
          card.id.toLowerCase().includes(searchLower)
        )};
      }
      return data;
    },
    getById: async (id: string) => {
      const { data } = await loadStaticData('cards');
      const card = data.find((c: Card) => c.id === id);
      if (!card) throw new Error('Card not found');
      return card;
    },
    create: async (cardData: Partial<Card>) => {
      await delay(300);
      const newCard: Card = {
        id: 'DEMO-' + (10000 + Math.floor(Math.random() * 90000)),
        number: 'CARD****' + String(Math.floor(Math.random() * 9999)).padStart(4, '0'),
        type: cardData.type || 'STANDARD',
        status: 'ACTIVE',
        balance: cardData.balance || 0,
        issuedAt: new Date().toISOString(),
        expiresAt: new Date(Date.now() + 5 * 365 * 24 * 60 * 60 * 1000).toISOString(),
        lastUsed: new Date().toISOString(),
        ...cardData,
      };
      const currentData = await loadStaticData('cards');
      const modifiedData = { ...currentData, data: [newCard, ...currentData.data], total: currentData.total + 1 };
      saveModifiedData('cards', modifiedData);
      return newCard;
    },
    update: async (id: string, updates: Partial<Card>) => {
      await delay(300);
      const currentData = await loadStaticData('cards');
      const cardIndex = currentData.data.findIndex((c: Card) => c.id === id);
      if (cardIndex === -1) throw new Error('Card not found');
      const updatedCard = { ...currentData.data[cardIndex], ...updates, id };
      const modifiedData = { ...currentData, data: [
        ...currentData.data.slice(0, cardIndex),
        updatedCard,
        ...currentData.data.slice(cardIndex + 1),
      ]};
      saveModifiedData('cards', modifiedData);
      return updatedCard;
    },
    delete: async (id: string) => {
      await delay(300);
      const currentData = await loadStaticData('cards');
      const modifiedData = { ...currentData, data: currentData.data.filter((c: Card) => c.id !== id), total: currentData.total - 1 };
      saveModifiedData('cards', modifiedData);
      return { success: true };
    },
    getStats: async () => {
      const { data } = await loadStaticData('cards');
      return {
        totalCards: data.length,
        activeCards: data.filter((c: Card) => c.status === 'ACTIVE').length,
        suspendedCards: data.filter((c: Card) => c.status === 'SUSPENDED').length,
        blacklistedCards: data.filter((c: Card) => c.status === 'BLACKLISTED').length,
        averageBalance: data.reduce((sum: number, c: Card) => sum + c.balance, 0) / data.length,
        totalBalance: data.reduce((sum: number, c: Card) => sum + c.balance, 0),
      };
    },
  },

  // ==========  ==========
  transactions: {
    getAll: async (filters?: { cardId?: string; type?: string; status?: string }) => {
      let data = await loadStaticData('transactions');
      if (filters?.cardId) data = { ...data, data: data.data.filter((t: Transaction) => t.cardId === filters.cardId) };
      if (filters?.type) data = { ...data, data: data.data.filter((t: Transaction) => t.type === filters.type) };
      if (filters?.status) data = { ...data, data: data.data.filter((t: Transaction) => t.status === filters.status) };
      return data;
    },
    getById: async (id: string) => {
      const { data } = await loadStaticData('transactions');
      const transaction = data.find((t: Transaction) => t.id === id);
      if (!transaction) throw new Error('Transaction not found');
      return transaction;
    },
    getStats: async () => loadStaticData('reports'),
  },

  // ==========  ==========
  terminals: {
    getAll: async (filters?: { status?: string; type?: string }) => {
      let data = await loadStaticData('terminals');
      if (filters?.status) data = { ...data, data: data.data.filter((t: any) => t.status === filters.status) };
      if (filters?.type) data = { ...data, data: data.data.filter((t: any) => t.type === filters.type) };
      return data;
    },
    getById: async (id: string) => {
      const { data } = await loadStaticData('terminals');
      const terminal = data.find((t: any) => t.id === id);
      if (!terminal) throw new Error('Terminal not found');
      return terminal;
    },
    update: async (id: string, updates: any) => {
      await delay(300);
      const currentData = await loadStaticData('terminals');
      const index = currentData.data.findIndex((t: any) => t.id === id);
      if (index === -1) throw new Error('Terminal not found');
      const updatedTerminal = { ...currentData.data[index], ...updates, id };
      const modifiedData = { ...currentData, data: [
        ...currentData.data.slice(0, index),
        updatedTerminal,
        ...currentData.data.slice(index + 1),
      ]};
      saveModifiedData('terminals', modifiedData);
      return updatedTerminal;
    },
  },

  // ==========  ==========
  dashboard: {
    getStats: async () => loadStaticData('dashboard'),
    getRecentActivity: async (limit: number = 5) => {
      const dashboard = await loadStaticData('dashboard');
      return dashboard.recentActivity.slice(0, limit);
    },
  },

  // ==========  ==========
  transit: {
    getRoutes: async () => (await loadStaticData('transit')).routes,
    getStations: async () => (await loadStaticData('transit')).stations,
    getVehicles: async () => (await loadStaticData('transit')).vehicles,
    getAnalytics: async () => (await loadStaticData('transit')).analytics,
  },

  // ========== MDM  ==========
  mdm: {
    getDevices: async () => (await loadStaticData('mdm')).devices,
    getDeviceById: async (id: string) => {
      const { data } = await loadStaticData('mdm');
      const device = data.devices.find((d: any) => d.id === id);
      if (!device) throw new Error('Device not found');
      return device;
    },
    getMetrics: async () => (await loadStaticData('mdm')).metrics,
    getFleetHealth: async () => (await loadStaticData('mdm')).fleetHealth,
  },

  // ==========  ==========
  reports: {
    getDaily: async () => (await loadStaticData('reports')).daily,
    getWeekly: async () => (await loadStaticData('reports')).weekly,
    getMonthly: async () => (await loadStaticData('reports')).monthly,
  },
};

// ==========================================
// 
// ==========================================

export const staticClient = {
  get: async (endpoint: string, params?: Record<string, any>) => {
    const queryString = params ? '?' + Object.entries(params).map(([k, v]) => encodeURIComponent(k) + '=' + encodeURIComponent(v)).join('&') : '';
    return loadStaticData(endpoint + queryString);
  },
  post: async (endpoint: string, data: any) => {
    await delay(300 + Math.random() * 400);
    return { success: true, data };
  },
  put: async (endpoint: string, data: any) => {
    await delay(300 + Math.random() * 400);
    return { success: true, data };
  },
  delete: async (endpoint: string) => {
    await delay(300 + Math.random() * 400);
    return { success: true };
  },
};

// ==========================================
// 
// ==========================================

export const clearDemoModifications = (): void => {
  Object.keys(localStorage).forEach(key => {
    if (key.startsWith('demo:')) {
      localStorage.removeItem(key);
    }
  });
  staticDataCache = {};
};

export const getDemoInfo = async () => {
  try {
    const response = await fetch('/demo-data/index.json');
    return await response.json();
  } catch (error) {
    console.error('[StaticDemo] Failed to load demo info:', error);
    return null;
  }
};

export default staticApi;
