import { useEffect, useRef } from 'react';

interface Particle {
    x: number;
    y: number;
    vx: number;
    vy: number;
    type: 'bus' | 'subway' | 'data';
    color: string;
    size: number;
}

interface Station {
    x: number;
    y: number;
    r: number; // radius
    active: boolean;
    pulse: number;
}

export default function LoginBackground() {
    const canvasRef = useRef<HTMLCanvasElement>(null);

    useEffect(() => {
        const canvas = canvasRef.current;
        if (!canvas) return;

        const ctx = canvas.getContext('2d');
        if (!ctx) return;

        let width = (canvas.width = window.innerWidth);
        let height = (canvas.height = window.innerHeight);

        // Configuration
        const stations: Station[] = [];
        const particles: Particle[] = [];
        const stationCount = 20;
        const particleCount = 60;

        // Create Stations (Gates/Stops)
        for (let i = 0; i < stationCount; i++) {
            stations.push({
                x: Math.random() * width,
                y: Math.random() * height,
                r: 3 + Math.random() * 3,
                active: false,
                pulse: 0,
            });
        }

        // Create Particles (Buses/Subways)
        for (let i = 0; i < particleCount; i++) {
            const type = Math.random() > 0.7 ? 'bus' : Math.random() > 0.4 ? 'subway' : 'data';
            particles.push({
                x: Math.random() * width,
                y: Math.random() * height,
                vx: (Math.random() - 0.5) * (type === 'subway' ? 2 : 1),
                vy: (Math.random() - 0.5) * (type === 'subway' ? 2 : 1),
                type,
                color:
                    type === 'subway'
                        ? '#22d3ee' // Cyan for Subway
                        : type === 'bus'
                            ? '#e879f9' // Purple for Bus
                            : '#94a3b8', // Grey for Data
                size: type === 'subway' ? 3 : type === 'bus' ? 2.5 : 1.5,
            });
        }

        let animationFrameId: number;

        const drawSubwayLine = (p1: Station, p2: Station) => {
            ctx.beginPath();
            ctx.strokeStyle = 'rgba(34, 211, 238, 0.1)'; // Faint Cyan
            ctx.lineWidth = 1;
            ctx.moveTo(p1.x, p1.y);
            ctx.lineTo(p2.x, p2.y);
            ctx.stroke();
        };

        const drawGate = (s: Station) => {
            //  - 
            ctx.fillStyle = 'rgba(71, 85, 105, 0.9)';
            ctx.fillRect(s.x - 20, s.y - 15, 8, 30);
            ctx.fillRect(s.x + 12, s.y - 15, 8, 30);

            // 
            ctx.fillStyle = 'rgba(51, 65, 85, 1)';
            ctx.fillRect(s.x - 20, s.y - 18, 40, 5);

            // 
            ctx.fillStyle = 'rgba(34, 211, 238, 0.8)';
            ctx.fillRect(s.x - 10, s.y - 3, 20, 6);

            // 
            if (s.pulse > 0) {
                ctx.beginPath();
                ctx.strokeStyle = `rgba(34, 211, 238, ${s.pulse})`;
                ctx.lineWidth = 3;
                ctx.arc(s.x, s.y, 25 + (1 - s.pulse) * 20, 0, Math.PI * 2);
                ctx.stroke();
                s.pulse -= 0.02;
            } else if (Math.random() > 0.98) {
                s.pulse = 1;
            }
        };

        const render = () => {
            ctx.clearRect(0, 0, width, height);

            // Draw faint connections (Routes)
            for (let i = 0; i < stations.length; i++) {
                for (let j = i + 1; j < stations.length; j++) {
                    const dx = stations[i].x - stations[j].x;
                    const dy = stations[i].y - stations[j].y;
                    const dist = Math.sqrt(dx * dx + dy * dy);
                    if (dist < 200) {
                        drawSubwayLine(stations[i], stations[j]);
                    }
                }
            }

            // Draw Stations (Gates)
            stations.forEach((s) => drawGate(s));

            // Update and Draw Particles
            particles.forEach((p) => {
                p.x += p.vx;
                p.y += p.vy;

                // Bounce off walls
                if (p.x < 0 || p.x > width) p.vx *= -1;
                if (p.y < 0 || p.y > height) p.vy *= -1;

                // Draw Particle
                const angle = Math.atan2(p.vy, p.vx);
                ctx.save();
                ctx.translate(p.x, p.y);
                ctx.rotate(angle);

                if (p.type === 'subway') {
                    // 
                    ctx.fillStyle = 'rgba(34, 211, 238, 0.9)';
                    ctx.fillRect(-25, -8, 50, 16);
                    // 
                    ctx.fillStyle = 'rgba(255, 255, 255, 0.4)';
                    ctx.fillRect(-20, -4, 10, 8);
                    ctx.fillRect(-5, -4, 10, 8);
                    ctx.fillRect(10, -4, 10, 8);
                    // 
                    ctx.fillStyle = 'rgba(34, 211, 238, 1)';
                    ctx.beginPath();
                    ctx.moveTo(25, -8);
                    ctx.lineTo(30, 0);
                    ctx.lineTo(25, 8);
                    ctx.fill();
                } else if (p.type === 'bus') {
                    // 
                    ctx.fillStyle = 'rgba(232, 121, 249, 0.9)';
                    ctx.fillRect(-20, -10, 40, 20);
                    // 
                    ctx.fillStyle = 'rgba(255, 255, 255, 0.4)';
                    ctx.fillRect(-15, -6, 8, 12);
                    ctx.fillRect(-3, -6, 8, 12);
                    ctx.fillRect(9, -6, 8, 12);
                    // 
                    ctx.fillStyle = 'rgba(51, 65, 85, 1)';
                    ctx.beginPath();
                    ctx.arc(-10, 10, 3, 0, Math.PI * 2);
                    ctx.arc(10, 10, 3, 0, Math.PI * 2);
                    ctx.fill();
                } else {
                    // 
                    ctx.fillStyle = 'rgba(148, 163, 184, 0.9)';
                    ctx.fillRect(-8, -6, 16, 12);
                    // 
                    ctx.fillStyle = 'rgba(251, 191, 36, 1)';
                    ctx.fillRect(-4, -2, 8, 4);
                }

                ctx.restore();

                // Proximity to stations (Simulate stopping/interaction)
                stations.forEach(s => {
                    const dx = p.x - s.x;
                    const dy = p.y - s.y;
                    const dist = Math.sqrt(dx * dx + dy * dy);
                    if (dist < 30) {
                        ctx.beginPath();
                        ctx.strokeStyle = p.color;
                        ctx.moveTo(p.x, p.y);
                        ctx.lineTo(s.x, s.y);
                        ctx.stroke();
                    }
                })
            });

            animationFrameId = requestAnimationFrame(render);
        };

        render();

        const handleResize = () => {
            width = canvas.width = window.innerWidth;
            height = canvas.height = window.innerHeight;
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
            cancelAnimationFrame(animationFrameId);
        };
    }, []);

    return (
        <canvas
            ref={canvasRef}
            className="absolute inset-0 z-0 bg-slate-900 pointer-events-none"
        />
    );
}
