import { useState, useEffect } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import { FlaskConical, Lock } from 'lucide-react'
import { isDemoMode, toggleDemoMode } from '../demoMode'
import { useAuth } from '../../hooks/useAuth'
import { DemoModeBanner as NewDemoModeBanner } from '../../components/transit/DemoModeBanner'
import { EnvironmentIndicator } from '../../components/transit/EnvironmentIndicator'

// Export new DemoBanner (using optimized component)
export function DemoBanner() {
  const [isDismissed, setIsDismissed] = useState(false)
  const isDemo = isDemoMode()
  const { isDemoMode: isDemoUser } = useAuth()
  const [timeLeft, setTimeLeft] = useState<number | undefined>(undefined)
  //  (30 = 1800)
  const [demoSessionTimeLeft, setDemoSessionTimeLeft] = useState<number>(1800)

  const handleSwitch = async (toDemo: boolean) => {
    if (isDemoUser) return // 
    toggleDemoMode()
    if (!toDemo) {
      //  10 
      setTimeLeft(600)
    } else {
      setTimeLeft(undefined)
    }
  }

  // 
  useEffect(() => {
    if (isDemo) {
      setTimeLeft(undefined)
      return
    }

    if (timeLeft === undefined) {
      setTimeLeft(600) //  10 
    }

    if (timeLeft === 0) {
      toggleDemoMode()
      return
    }

    const timer = setInterval(() => {
      setTimeLeft(prev => prev !== undefined ? Math.max(0, prev - 1) : undefined)
    }, 1000)

    return () => clearInterval(timer)
  }, [isDemo, timeLeft])

  // 
  useEffect(() => {
    if (!isDemo || !isDemoUser) return

    const timer = setInterval(() => {
      setDemoSessionTimeLeft(prev => Math.max(0, prev - 1))
    }, 1000)

    return () => clearInterval(timer)
  }, [isDemo, isDemoUser])

  if (isDismissed) return null

  return (
    <div className="fixed top-0 left-1/2 -translate-x-1/2 z-[60] p-2 w-full max-w-xl pointer-events-none">
      <div className="pointer-events-auto">
        <NewDemoModeBanner
          isDemo={isDemo}
          onSwitch={handleSwitch}
          onClose={() => setIsDismissed(true)}
          accessTimeout={isDemo && isDemoUser ? demoSessionTimeLeft : timeLeft}
          hideSwitchButton={isDemoUser}
        />
      </div>
    </div>
  )
}

// 
export function DemoEnvironmentIndicator() {
  const { isDemoMode: isDemoUser } = useAuth()

  const handleClick = () => {
    if (isDemoUser) return // 
    toggleDemoMode()
  }

  return (
    <EnvironmentIndicator
      isDemo={isDemoMode()}
      onClick={handleClick}
      disabled={isDemoUser}
    />
  )
}

// Keep original DemoBadge
export function DemoBadge() {
  const { t } = useTranslation()
  if (!isDemoMode()) return null

  return (
    <span className="inline-flex items-center gap-1 px-2 py-0.5 rounded text-xs font-medium bg-warning/15 text-warning border border-warning/30">
      <FlaskConical className="w-3 h-3" />
      DEMO
    </span>
  )
}

// Keep original DemoToggle (optimized style)
export function DemoToggle() {
  const { t } = useTranslation()
  const { isDemoMode: isDemoUser } = useAuth()
  const demoMode = isDemoMode()

  return (
    <button
      onClick={() => !isDemoUser && toggleDemoMode()}
      disabled={isDemoUser}
      className={`flex items-center gap-2 px-3 py-2 rounded-lg transition-colors ${isDemoUser ? 'opacity-70 cursor-not-allowed' : ''
        } ${demoMode
          ? 'bg-warning/10 hover:bg-warning/20'
          : 'bg-success/10 hover:bg-success/20'
        }`}
      title={isDemoUser ? t('demo.demoModeLocked') : (demoMode ? t('demo.switchToReal') : t('demo.switchToDemo'))}
    >
      {demoMode ? (
        <>
          <FlaskConical className="w-4 h-4 text-warning" />
          <span className="text-sm text-warning font-medium">{t('demo.demoMode')}</span>
        </>
      ) : (
        <>
          <Lock className="w-4 h-4 text-success" />
          <span className="text-sm text-success font-medium">{t('demo.realMode')}</span>
        </>
      )}
    </button>
  )
}