import { useState, useCallback, useRef } from 'react';
import type { SpotlightStep } from './SpotlightTour';

export interface UseSpotlightReturn {
  isActive: boolean;
  currentStep: number;
  startTour: () => void;
  stopTour: () => void;
  nextStep: () => void;
  prevStep: () => void;
  goToStep: (step: number) => void;
}

export function useSpotlight(steps: SpotlightStep[], tourId: string): UseSpotlightReturn {
  const [isActive, setIsActive] = useState(false);
  const [currentStep, setCurrentStep] = useState(0);
  const hasStartedRef = useRef(false);

  const startTour = useCallback(() => {
    const hasSeen = localStorage.getItem(`spotlight_${tourId}`);
    if (!hasSeen || hasStartedRef.current) {
      setIsActive(true);
      setCurrentStep(0);
      hasStartedRef.current = true;
    }
  }, [tourId]);

  const stopTour = useCallback(() => {
    setIsActive(false);
    setCurrentStep(0);
    localStorage.setItem(`spotlight_${tourId}`, 'completed');
  }, [tourId]);

  const nextStep = useCallback(() => {
    if (currentStep < steps.length - 1) {
      setCurrentStep(prev => prev + 1);
    } else {
      stopTour();
    }
  }, [currentStep, steps.length, stopTour]);

  const prevStep = useCallback(() => {
    if (currentStep > 0) {
      setCurrentStep(prev => prev - 1);
    }
  }, [currentStep]);

  const goToStep = useCallback((step: number) => {
    if (step >= 0 && step < steps.length) {
      setCurrentStep(step);
    }
  }, [steps.length]);

  return {
    isActive,
    currentStep,
    startTour,
    stopTour,
    nextStep,
    prevStep,
    goToStep,
  };
}
