import { useEffect, useState } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { motion } from 'framer-motion';
import { useTranslation } from 'react-i18next';
import { Loader2, CheckCircle, AlertCircle } from 'lucide-react';

export default function OAuthCallback() {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const { t } = useTranslation();
  const [status, setStatus] = useState<'loading' | 'success' | 'error'>('loading');
  const [message, setMessage] = useState('');

  useEffect(() => {
    const accessToken = searchParams.get('access_token');
    const refreshToken = searchParams.get('refresh_token');
    const userId = searchParams.get('user_id');
    const isNewUser = searchParams.get('is_new_user') === 'true';
    const expiresAt = searchParams.get('expires_at');
    const error = searchParams.get('error');

    if (error) {
      setStatus('error');
      setMessage(t('login.errors.oauthFailed'));
      setTimeout(() => {
        navigate('/login');
      }, 3000);
      return;
    }

    if (!accessToken || !userId) {
      setStatus('error');
      setMessage(t('login.errors.oauthFailed'));
      setTimeout(() => {
        navigate('/login');
      }, 3000);
      return;
    }

    localStorage.setItem('auth_token', accessToken);
    if (refreshToken) {
      localStorage.setItem('refresh_token', refreshToken);
    }
    localStorage.setItem('demo_token', accessToken);
    localStorage.setItem('transitcard_demo_mode', 'true');

    const user = {
      userId,
      email: '',
      username: '',
      displayName: isNewUser ? t('demo.demoVisitor') : '',
      role: 'demo',
      status: 'active',
      projectId: null,
      permissions: {}
    };
    localStorage.setItem('user', JSON.stringify(user));
    localStorage.setItem('user_role', 'demo');

    if (expiresAt) {
      localStorage.setItem('demo_expires_at', new Date(parseInt(expiresAt)).toISOString());
    }

    setStatus('success');
    setMessage(isNewUser ? t('login.oauthWelcome') : t('login.oauthSuccess'));

    setTimeout(() => {
      navigate('/');
    }, 1500);
  }, [searchParams, navigate, t]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 dark:from-slate-900 dark:to-slate-800 flex items-center justify-center p-4">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="w-full max-w-md"
      >
        <div className="bg-white dark:bg-slate-800 rounded-2xl shadow-2xl p-8 text-center">
          {status === 'loading' && (
            <>
              <Loader2 className="w-16 h-16 text-blue-600 animate-spin mx-auto mb-4" />
              <h2 className="text-xl font-bold text-slate-900 dark:text-white mb-2">
                {t('login.oauthProcessing')}
              </h2>
              <p className="text-slate-500 dark:text-slate-400">
                {t('login.pleaseWait')}
              </p>
            </>
          )}

          {status === 'success' && (
            <>
              <CheckCircle className="w-16 h-16 text-green-600 mx-auto mb-4" />
              <h2 className="text-xl font-bold text-slate-900 dark:text-white mb-2">
                {t('login.success')}
              </h2>
              <p className="text-slate-500 dark:text-slate-400">
                {message}
              </p>
            </>
          )}

          {status === 'error' && (
            <>
              <AlertCircle className="w-16 h-16 text-red-600 mx-auto mb-4" />
              <h2 className="text-xl font-bold text-slate-900 dark:text-white mb-2">
                {t('login.error')}
              </h2>
              <p className="text-slate-500 dark:text-slate-400">
                {message}
              </p>
            </>
          )}
        </div>
      </motion.div>
    </div>
  );
}
