import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { useTranslation } from 'react-i18next';
import {
  Play,
  Eye,
  Shield,
  Globe,
  ArrowRight,
  CheckCircle,
  Info,
  Github,
  ExternalLink,
} from 'lucide-react';
import { initStaticDemo, isStaticDemoInitialized } from '../demo/staticDemo';

export default function StaticDemoLogin() {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);

  // 
  useEffect(() => {
    if (isStaticDemoInitialized()) {
      navigate('/');
    }
  }, [navigate]);

  const handleStartDemo = async () => {
    setIsLoading(true);

    // 
    await new Promise(resolve => setTimeout(resolve, 800));

    // 
    initStaticDemo();

    // 
    navigate('/', { replace: true });
  };

  const features = [
    {
      icon: Eye,
      title: t('staticDemo.features.fullAccess', ''),
      description: t(
        'staticDemo.features.fullAccessDesc',
        ''
      ),
    },
    {
      icon: Shield,
      title: t('staticDemo.features.noBackend', ''),
      description: t(
        'staticDemo.features.noBackendDesc',
        ''
      ),
    },
    {
      icon: Globe,
      title: t('staticDemo.features.deployAnywhere', ''),
      description: t(
        'staticDemo.features.deployAnywhereDesc',
        ' GitHub PagesNetlifyVercel '
      ),
    },
  ];

  const limitations = [
    t('staticDemo.limitations.localData', ''),
    t('staticDemo.limitations.refreshReset', ''),
    t('staticDemo.limitations.noRealtime', ''),
    t('staticDemo.limitations.singleUser', ''),
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-indigo-50 to-purple-50 dark:from-gray-900 dark:via-gray-800 dark:to-gray-900 flex items-center justify-center p-4">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="w-full max-w-5xl"
      >
        {/*  */}
        <div className="bg-white/80 dark:bg-gray-800/80 backdrop-blur-xl rounded-3xl shadow-2xl overflow-hidden border border-white/20 dark:border-gray-700/50">
          <div className="grid lg:grid-cols-2">
            {/*  */}
            <div className="p-8 lg:p-12 bg-gradient-to-br from-blue-600 via-indigo-600 to-purple-600 text-white">
              <motion.div
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.2 }}
              >
                <div className="flex items-center gap-3 mb-8">
                  <div className="w-14 h-14 bg-white/20 rounded-2xl flex items-center justify-center backdrop-blur-sm">
                    <Play className="w-7 h-7 text-white" />
                  </div>
                  <div>
                    <h1 className="text-2xl font-bold">
                      {t('staticDemo.title', '')}
                    </h1>
                    <p className="text-blue-100 text-sm">
                      {t('staticDemo.subtitle', 'TransitCard Pro Demo')}
                    </p>
                  </div>
                </div>

                <p className="text-blue-50 mb-8 leading-relaxed">
                  {t(
                    'staticDemo.description',
                    ' JSON '
                  )}
                </p>

                <div className="space-y-4">
                  {features.map((feature, index) => (
                    <motion.div
                      key={index}
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      transition={{ delay: 0.3 + index * 0.1 }}
                      className="flex items-start gap-4 p-4 bg-white/10 rounded-xl backdrop-blur-sm"
                    >
                      <div className="w-10 h-10 bg-white/20 rounded-lg flex items-center justify-center flex-shrink-0">
                        <feature.icon className="w-5 h-5" />
                      </div>
                      <div>
                        <h3 className="font-semibold">{feature.title}</h3>
                        <p className="text-sm text-blue-100 mt-1">
                          {feature.description}
                        </p>
                      </div>
                    </motion.div>
                  ))}
                </div>
              </motion.div>
            </div>

            {/*  */}
            <div className="p-8 lg:p-12 flex flex-col justify-center">
              <motion.div
                initial={{ opacity: 0, x: 20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.3 }}
              >
                {/*  */}
                <button
                  onClick={handleStartDemo}
                  disabled={isLoading}
                  className="w-full mb-6 py-5 px-6 bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 disabled:from-blue-400 disabled:to-indigo-400 text-white rounded-2xl font-semibold flex items-center justify-center gap-3 transition-all duration-300 shadow-lg hover:shadow-xl disabled:cursor-not-allowed group"
                >
                  {isLoading ? (
                    <>
                      <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                      <span>{t('staticDemo.loading', '...')}</span>
                    </>
                  ) : (
                    <>
                      <Play className="w-5 h-5 group-hover:scale-110 transition-transform" />
                      <span>{t('staticDemo.startButton', '')}</span>
                      <ArrowRight className="w-5 h-5 group-hover:translate-x-1 transition-transform" />
                    </>
                  )}
                </button>

                {/*  */}
                <div className="p-5 bg-amber-50/80 dark:bg-amber-900/20 border border-amber-200 dark:border-amber-800/50 rounded-2xl mb-6">
                  <div className="flex items-center gap-2 mb-3">
                    <Info className="w-4 h-4 text-amber-600 dark:text-amber-400" />
                    <span className="text-sm font-semibold text-amber-800 dark:text-amber-200">
                      {t('staticDemo.limitationsTitle', '')}
                    </span>
                  </div>
                  <ul className="space-y-2">
                    {limitations.map((limitation, index) => (
                      <li
                        key={index}
                        className="flex items-center gap-2 text-sm text-amber-700 dark:text-amber-300"
                      >
                        <CheckCircle className="w-4 h-4 text-amber-500 flex-shrink-0" />
                        <span>{limitation}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/*  */}
                <div className="p-5 bg-gray-50 dark:bg-gray-700/50 rounded-2xl">
                  <h3 className="text-sm font-semibold text-gray-700 dark:text-gray-300 mb-3">
                    {t('staticDemo.deployTitle', '')}
                  </h3>
                  <div className="space-y-2">
                    <a
                      href="#"
                      className="flex items-center gap-3 p-3 bg-white dark:bg-gray-600 rounded-xl hover:shadow-md transition-shadow group"
                    >
                      <div className="w-10 h-10 bg-gray-100 dark:bg-gray-500 rounded-lg flex items-center justify-center">
                        <Github className="w-5 h-5 text-gray-700 dark:text-gray-300" />
                      </div>
                      <div className="flex-1">
                        <div className="text-sm font-medium text-gray-900 dark:text-white">
                          GitHub Pages
                        </div>
                        <div className="text-xs text-gray-500 dark:text-gray-400">
                          
                        </div>
                      </div>
                      <ExternalLink className="w-4 h-4 text-gray-400 group-hover:text-gray-600 dark:group-hover:text-gray-300" />
                    </a>
                    <a
                      href="#"
                      className="flex items-center gap-3 p-3 bg-white dark:bg-gray-600 rounded-xl hover:shadow-md transition-shadow group"
                    >
                      <div className="w-10 h-10 bg-gray-100 dark:bg-gray-500 rounded-lg flex items-center justify-center">
                        <Globe className="w-5 h-5 text-gray-700 dark:text-gray-300" />
                      </div>
                      <div className="flex-1">
                        <div className="text-sm font-medium text-gray-900 dark:text-white">
                          Netlify / Vercel
                        </div>
                        <div className="text-xs text-gray-500 dark:text-gray-400">
                          
                        </div>
                      </div>
                      <ExternalLink className="w-4 h-4 text-gray-400 group-hover:text-gray-600 dark:group-hover:text-gray-300" />
                    </a>
                  </div>
                </div>

                {/*  */}
                <div className="mt-8 flex items-center justify-center gap-6 text-sm">
                  <a
                    href="/login"
                    className="text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200 transition-colors"
                  >
                    {t('staticDemo.backToLogin', '')}
                  </a>
                  <span className="text-gray-300 dark:text-gray-600">|</span>
                  <a
                    href="/demo-login"
                    className="text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200 transition-colors"
                  >
                    {t('staticDemo.tryDynamicDemo', '')}
                  </a>
                </div>
              </motion.div>
            </div>
          </div>
        </div>

        {/*  */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.8 }}
          className="mt-8 text-center"
        >
          <p className="text-sm text-gray-500 dark:text-gray-400">
            {t(
              'staticDemo.footer',
              'TransitCard Pro - '
            )}
          </p>
          <p className="text-xs text-gray-400 dark:text-gray-500 mt-2">
            {t('staticDemo.version', ' v1.0.0')}
          </p>
        </motion.div>
      </motion.div>
    </div>
  );
}
