import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { reportsApi } from '../api/reports'
import { isDemoMode } from '../demo/demoMode'
import i18n from '../i18n'
import type {
  Report,
  CreateReportRequest,
  DashboardMetrics,
  ChartData,
  TopRouteData,
  PeakHourData,
  CardTypeDistribution,
} from '../api/reports'

const REPORTS_QUERY_KEY = 'reports'
const DASHBOARD_METRICS_QUERY_KEY = 'dashboardMetrics'
const REVENUE_CHART_QUERY_KEY = 'revenueChart'
const TOP_ROUTES_QUERY_KEY = 'topRoutes'
const PEAK_HOURS_QUERY_KEY = 'peakHours'
const CARD_TYPE_DISTRIBUTION_QUERY_KEY = 'cardTypeDistribution'

export const useReports = () => {
  return useQuery({
    queryKey: [REPORTS_QUERY_KEY],
    queryFn: () => reportsApi.getReports(),
  })
}

export const useCreateReport = () => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: (data: CreateReportRequest) => reportsApi.createReport(data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [REPORTS_QUERY_KEY] })
    },
  })
}

export const useDeleteReport = () => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: (id: string) => reportsApi.deleteReport(id),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [REPORTS_QUERY_KEY] })
    },
  })
}

export const useDownloadReport = () => {
  return useMutation({
    mutationFn: (id: string) => reportsApi.downloadReport(id),
  })
}

export const useDashboardMetrics = () => {
  return useQuery({
    queryKey: [DASHBOARD_METRICS_QUERY_KEY],
    queryFn: async () => {
      if (isDemoMode()) {
        return {
          totalTransactions: Math.floor(Math.random() * 50000 + 10000),
          totalRevenue: Math.floor(Math.random() * 100000 + 50000),
          activeCards: Math.floor(Math.random() * 5000 + 1000),
          activeTerminals: Math.floor(Math.random() * 100 + 20),
          averageTransactionAmount: Math.floor(Math.random() * 50 + 10),
          successRate: Math.floor(Math.random() * 5 + 95),
          todayTransactions: Math.floor(Math.random() * 5000 + 1000),
          todayRevenue: Math.floor(Math.random() * 10000 + 5000),
          yesterdayTransactions: Math.floor(Math.random() * 4000 + 800),
          yesterdayRevenue: Math.floor(Math.random() * 8000 + 4000),
          transactionGrowth: Math.floor(Math.random() * 20 - 5),
          revenueGrowth: Math.floor(Math.random() * 15 - 3),
        }
      }
      return reportsApi.getDashboardMetrics()
    },
    refetchInterval: isDemoMode() ? 5000 : 60000,
  })
}

export const useRevenueChart = (period: '7d' | '30d' | '90d') => {
  return useQuery({
    queryKey: [REVENUE_CHART_QUERY_KEY, period],
    queryFn: async () => {
      if (isDemoMode()) {
        const days = period === '7d' ? 7 : period === '30d' ? 30 : 90
        const labels = Array.from({ length: days }).map((_, i) => {
          const date = new Date()
          date.setDate(date.getDate() - (days - 1 - i))
          return date.toLocaleDateString(i18n.language === 'zh' ? 'zh-CN' : 'en-US', { month: 'short', day: 'numeric' })
        })
        return {
          labels,
          datasets: [
            {
              label: i18n.t('charts.transactionVolume'),
              data: labels.map(() => Math.floor(Math.random() * 1000 + 200)),
            },
            {
              label: i18n.t('charts.revenue'),
              data: labels.map(() => Math.floor(Math.random() * 5000 + 1000)),
            },
          ],
        }
      }
      return reportsApi.getRevenueChart(period)
    },
    refetchInterval: isDemoMode() ? 5000 : false,
  })
}

export const useTopRoutes = (limit: number = 5) => {
  return useQuery({
    queryKey: [TOP_ROUTES_QUERY_KEY, limit],
    queryFn: () => reportsApi.getTopRoutes(limit),
  })
}

export const usePeakHours = () => {
  return useQuery({
    queryKey: [PEAK_HOURS_QUERY_KEY],
    queryFn: () => reportsApi.getPeakHours(),
  })
}

export const useCardTypeDistribution = () => {
  return useQuery({
    queryKey: [CARD_TYPE_DISTRIBUTION_QUERY_KEY],
    queryFn: async () => {
      if (isDemoMode()) {
        return [
          { type: 'STANDARD', count: 1250, percentage: 45.5 },
          { type: 'STUDENT', count: 680, percentage: 24.8 },
          { type: 'SENIOR', count: 420, percentage: 15.3 },
          { type: 'TOURIST', count: 395, percentage: 14.4 },
        ]
      }
      return reportsApi.getCardTypeDistribution()
    },
  })
}
