import apiClient from './client'
import type { PaginatedResponse } from '../types/api'

export interface Terminal {
  id: string
  type: 'ONBOARD' | 'TOPUP' | 'VALIDATOR'
  status: 'ONLINE' | 'OFFLINE' | 'MAINTENANCE'
  route: string | null
  lastSync: string
  battery: number
  storage: number
  pendingTx: number
  location: string
  // / (V1 )
  serialNumber?: string
  manufacturer?: string
  model?: string
  firmwareVersion?: string
  purchaseDate?: string
  warrantyExpiry?: string
  stationId?: string
  blacklistVersion?: string
  transactionCount?: number
}

export interface TerminalFilters {
  type?: string
  status?: string
  search?: string
  page?: number
  limit?: number
}

export interface RegisterTerminalRequest {
  id: string
  type: 'ONBOARD' | 'TOPUP' | 'VALIDATOR'
  location: string
  route?: string
}

export interface UpdateTerminalRequest {
  location?: string
  route?: string
  status?: 'ONLINE' | 'OFFLINE' | 'MAINTENANCE'
}

export interface TerminalStats {
  total: number
  online: number
  offline: number
  maintenance: number
  pendingSync: number
}

export const terminalsApi = {
  getTerminals: async (filters?: TerminalFilters): Promise<PaginatedResponse<Terminal>> => {
    const response = await apiClient.get('/terminals', { params: filters })
    return response.data
  },

  getTerminal: async (id: string): Promise<Terminal> => {
    const response = await apiClient.get(`/terminals/${id}`)
    return response.data
  },

  registerTerminal: async (data: RegisterTerminalRequest): Promise<Terminal> => {
    const response = await apiClient.post('/terminals', data)
    return response.data
  },

  updateTerminal: async (id: string, data: UpdateTerminalRequest): Promise<Terminal> => {
    const response = await apiClient.patch(`/terminals/${id}`, data)
    return response.data
  },

  deleteTerminal: async (id: string): Promise<void> => {
    await apiClient.delete(`/terminals/${id}`)
  },

  getTerminalStats: async (): Promise<TerminalStats> => {
    const response = await apiClient.get('/terminals/stats')
    return response.data
  },

  syncTerminal: async (id: string): Promise<void> => {
    await apiClient.post(`/terminals/${id}/sync`)
  },

  heartbeat: async (id: string): Promise<void> => {
    await apiClient.post(`/terminals/${id}/heartbeat`)
  },
}
