import apiClient from './client'

export interface Report {
  id: string
  name: string
  type: ReportType
  format: ReportFormat
  status: ReportStatus
  parameters: ReportParameters
  fileUrl?: string
  fileSize?: number
  createdBy: string
  createdAt: string
  completedAt?: string
}

export type ReportType = 
  | 'TRANSACTION_SUMMARY' 
  | 'TRANSACTION_DETAIL' 
  | 'REVENUE_ANALYSIS' 
  | 'CARD_USAGE' 
  | 'TERMINAL_PERFORMANCE' 
  | 'ROUTE_ANALYTICS'
  | 'CUSTOM'

export type ReportFormat = 'PDF' | 'EXCEL' | 'CSV' | 'JSON'

export type ReportStatus = 'PENDING' | 'GENERATING' | 'COMPLETED' | 'FAILED'

export interface ReportParameters {
  startDate?: string
  endDate?: string
  cardId?: string
  terminalId?: string
  routeId?: string
  groupBy?: 'DAY' | 'WEEK' | 'MONTH' | 'YEAR'
  includeCharts?: boolean
  filters?: Record<string, any>
}

export interface CreateReportRequest {
  name: string
  type: ReportType
  format: ReportFormat
  parameters: ReportParameters
}

export interface DashboardMetrics {
  totalTransactions: number
  totalRevenue: number
  activeCards: number
  activeTerminals: number
  averageTransactionAmount: number
  successRate: number
  todayTransactions: number
  todayRevenue: number
  yesterdayTransactions: number
  yesterdayRevenue: number
  transactionGrowth: number
  revenueGrowth: number
}

export interface ChartData {
  labels: string[]
  datasets: ChartDataset[]
}

export interface ChartDataset {
  label: string
  data: number[]
  backgroundColor?: string | string[]
  borderColor?: string
  borderWidth?: number
}

export interface TopRouteData {
  routeId: string
  routeName: string
  transactionCount: number
  totalRevenue: number
  passengerCount: number
}

export interface PeakHourData {
  hour: number
  transactionCount: number
  avgAmount: number
}

export interface CardTypeDistribution {
  type: string
  count: number
  percentage: number
}

export const reportsApi = {
  getReports: async (): Promise<Report[]> => {
    const response = await apiClient.get('/reports')
    return response.data
  },

  createReport: async (data: CreateReportRequest): Promise<Report> => {
    const response = await apiClient.post('/reports', data)
    return response.data
  },

  deleteReport: async (id: string): Promise<void> => {
    await apiClient.delete(`/reports/${id}`)
  },

  downloadReport: async (id: string): Promise<Blob> => {
    const response = await apiClient.get(`/reports/${id}/download`, {
      responseType: 'blob',
    })
    return response.data
  },

  getDashboardMetrics: async (): Promise<DashboardMetrics> => {
    const response = await apiClient.get('/reports/dashboard/metrics')
    return response.data
  },

  getRevenueChart: async (period: '7d' | '30d' | '90d'): Promise<ChartData> => {
    const response = await apiClient.get('/reports/dashboard/revenue-chart', { params: { period } })
    return response.data
  },

  getTopRoutes: async (limit: number = 5): Promise<TopRouteData[]> => {
    const response = await apiClient.get('/reports/dashboard/top-routes', { params: { limit } })
    return response.data
  },

  getPeakHours: async (): Promise<PeakHourData[]> => {
    const response = await apiClient.get('/reports/dashboard/peak-hours')
    return response.data
  },

  getCardTypeDistribution: async (): Promise<CardTypeDistribution[]> => {
    const response = await apiClient.get('/reports/dashboard/card-types')
    return response.data
  },
}
