import apiClient from './client'
import type { PaginatedResponse } from '../types/api'
import type {
  Device,
  DeviceFilters,
  DeviceStatus,
  DeviceLog,
  DeviceAlert,
  DeviceAlertFilters,
  DeviceLogFilters,
  DeviceMetrics,
  FirmwareVersion,
  FirmwareUpgrade,
  FirmwareUpgradeTask,
  RemoteCommand,
  CommandTemplate,
  DeviceGroup,
  CreateDeviceRequest,
  UpdateDeviceRequest,
  CreateFirmwareVersionRequest,
  CreateUpgradeTaskRequest,
  SendCommandRequest,
} from '../types/mdm'

export const mdmApi = {
  getDevices: async (filters?: DeviceFilters): Promise<PaginatedResponse<Device>> => {
    const response = await apiClient.get('/mdm/devices', { params: filters })
    return response.data
  },

  getDevice: async (id: string): Promise<Device> => {
    const response = await apiClient.get(`/mdm/devices/${id}`)
    return response.data
  },

  createDevice: async (data: CreateDeviceRequest): Promise<Device> => {
    const response = await apiClient.post('/mdm/devices', data)
    return response.data
  },

  updateDevice: async (id: string, data: UpdateDeviceRequest): Promise<Device> => {
    const response = await apiClient.patch(`/mdm/devices/${id}`, data)
    return response.data
  },

  deleteDevice: async (id: string): Promise<void> => {
    await apiClient.delete(`/mdm/devices/${id}`)
  },

  getDeviceStatus: async (deviceId: string): Promise<DeviceStatus> => {
    const response = await apiClient.get(`/mdm/devices/${deviceId}/status`)
    return response.data
  },

  getDeviceMetrics: async (): Promise<DeviceMetrics> => {
    const response = await apiClient.get('/mdm/devices/metrics')
    return response.data
  },

  getFirmwareVersions: async (): Promise<FirmwareVersion[]> => {
    const response = await apiClient.get('/mdm/firmware/versions')
    return response.data
  },

  createFirmwareVersion: async (data: CreateFirmwareVersionRequest): Promise<FirmwareVersion> => {
    const response = await apiClient.post('/mdm/firmware/versions', data)
    return response.data
  },

  deleteFirmwareVersion: async (id: string): Promise<void> => {
    await apiClient.delete(`/mdm/firmware/versions/${id}`)
  },

  getFirmwareUpgrades: async (): Promise<FirmwareUpgrade[]> => {
    const response = await apiClient.get('/mdm/firmware/upgrades')
    return response.data
  },

  getFirmwareUpgradeTasks: async (): Promise<FirmwareUpgradeTask[]> => {
    const response = await apiClient.get('/mdm/firmware/upgrade-tasks')
    return response.data
  },

  createUpgradeTask: async (data: CreateUpgradeTaskRequest): Promise<FirmwareUpgradeTask> => {
    const response = await apiClient.post('/mdm/firmware/upgrade-tasks', data)
    return response.data
  },

  cancelUpgradeTask: async (taskId: string): Promise<void> => {
    await apiClient.post(`/mdm/firmware/upgrade-tasks/${taskId}/cancel`)
  },

  getRemoteCommands: async (deviceId?: string): Promise<RemoteCommand[]> => {
    const response = await apiClient.get('/mdm/commands', { params: { deviceId } })
    return response.data
  },

  sendCommand: async (data: SendCommandRequest): Promise<RemoteCommand[]> => {
    const response = await apiClient.post('/mdm/commands', data)
    return response.data
  },

  cancelCommand: async (commandId: string): Promise<void> => {
    await apiClient.post(`/mdm/commands/${commandId}/cancel`)
  },

  getCommandTemplates: async (): Promise<CommandTemplate[]> => {
    const response = await apiClient.get('/mdm/commands/templates')
    return response.data
  },

  createCommandTemplate: async (data: Omit<CommandTemplate, 'id' | 'createdAt'>): Promise<CommandTemplate> => {
    const response = await apiClient.post('/mdm/commands/templates', data)
    return response.data
  },

  getDeviceLogs: async (filters?: DeviceLogFilters): Promise<PaginatedResponse<DeviceLog>> => {
    const response = await apiClient.get('/mdm/logs', { params: filters })
    return response.data
  },

  getDeviceAlerts: async (filters?: DeviceAlertFilters): Promise<PaginatedResponse<DeviceAlert>> => {
    const response = await apiClient.get('/mdm/alerts', { params: filters })
    return response.data
  },

  acknowledgeAlert: async (alertId: string): Promise<DeviceAlert> => {
    const response = await apiClient.post(`/mdm/alerts/${alertId}/acknowledge`)
    return response.data
  },

  resolveAlert: async (alertId: string, resolution: string): Promise<DeviceAlert> => {
    const response = await apiClient.post(`/mdm/alerts/${alertId}/resolve`, { resolution })
    return response.data
  },

  getDeviceGroups: async (): Promise<DeviceGroup[]> => {
    const response = await apiClient.get('/mdm/groups')
    return response.data
  },

  createDeviceGroup: async (data: Omit<DeviceGroup, 'id' | 'createdAt' | 'updatedAt'>): Promise<DeviceGroup> => {
    const response = await apiClient.post('/mdm/groups', data)
    return response.data
  },

  updateDeviceGroup: async (id: string, data: Partial<DeviceGroup>): Promise<DeviceGroup> => {
    const response = await apiClient.patch(`/mdm/groups/${id}`, data)
    return response.data
  },

  deleteDeviceGroup: async (id: string): Promise<void> => {
    await apiClient.delete(`/mdm/groups/${id}`)
  },

  getDeviceHealthScore: async (deviceId: string) => {
    const response = await apiClient.post(`/mdm/devices/${deviceId}/health-score`)
    return response.data
  },

  getDeviceHealthHistory: async (deviceId: string, days?: number) => {
    const response = await apiClient.get(`/mdm/devices/${deviceId}/health-history`, {
      params: { days },
    })
    return response.data
  },

  getDevicesByHealthStatus: async (status: string) => {
    const response = await apiClient.get(`/mdm/devices/health/status/${status}`)
    return response.data
  },

  predictMaintenance: async (deviceId: string) => {
    const response = await apiClient.get(`/mdm/devices/${deviceId}/predictive-maintenance`)
    return response.data
  },

  analyzeDeviceTrends: async (deviceId: string, days?: number) => {
    const response = await apiClient.get(`/mdm/devices/${deviceId}/trends`, {
      params: { days },
    })
    return response.data
  },

  predictFailure: async (deviceId: string) => {
    const response = await apiClient.post(`/mdm/devices/${deviceId}/predict`)
    return response.data
  },

  detectAnomalies: async (deviceId: string) => {
    const response = await apiClient.get(`/mdm/devices/${deviceId}/anomalies`)
    return response.data
  },

  getFleetHealthOverview: async () => {
    const response = await apiClient.get('/mdm/fleet/health-overview')
    return response.data
  },

  generateMaintenanceSchedule: async () => {
    const response = await apiClient.post('/mdm/maintenance/schedule')
    return response.data
  },

  getMaintenanceSchedule: async () => {
    const response = await apiClient.get('/mdm/maintenance/schedule')
    return response.data
  },

  getPredictiveInsights: async (fleetLevel?: boolean) => {
    const response = await apiClient.get('/mdm/predictions/insights', {
      params: { fleetLevel },
    })
    return response.data
  },

  getAnomalousDevices: async () => {
    const response = await apiClient.get('/mdm/anomalies')
    return response.data
  },

  createCanaryDeployment: async (config: {
    firmwareVersion: string
    previousVersion: string
    initialPercentage?: number
    incrementPercentage?: number
    targetPercentage?: number
    healthCheckInterval?: number
    healthCriteria?: {
      minHealthScore: number
      maxErrorRate: number
      maxResponseTime: number
      minSuccessRate: number
    }
  }) => {
    const response = await apiClient.post('/mdm/canary/deployments', config)
    return response.data
  },

  startCanaryDeployment: async (deploymentId: string) => {
    const response = await apiClient.post(`/mdm/canary/deployments/${deploymentId}/start`)
    return response.data
  },

  getCanaryStatus: async (deploymentId: string) => {
    const response = await apiClient.get(`/mdm/canary/deployments/${deploymentId}/status`)
    return response.data
  },

  performCanaryHealthCheck: async (deploymentId: string) => {
    const response = await apiClient.post(`/mdm/canary/deployments/${deploymentId}/health-check`)
    return response.data
  },

  promoteCanary: async (deploymentId: string) => {
    const response = await apiClient.post(`/mdm/canary/deployments/${deploymentId}/promote`)
    return response.data
  },

  rollbackCanary: async (
    deploymentId: string,
    data: { trigger: string; reason: string; triggeredBy: string },
  ) => {
    const response = await apiClient.post(`/mdm/canary/deployments/${deploymentId}/rollback`, data)
    return response.data
  },

  pauseCanaryDeployment: async (deploymentId: string) => {
    const response = await apiClient.put(`/mdm/canary/deployments/${deploymentId}/pause`)
    return response.data
  },

  resumeCanaryDeployment: async (deploymentId: string) => {
    const response = await apiClient.put(`/mdm/canary/deployments/${deploymentId}/resume`)
    return response.data
  },

  listCanaryDeployments: async (status?: string) => {
    const response = await apiClient.get('/mdm/canary/deployments', {
      params: { status },
    })
    return response.data
  },

  autoRollbackCheck: async (deploymentId: string) => {
    const response = await apiClient.post(`/mdm/canary/deployments/${deploymentId}/auto-check`)
    return response.data
  },
}
