import { Routes, Route, Navigate } from 'react-router-dom'
import { AnimatePresence } from 'framer-motion'
import { Suspense, lazy, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import Layout from './components/Layout'
import Dashboard from './pages/Dashboard'
import Login from './pages/Login'
import DemoLogin from './pages/DemoLogin'
import StaticDemoLogin from './pages/StaticDemoLogin'
import OAuthCallback from './pages/OAuthCallback'
import AuthRouteGuard from './components/AuthRouteGuard'
import { HelpProvider } from './contexts/HelpContext'
import { ToastContainer } from './components/ui/Toast'

const Cards = lazy(() => import('./pages/Cards'))
const Transactions = lazy(() => import('./pages/Transactions'))
const Reports = lazy(() => import('./pages/Reports'))
const Settings = lazy(() => import('./pages/Settings'))
const TransitManagement = lazy(() => import('./pages/TransitManagement'))
const DeviceManagement = lazy(() => import('./pages/DeviceManagement'))
const SetupWizard = lazy(() => import('./pages/SetupWizard'))
const EnhancedCards = lazy(() => import('./pages/EnhancedCards'))
const UserManagement = lazy(() => import('./pages/UserManagement'))
const TokenManagement = lazy(() => import('./pages/TokenManagement'))
const ScenarioConfig = lazy(() => import('./pages/ScenarioConfig'))
const PredictiveMaintenanceDashboard = lazy(() => import('./pages/PredictiveMaintenanceDashboard'))

function LoadingFallback() {
  return (
    <div className="flex items-center justify-center h-screen">
      <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
    </div>
  )
}

function ProtectedRoutes() {
  return (
    <AuthRouteGuard>
      <Layout />
    </AuthRouteGuard>
  )
}

function App() {
  const { i18n } = useTranslation()

  useEffect(() => {
    const lang = i18n.language
    document.documentElement.dir = lang === 'ar' ? 'rtl' : 'ltr'
    document.documentElement.lang = lang
  }, [i18n.language])

  return (
    <HelpProvider>
      <ToastContainer />
      <AnimatePresence mode="wait">
        <Suspense fallback={<LoadingFallback />}>
          <Routes>
              <Route path="/login" element={<Login />} />
              <Route path="/demo-login" element={<DemoLogin />} />
          <Route path="/static-demo" element={<StaticDemoLogin />} />
              <Route path="/token-login" element={<Login />} />
              <Route path="/magic-login" element={<Login />} />
              <Route path="/oauth/callback" element={<OAuthCallback />} />
            <Route path="/" element={<ProtectedRoutes />}>
              <Route index element={<Dashboard />} />
              <Route path="cards" element={<Cards />} />
              <Route path="cards-enhanced" element={<EnhancedCards />} />
              <Route path="transactions" element={<Transactions />} />
              <Route path="terminals" element={<Navigate to="/devices/list" replace />} />
              <Route path="reports" element={<Reports />} />
              <Route path="settings/*" element={<Settings />} />
              <Route path="setup-wizard" element={<SetupWizard />} />
<Route path="transit" element={<TransitManagement />} />
            <Route path="devices/*" element={<DeviceManagement />} />
              <Route path="users" element={<UserManagement />} />
              <Route path="tokens" element={<TokenManagement />} />
              <Route path="scenario-config" element={<ScenarioConfig />} />
              <Route path="predictive-maintenance" element={<PredictiveMaintenanceDashboard />} />
              <Route path="mdm" element={<Navigate to="/devices" replace />} />
            <Route path="mdm-enhanced" element={<Navigate to="/devices" replace />} />
            <Route path="scenario-settings" element={<Navigate to="/settings/scenarios" replace />} />
            </Route>
          </Routes>
        </Suspense>
      </AnimatePresence>
    </HelpProvider>
  )
}

export default App
