import apiClient from './client'
import type { 
  Card, 
  CardFilters, 
  IssueCardRequest, 
  UpdateCardStatusRequest, 
  LoadValueRequest,
  Transaction,
  PaginatedResponse 
} from '../types/api'

export const cardsApi = {
  // 
  getCards: async (filters?: CardFilters): Promise<PaginatedResponse<Card>> => {
    const response = await apiClient.get('/cards', { params: filters })
    return response.data
  },

  getCard: async (id: string): Promise<Card> => {
    const response = await apiClient.get(`/cards/${id}`)
    return response.data
  },

  issueCard: async (data: IssueCardRequest): Promise<Card> => {
    const response = await apiClient.post('/cards', data)
    return response.data
  },

  updateCard: async (id: string, data: Partial<Card>): Promise<Card> => {
    const response = await apiClient.patch(`/cards/${id}`, data)
    return response.data
  },

  // 
  updateCardStatus: async (id: string, data: UpdateCardStatusRequest): Promise<Card> => {
    const response = await apiClient.patch(`/cards/${id}/status`, data)
    return response.data
  },

  lockCard: async (id: string, data: { reason?: string; operatorId?: string }): Promise<Card> => {
    const response = await apiClient.post(`/cards/${id}/lock`, data)
    return response.data
  },

  unlockCard: async (id: string, data?: { operatorId?: string }): Promise<Card> => {
    const response = await apiClient.post(`/cards/${id}/unlock`, data)
    return response.data
  },

  // 
  reportLoss: async (id: string, data: { reporterName: string; reporterPhone: string; reporterId?: string; operatorId?: string }): Promise<Card> => {
    const response = await apiClient.post(`/cards/${id}/report-loss`, data)
    return response.data
  },

  cancelLoss: async (id: string, data?: { operatorId?: string }): Promise<Card> => {
    const response = await apiClient.post(`/cards/${id}/cancel-loss`, data)
    return response.data
  },

  // 
  replaceCard: async (id: string, data: { newCardNumber: string; reason: string; transferBalance?: boolean; operatorId?: string }): Promise<{ oldCard: Card; newCard: Card }> => {
    const response = await apiClient.post(`/cards/${id}/replace`, data)
    return response.data
  },

  getReplacementHistory: async (id: string): Promise<any[]> => {
    const response = await apiClient.get(`/cards/${id}/replacement-history`)
    return response.data
  },

  // 
  extendValidity: async (id: string, data: { extensionMonths: number; fee?: number; operatorId?: string }): Promise<Card> => {
    const response = await apiClient.post(`/cards/${id}/extend`, data)
    return response.data
  },

  // /
  refundCard: async (id: string, data: { refundAmount?: number; reason: string; operatorId?: string }): Promise<{ card: Card; refundAmount: number }> => {
    const response = await apiClient.post(`/cards/${id}/refund`, data)
    return response.data
  },

  // 
  loadValue: async (id: string, data: LoadValueRequest): Promise<Card> => {
    const response = await apiClient.post(`/cards/${id}/load`, data)
    return response.data
  },

  getBalance: async (id: string): Promise<{ balance: number; lastUpdated: string }> => {
    const response = await apiClient.get(`/cards/${id}/balance`)
    return response.data
  },

  getBalanceHistory: async (id: string, startDate?: string, endDate?: string): Promise<any[]> => {
    const response = await apiClient.get(`/cards/${id}/balance-history`, { params: { startDate, endDate } })
    return response.data
  },

  // 
  getCardTransactions: async (id: string, params?: { startDate?: string; endDate?: string; page?: number; pageSize?: number }): Promise<PaginatedResponse<Transaction>> => {
    const response = await apiClient.get(`/cards/${id}/transactions`, { params })
    return response.data
  },

  getTransactionSummary: async (id: string, startDate?: string, endDate?: string): Promise<any> => {
    const response = await apiClient.get(`/cards/${id}/transaction-summary`, { params: { startDate, endDate } })
    return response.data
  },

  // 
  batchIssueCards: async (data: { cards: IssueCardRequest[]; operatorId?: string }): Promise<{ success: Card[]; failed: any[] }> => {
    const response = await apiClient.post('/cards/batch/issue', data)
    return response.data
  },

  batchUpdateStatus: async (data: { cardIds: string[]; status: string; operatorId?: string }): Promise<{ success: string[]; failed: string[] }> => {
    const response = await apiClient.post('/cards/batch/update-status', data)
    return response.data
  },

  batchLockCards: async (data: { cardIds: string[]; reason?: string; operatorId?: string }): Promise<{ success: string[]; failed: string[] }> => {
    const response = await apiClient.post('/cards/batch/lock', data)
    return response.data
  },

  batchUnlockCards: async (data: { cardIds: string[]; operatorId?: string }): Promise<{ success: string[]; failed: string[] }> => {
    const response = await apiClient.post('/cards/batch/unlock', data)
    return response.data
  },

  batchExtendValidity: async (data: { cardIds: string[]; extensionMonths: number; operatorId?: string }): Promise<{ success: string[]; failed: string[] }> => {
    const response = await apiClient.post('/cards/batch/extend', data)
    return response.data
  },

  // 
  addToBlacklist: async (data: { cardId: string; reason: string; operatorId?: string }): Promise<void> => {
    await apiClient.post('/cards/blacklist/add', data)
  },

  removeFromBlacklist: async (data: { cardId: string; operatorId?: string }): Promise<void> => {
    await apiClient.post('/cards/blacklist/remove', data)
  },

  getBlacklist: async (page?: number, pageSize?: number): Promise<PaginatedResponse<Card>> => {
    const response = await apiClient.get('/cards/blacklist', { params: { page, pageSize } })
    return response.data
  },

  checkBlacklist: async (cardNumber: string): Promise<{ isBlacklisted: boolean; reason?: string; addedAt?: string }> => {
    const response = await apiClient.post('/cards/blacklist/check', { cardNumber })
    return response.data
  },

  // 
  getStatsOverview: async (): Promise<any> => {
    const response = await apiClient.get('/cards/stats/overview')
    return response.data
  },

  getStatsByType: async (): Promise<any[]> => {
    const response = await apiClient.get('/cards/stats/by-type')
    return response.data
  },

  getStatsByStatus: async (): Promise<any[]> => {
    const response = await apiClient.get('/cards/stats/by-status')
    return response.data
  },

  getRechargeSummary: async (startDate?: string, endDate?: string): Promise<any> => {
    const response = await apiClient.get('/cards/stats/recharge-summary', { params: { startDate, endDate } })
    return response.data
  },

  // 
  exportCards: async (data: { cardIds?: string[]; filter?: CardFilters; format: 'csv' | 'excel' | 'pdf'; fields?: string[] }): Promise<Blob> => {
    const response = await apiClient.post('/cards/export', data, { responseType: 'blob' })
    return response.data
  },

  importCards: async (data: { data: any[]; format: 'csv' | 'excel'; operatorId?: string }): Promise<{ success: number; failed: number; errors: any[] }> => {
    const response = await apiClient.post('/cards/import', data)
    return response.data
  },
}
