import apiClient from './client'
import type { PaginatedResponse } from '../types/api'
import type {
  Transaction,
  TransactionFilters,
  TransactionStats,
  DailyTransactionStats,
  HourlyTransactionStats,
  TransactionAnomaly,
  CreateTransactionRequest,
  RefundTransactionRequest,
  ReconciliationReport,
} from '../types/transactions'

export const transactionsApi = {
  getTransactions: async (filters?: TransactionFilters): Promise<PaginatedResponse<Transaction>> => {
    const response = await apiClient.get('/transactions', { params: filters })
    return response.data
  },

  getTransaction: async (id: string): Promise<Transaction> => {
    const response = await apiClient.get(`/transactions/${id}`)
    return response.data
  },

  createTransaction: async (data: CreateTransactionRequest): Promise<Transaction> => {
    const response = await apiClient.post('/transactions', data)
    return response.data
  },

  refundTransaction: async (data: RefundTransactionRequest): Promise<Transaction> => {
    const response = await apiClient.post('/transactions/refund', data)
    return response.data
  },

  getTransactionStats: async (startDate?: string, endDate?: string): Promise<TransactionStats> => {
    const response = await apiClient.get('/transactions/stats', { params: { startDate, endDate } })
    return response.data
  },

  getDailyStats: async (startDate: string, endDate: string): Promise<DailyTransactionStats[]> => {
    const response = await apiClient.get('/transactions/stats/daily', { params: { startDate, endDate } })
    return response.data
  },

  getHourlyStats: async (date: string): Promise<HourlyTransactionStats[]> => {
    const response = await apiClient.get('/transactions/stats/hourly', { params: { date } })
    return response.data
  },

  getAnomalies: async (status?: string): Promise<TransactionAnomaly[]> => {
    const response = await apiClient.get('/transactions/anomalies', { params: { status } })
    return response.data
  },

  acknowledgeAnomaly: async (anomalyId: string): Promise<TransactionAnomaly> => {
    const response = await apiClient.post(`/transactions/anomalies/${anomalyId}/acknowledge`)
    return response.data
  },

  resolveAnomaly: async (anomalyId: string, resolution: string): Promise<TransactionAnomaly> => {
    const response = await apiClient.post(`/transactions/anomalies/${anomalyId}/resolve`, { resolution })
    return response.data
  },

  getReconciliationReports: async (startDate: string, endDate: string): Promise<ReconciliationReport[]> => {
    const response = await apiClient.get('/transactions/reconciliation', { params: { startDate, endDate } })
    return response.data
  },

  exportTransactions: async (filters?: TransactionFilters): Promise<Blob> => {
    const response = await apiClient.get('/transactions/export', {
      params: filters,
      responseType: 'blob',
    })
    return response.data
  },
}
