import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { transactionsApi } from '../api/transactions'
import type {
  Transaction,
  TransactionFilters,
  TransactionStats,
  DailyTransactionStats,
  HourlyTransactionStats,
  TransactionAnomaly,
  CreateTransactionRequest,
  RefundTransactionRequest,
  ReconciliationReport,
} from '../types/transactions'

const TRANSACTIONS_QUERY_KEY = 'transactions'
const TRANSACTION_STATS_QUERY_KEY = 'transactionStats'
const DAILY_STATS_QUERY_KEY = 'dailyStats'
const HOURLY_STATS_QUERY_KEY = 'hourlyStats'
const ANOMALIES_QUERY_KEY = 'anomalies'
const RECONCILIATION_QUERY_KEY = 'reconciliation'

import { isDemoMode } from '../demo/demoMode'

export const useTransactions = (filters?: TransactionFilters) => {
  return useQuery({
    queryKey: [TRANSACTIONS_QUERY_KEY, filters],
    queryFn: async () => {
      if (isDemoMode()) {
        const limit = filters?.limit || 10
        const page = filters?.page || 1
        const total = 150
        // Generate demo data based on dynamic limit with real-time randomness
        return {
          data: Array.from({ length: limit }).map((_, i) => {
            const index = (page - 1) * limit + i
            const now = Date.now()
            return {
              id: `txn-${now}-${index}`,
              terminalId: `TERM-${100 + Math.floor(Math.random() * 20)}`,
              type: (['PURCHASE', 'RECHARGE', 'REFUND', 'TRANSFER', 'ADJUSTMENT'] as const)[Math.floor(Math.random() * 5)],
              status: (['COMPLETED', 'PENDING', 'FAILED', 'CANCELLED'] as const)[Math.floor(Math.random() * 4)],
              amount: parseFloat((Math.random() * 100).toFixed(2)),
              balance: parseFloat((Math.random() * 1000).toFixed(2)),
              createdAt: new Date(now - i * 60000).toISOString(),
              completedAt: new Date(now - i * 60000 + 1000).toISOString(),
              cardId: `card-${100 + Math.floor(Math.random() * 50)}`,
              cardNumber: `8800 **** **** ${1000 + Math.floor(Math.random() * 100)}`
            }
          }),
          total,
          page,
          limit
        }
      }
      return transactionsApi.getTransactions(filters)
    },
    refetchInterval: isDemoMode() ? 5000 : false,
  })
}

// ... useTransaction ...

export const useTransactionStats = (startDate?: string, endDate?: string) => {
  return useQuery({
    queryKey: [TRANSACTION_STATS_QUERY_KEY, startDate, endDate],
    queryFn: async () => {
      if (isDemoMode()) {
        return {
          totalTransactions: 1250,
          totalAmount: 15430.50,
          successfulTransactions: 1200,
          failedTransactions: 30,
          pendingTransactions: 20,
          averageAmount: 12.34,
          totalRechargeAmount: 5000,
          totalPurchaseAmount: 10000,
          totalRefundAmount: 430.50
        }
      }
      return transactionsApi.getTransactionStats(startDate, endDate)
    },
  })
}

export const useDailyStats = (startDate: string, endDate: string) => {
  return useQuery({
    queryKey: [DAILY_STATS_QUERY_KEY, startDate, endDate],
    queryFn: () => transactionsApi.getDailyStats(startDate, endDate),
    enabled: !!startDate && !!endDate,
  })
}

export const useHourlyStats = (date: string) => {
  return useQuery({
    queryKey: [HOURLY_STATS_QUERY_KEY, date],
    queryFn: () => transactionsApi.getHourlyStats(date),
    enabled: !!date,
  })
}

export const useAnomalies = (status?: string, options?: { retry?: boolean; enabled?: boolean }) => {
  return useQuery({
    queryKey: [ANOMALIES_QUERY_KEY, status],
    queryFn: () => transactionsApi.getAnomalies(status),
    retry: options?.retry ?? true,
    enabled: options?.enabled ?? true,
  })
}

export const useAcknowledgeAnomaly = () => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: (anomalyId: string) => transactionsApi.acknowledgeAnomaly(anomalyId),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [ANOMALIES_QUERY_KEY] })
    },
  })
}

export const useResolveAnomaly = () => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: ({ anomalyId, resolution }: { anomalyId: string; resolution: string }) =>
      transactionsApi.resolveAnomaly(anomalyId, resolution),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [ANOMALIES_QUERY_KEY] })
    },
  })
}

export const useReconciliationReports = (startDate: string, endDate: string) => {
  return useQuery({
    queryKey: [RECONCILIATION_QUERY_KEY, startDate, endDate],
    queryFn: () => transactionsApi.getReconciliationReports(startDate, endDate),
    enabled: !!startDate && !!endDate,
  })
}

export const useExportTransactions = () => {
  return useMutation({
    mutationFn: (filters?: TransactionFilters) => transactionsApi.exportTransactions(filters),
  })
}
