export const useDevices = (filters?: DeviceFilters) => {
  return useQuery({
    queryKey: [MDM_QUERY_KEY, DEVICES_QUERY_KEY, filters],
    queryFn: async () => {
      if (isDemoMode()) {
        const now = Date.now()
        const deviceTypes = ['ONBOARD', 'VALIDATOR', 'TOPUP', 'EDGE_GATEWAY', 'STATION_SENSOR'] as const
        const demoDevices: Device[] = Array.from({ length: 60 }).map((_, index) => {
          const deviceNo = index + 1
          const status = (
            ['ACTIVE', 'ACTIVE', 'ACTIVE', 'OFFLINE', 'MAINTENANCE'] as const
          )[index % 5]
          const deviceType = deviceTypes[index % deviceTypes.length]
          const isTerminalBusinessDevice = ['ONBOARD', 'VALIDATOR', 'TOPUP'].includes(deviceType)
          const nfcStatus = status === 'OFFLINE'
            ? 'NOT_AVAILABLE'
            : status === 'MAINTENANCE'
              ? 'MAINTENANCE'
              : 'OPERATIONAL'

          return {
            id: `DEV-DEMO-${String(deviceNo).padStart(3, '0')}`,
            type: deviceType,
            serialNumber: `SN2026${String(deviceNo).padStart(6, '0')}`,
            firmwareVersion: `v2.${5 + (index % 2)}.${index % 10}`,
            status,
            vehicleId: isTerminalBusinessDevice ? `LINE-${String((index % 20) + 1).padStart(2, '0')}` : undefined,
            terminalId: isTerminalBusinessDevice ? `TERM-DEMO-${String(deviceNo).padStart(3, '0')}` : undefined,
            lastActivityAt: new Date(now - index * 10 * 60 * 1000).toISOString(),
            registeredAt: new Date(now - (60 + index) * 24 * 60 * 60 * 1000).toISOString(),
            metadata: {
              manufacturer: 'DemoCorp',
              model: deviceType,
              batteryLevel: Math.max(8, 95 - (index % 70)),
              storageUsed: 20 + (index * 3) % 70,
              memoryUsed: 25 + (index * 5) % 70,
              ipAddress: `10.20.${Math.floor(index / 20) + 1}.${(index % 20) + 10}`,
            },
            capabilities: [
              { type: 'NFC', status: nfcStatus },
              {
                type: 'QR_CODE',
                status: status === 'OFFLINE' ? 'NOT_AVAILABLE' : 'OPERATIONAL',
              },
            ],
          }
        })

        let filtered = demoDevices
        if (filters?.status) {
          filtered = filtered.filter((device) => device.status === filters.status)
        }
        if (filters?.type) {
          filtered = filtered.filter((device) => device.type === filters.type)
        }
        if (filters?.vehicleId) {
          filtered = filtered.filter((device) => device.vehicleId === filters.vehicleId)
        }
        if (filters?.search) {
          const search = filters.search.toLowerCase()
          filtered = filtered.filter((device) =>
            device.id.toLowerCase().includes(search) ||
            device.serialNumber.toLowerCase().includes(search) ||
            device.type.toLowerCase().includes(search) ||
            (device.vehicleId || '').toLowerCase().includes(search) ||
            (device.terminalId || '').toLowerCase().includes(search)
          )
        }

        const page = Math.max(filters?.page || 1, 1)
        const limit = Math.max(filters?.limit || filtered.length || 1, 1)
        const start = (page - 1) * limit
        const data = filtered.slice(start, start + limit)

        return {
          data,
          total: filtered.length,
          page,
          limit,
          totalPages: Math.max(1, Math.ceil(filtered.length / limit)),
        }
      }

      return mdmApi.getDevices(filters)
    },
    refetchInterval: isDemoMode() ? 30000 : false,
  })
}

export const useDevice = (id: string) => {
  return useQuery({
    queryKey: [MDM_QUERY_KEY, DEVICES_QUERY_KEY, id],
    queryFn: () => mdmApi.getDevice(id),
    enabled: !!id,
  })
}

export const useCreateDevice = () => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: (data: CreateDeviceRequest) => mdmApi.createDevice(data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [MDM_QUERY_KEY, DEVICES_QUERY_KEY] })
      queryClient.invalidateQueries({ queryKey: [MDM_QUERY_KEY, DEVICE_METRICS_QUERY_KEY] })
    },
  })
}

