export const useDeviceLogs = (filters?: DeviceLogFilters, options?: { retry?: boolean; enabled?: boolean }) => {
  return useQuery({
    queryKey: [MDM_QUERY_KEY, LOGS_QUERY_KEY, filters],
    queryFn: async () => {
      if (isDemoMode()) {
        const now = Date.now()
        const levels: DeviceLog['level'][] = ['ERROR', 'WARNING', 'INFO', 'DEBUG', 'FATAL']
        const categories: DeviceLog['category'][] = ['SYSTEM', 'NETWORK', 'SECURITY', 'APPLICATION', 'PERFORMANCE']
        const mockLogs: DeviceLog[] = Array.from({ length: 220 }).map((_, index) => {
          const deviceNo = (index % 60) + 1
          const level = levels[index % levels.length]
          const category = categories[index % categories.length]
          const deviceId = `DEV-DEMO-${String(deviceNo).padStart(3, '0')}`

          return {
            id: `log-${index + 1}`,
            deviceId,
            level,
            category,
            message: `${category} ${level} event on ${deviceId}`,
            timestamp: new Date(now - index * 8 * 60 * 1000).toISOString(),
          }
        })

        let filtered = mockLogs
        if (filters?.deviceId) {
          filtered = filtered.filter((log) => log.deviceId === filters.deviceId)
        }
        if (filters?.level) {
          filtered = filtered.filter((log) => log.level === filters.level)
        }
        if (filters?.category) {
          filtered = filtered.filter((log) => log.category === filters.category)
        }
        if (filters?.search) {
          const search = filters.search.toLowerCase()
          filtered = filtered.filter((log) =>
            log.deviceId.toLowerCase().includes(search) ||
            log.message.toLowerCase().includes(search)
          )
        }

        const page = Math.max(filters?.page || 1, 1)
        const limit = Math.max(filters?.limit || 50, 1)
        const start = (page - 1) * limit

        return {
          data: filtered.slice(start, start + limit),
          total: filtered.length,
          page,
          limit,
          totalPages: Math.max(1, Math.ceil(filtered.length / limit)),
        }
      }

      return mdmApi.getDeviceLogs(filters)
    },
    retry: options?.retry ?? true,
    enabled: options?.enabled ?? true,
  })
}

export const useDeviceAlerts = (filters?: DeviceAlertFilters, options?: { retry?: boolean; enabled?: boolean }) => {
  return useQuery({
    queryKey: [MDM_QUERY_KEY, ALERTS_QUERY_KEY, filters],
    queryFn: async () => {
      if (isDemoMode()) {
        const alertTypes = ['LOW_BATTERY', 'OFFLINE', 'SYNC_FAILED', 'TAMPER_DETECTED', 'FIRMWARE_OUTDATED']
        const severities = ['CRITICAL', 'HIGH', 'MEDIUM', 'LOW']
        return {
          data: Array.from({ length: 5 }).map((_, i) => ({
            id: `alert-${i + 1}`,
            deviceId: `device-${Math.floor(Math.random() * 10) + 1}`,
            title: i18n.t('alerts.deviceAlert', { type: alertTypes[i % alertTypes.length] }),
            type: alertTypes[i % alertTypes.length],
            severity: severities[i % severities.length],
            description: i18n.t('alerts.deviceAlertMessage', { deviceId: `TERM-00${i + 1}`, type: alertTypes[i % alertTypes.length] }),
            status: 'ACTIVE',
            createdAt: new Date(Date.now() - Math.random() * 86400000).toISOString(),
            updatedAt: new Date().toISOString(),
          }))
        }
      }
      return mdmApi.getDeviceAlerts(filters)
    },
    retry: options?.retry ?? true,
    enabled: options?.enabled ?? true,
  })
}

