import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { isDemoMode } from '../demo/demoMode'
import { cardsApi } from '../api/cards'
import type {
  Card,
  CardFilters,
  IssueCardRequest,
  UpdateCardStatusRequest,
  LoadValueRequest,
  PaginatedResponse
} from '../types/api'

const CARDS_QUERY_KEY = 'cards'
const CARD_STATS_QUERY_KEY = 'cardStats'



// ... existing imports ...

export const useCards = (filters?: CardFilters) => {
  return useQuery({
    queryKey: [CARDS_QUERY_KEY, filters],
    queryFn: async () => {
      if (isDemoMode()) {
        const now = Date.now()
        const mockCards = Array.from({ length: 60 }).map((_, i) => ({
          id: `card-${now}-${i + 1}`,
          number: `8800 ${1000 + Math.floor(Math.random() * 9000)} ${5000 + Math.floor(Math.random() * 4000)} ${9000 + i}`,
          type: (['STANDARD', 'STUDENT', 'SENIOR', 'STAFF', 'TOURIST_1D', 'TOURIST_3D', 'TOURIST_7D', 'PERIOD'] as const)[Math.floor(Math.random() * 8)],
          status: (['ACTIVE', 'SUSPENDED', 'BLACKLISTED', 'EXPIRED'] as const)[Math.floor(Math.random() * 4)],
          balance: Math.floor(Math.random() * 1000),
          issuedAt: '2025-01-01',
          expiresAt: '2030-01-01',
          lastUsed: new Date(now - Math.floor(Math.random() * 86400000)).toISOString(),
          membershipLevel: (['GOLD', 'SILVER', 'BRONZE'] as const)[Math.floor(Math.random() * 3)],
          ecoPoints: Math.floor(Math.random() * 500),
          carbonReduced: (Math.random() * 10).toFixed(2),
        }))

        let filtered = mockCards
        if (filters?.type) filtered = filtered.filter(c => c.type === filters.type)
        if (filters?.status) filtered = filtered.filter(c => c.status === filters.status)
        if (filters?.search) {
          const search = filters.search.toLowerCase()
          filtered = filtered.filter(c =>
            c.number.toLowerCase().includes(search) ||
            c.id.toLowerCase().includes(search)
          )
        }

        return {
          data: filtered.slice(0, 20),
          total: filtered.length,
          page: 1,
          pageSize: 20,
          totalPages: Math.ceil(filtered.length / 20)
        }
      }
      return cardsApi.getCards(filters)
    },
    refetchInterval: isDemoMode() ? 5000 : false,
  })
}

export const useCard = (id: string) => {
  return useQuery({
    queryKey: [CARDS_QUERY_KEY, id],
    queryFn: () => cardsApi.getCard(id),
    enabled: !!id,
  })
}

export const useIssueCard = () => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: async (data: IssueCardRequest) => {
      if (isDemoMode()) {
        await new Promise(resolve => setTimeout(resolve, 1000))
        // Return a mock ID that we can use for highlighting
        return { success: true, id: `new-card-${Date.now()}` }
      }
      return cardsApi.issueCard(data)
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [CARDS_QUERY_KEY] })
      queryClient.invalidateQueries({ queryKey: [CARD_STATS_QUERY_KEY] })
    },
  })
}

export const useUpdateCard = () => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: ({ id, data }: { id: string; data: Partial<Card> }) =>
      cardsApi.updateCard(id, data),
    onSuccess: (_, variables) => {
      queryClient.invalidateQueries({ queryKey: [CARDS_QUERY_KEY, variables.id] })
      queryClient.invalidateQueries({ queryKey: [CARDS_QUERY_KEY] })
    },
  })
}

