export const useCardTransactions = (id: string) => {
  return useQuery({
    queryKey: [CARDS_QUERY_KEY, id, 'transactions'],
    queryFn: async () => {
      if (isDemoMode()) {
        return {
          data: Array.from({ length: 5 }).map((_, i) => ({
            id: `tx-${i + 1}`,
            type: i % 3 === 0 ? 'CREDIT' : 'DEBIT',
            amount: i % 3 === 0 ? (i + 1) * 100 : (i + 1) * 2.5,
            timestamp: `2025-02-01 ${10 + i}:00:00`,
            terminal: `Terminal-${100 + i}`,
            location: i % 2 === 0 ? 'Central Station' : 'Airport Express',
            status: 'SUCCESS'
          })),
          total: 5
        }
      }
      return cardsApi.getCardTransactions(id)
    },
    enabled: !!id,
  })
}

